import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import TaskComponent from '../../task';

import { onlyColumnTasks } from '../../../selectors/tasks';

import language from '../../../constants/language';

const styles = {
  container: {
    color: '#919A9B',
    backgroundColor: 'white',
    minHeight: 75,
    fontFamily: 'Roboto, Helvetica, Arial, sans-serif',
    position: 'relative',
    textAlign: 'center',
    top: '45%',
  },
};

class InnerListColumnComponent extends Component {
  shouldComponentUpdate(nextProps) {
    if (nextProps.tasks.length !== this.props.tasks.length) {
      return true;
    }
    for (let i = 0; i < nextProps.tasks.length; i += 1) {
      if (nextProps.tasks[i] !== this.props.tasks[i]) {
        return true;
      }
    }
    if (nextProps.selectedLanguage !== this.props.selectedLenguage) {
      return true;
    }
    return false;
  }

  render() {
    const { classes, selectedLanguage } = this.props;
    if (this.props.tasks.length > 0){
      return this.props.tasks.map((task, index) => (
        <TaskComponent key={task.id} taskId={task.id} index={index} />
      ));
    } else {
      return <div className={classes.container}>{language[selectedLanguage].tasks.noTask}</div>
    }
  }
}

InnerListColumnComponent.propTypes = {
  tasks: PropTypes.array.isRequired,
  classes: PropTypes.object.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    tasks: onlyColumnTasks(ownProps, state),
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(
    mapStateToProps,
    mapDispatchToProps,
    )(withStyles(styles)(InnerListColumnComponent)
);
