import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Dialog from '../../../lib/Dialog';
import Form from '../../../lib/Form';
import TextField from '../../../lib/TextField';
import PrimaryButton from '../../../lib/Button/PrimaryButton';

import { notifySuccess, notifyError } from '../../../../actions/notification';

import { toggleEditColumnForm } from '../../../../actions/modals/columns';
import { fetchUpdateColumn } from '../../../../actions/api/columns';

import { oneColumnFormModal } from '../../../../selectors/boards';

import language from '../../../../constants/language';

class EditModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: props.column.name,
    };

    this.nameChange = this.nameChange.bind(this);
    this.handleCloseColumn = this.handleCloseColumn.bind(this);
    this.handleEditSubmit = this.handleEditSubmit.bind(this);
  }

  nameChange(value) {
    this.setState({ name: value });
  }

  handleCloseColumn() {
    this.props.toggleEditColumnForm();
  }

  handleEditSubmit(event) {
    if (event) event.preventDefault();
    const { name } = this.state;
    const { column } = this.props;
    const newName = name.trim();
    if (newName !== '') {
      this.props.fetchUpdateColumn(column.id, { name: newName })
        .then((response) => {
          if (response.ok) {
            this.handleCloseColumn();
            this.props.notifySuccess('Column updated');
          }
        });
    } else {
      this.props.notifyError('Field name cannot be empty');
    }
  }

  render() {
    const { isFetching, selectedLanguage } = this.props;
    const { name } = this.state;
    const footer = (
      <div>
        <PrimaryButton
          type="submit"
          onClick={this.handleEditSubmit}
          loading={isFetching}
        >
          {language[selectedLanguage].modals.editColumnConfirm}
        </PrimaryButton>
      </div>
    );
    return (
      <Dialog
        open
        title={language[selectedLanguage].modals.editColumnTitle}
        footer={footer}
        onClose={this.handleCloseColumn}
        clickClose={this.handleCloseColumn}
      >
        <Form
          onSubmit={this.handleEditSubmit}
        >
          <TextField
            onChange={this.nameChange}
            type="name"
            name={name}
            value={name}
            fullWidth
            label={language[selectedLanguage].modals.editColumnNameLabel}
            required
            id="name"
            autoFocus
          />
        </Form>
      </Dialog>
    );
  }
}

EditModal.propTypes = {
  toggleEditColumnForm: PropTypes.func.isRequired,
  fetchUpdateColumn: PropTypes.func.isRequired,
  column: PropTypes.object.isRequired,
  isFetching: PropTypes.bool.isRequired,
  notifyError: PropTypes.any.isRequired,
  notifySuccess: PropTypes.any.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    column: oneColumnFormModal(ownProps, state),
    isFetching: state.app.isFetching,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleEditColumnForm,
    fetchUpdateColumn,
    notifySuccess,
    notifyError,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(EditModal);
