import {
  TOGGLE_EDIT_MODAL_BOARD,
  TOGGLE_DELETE_MODAL_BOARD,
} from '../../constants/modals/boards';

export const toggleEditBoardForm = id => ({
  type: TOGGLE_EDIT_MODAL_BOARD,
  payload: {
    body: {
      id,
    },
  },
});

export const toggleDeleteBoardForm = id => ({
  type: TOGGLE_DELETE_MODAL_BOARD,
  payload: {
    body: {
      id,
    },
  },
});
