import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Dialog from '../../../lib/Dialog';
import Form from '../../../lib/Form';
import TextField from '../../../lib/TextField';
import PrimaryButton from '../../../lib/Button/PrimaryButton';

import { notifySuccess, notifyError } from '../../../../actions/notification';

import { toggleEditBoardForm } from '../../../../actions/modals/boards';
import { fetchUpdateBoard } from '../../../../actions/api/boards';

import { oneBoardFormModal } from '../../../../selectors/boards';

class EditModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: props.board.name,
    };

    this.nameChange = this.nameChange.bind(this);
    this.handleCloseBoard = this.handleCloseBoard.bind(this);
    this.handleEditSubmit = this.handleEditSubmit.bind(this);
  }

  nameChange(value) {
    this.setState({ name: value });
  }

  handleCloseBoard() {
    this.props.toggleEditBoardForm();
  }

  handleEditSubmit(event) {
    if (event) event.preventDefault();
    const { name } = this.state;
    const { board } = this.props;
    const newName = name.trim();
    if (newName !== '') {
      this.props.fetchUpdateBoard(board.id, { name: newName })
        .then((response) => {
          if (response.ok) {
            this.handleCloseBoard();
            this.props.notifySuccess('Board updated');
          }
        });
    } else {
      this.props.notifyError('Field name cannot be empty');
    }
  }

  render() {
    const { isFetching } = this.props;
    const { name } = this.state;
    const footer = (
      <div>
        <PrimaryButton
          type="submit"
          onClick={this.handleEditSubmit}
          loading={isFetching}
        >
          EDIT BOARD
        </PrimaryButton>
      </div>
    );
    return (
      <Dialog
        open
        title="Edit Board"
        footer={footer}
        onClose={this.handleCloseBoard}
        clickClose={this.handleCloseBoard}
      >
        <Form
          onSubmit={this.handleEditSubmit}
        >
          <TextField
            onChange={this.nameChange}
            type="name"
            name={name}
            value={name}
            fullWidth
            label="Name"
            required
            id="name"
            autoFocus
          />
        </Form>
      </Dialog>
    );
  }
}

EditModal.propTypes = {
  toggleEditBoardForm: PropTypes.func.isRequired,
  fetchUpdateBoard: PropTypes.func.isRequired,
  board: PropTypes.object.isRequired,
  isFetching: PropTypes.bool.isRequired,
  notifyError: PropTypes.any.isRequired,
  notifySuccess: PropTypes.any.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    board: oneBoardFormModal(ownProps, state),
    isFetching: state.app.isFetching,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleEditBoardForm,
    fetchUpdateBoard,
    notifySuccess,
    notifyError,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(EditModal);
