export const reorder = (list, source, destination) => {
  // source.index is the same as position becasuse ir ordered
  const result = JSON.parse(JSON.stringify(list));

  const prev = source.index;
  const next = destination.index;

  // Update the position of the moved task
  result[prev].position = next;

  // Update the positon of the others tasks between the source positon
  // and the destination position
  let i = (prev < next) ? prev + 1 : prev - 1;
  while ((prev > next && next <= i) || (prev < next && i <= next)) {
    if (prev < next) {
      result[i].position -= 1;
      i += 1;
    } else if (prev > next) {
      result[i].position += 1;
      i -= 1;
    }
  }
  return result;
};

export const move = (source, destination, droppableSource, droppableDestination) => {
  // Clone the original arrays
  const destinationIndex = droppableDestination.index;
  const [removed] = source.tasks.splice(droppableSource.index, 1);
  return [removed.id, (destinationIndex - 1), (destinationIndex)];
};

export const getObjectIdOrNull = (object) => {
  return object ? object.id : null;
};

export const getPositions = (destination, source) => {
  let prevPos; let nextPos;
  if (destination.index > source.index) {
    prevPos = destination.index;
    nextPos = prevPos + 1;
  } else {
    prevPos = destination.index - 1;
    nextPos = prevPos + 1;
  }
  return [prevPos, nextPos];
};

export const calcPosition = (prev, next) => {
  if (prev && next) return (prev.position + next.position) / 2;
  if (prev) return prev.position + 1024;
  if (next) return next.position / 2;
  return 1024;
};

export const checkBorder = (previous, current, next) => {
  let newNext; let newPrevious;
  if (current === next) {
    newPrevious = null;
    newNext = previous;
    return [newPrevious, newNext];
  }

  if (current === previous) {
    newPrevious = next;
    newNext = null;
    return [newPrevious, newNext];
  }
  return [previous, next];
};
