import React from 'react';
import PropTypes from 'prop-types';
import { ColorWrap, Swatch } from 'react-color/lib/components/common';
import color from 'react-color/lib/helpers/color';

/* eslint-disable react/no-array-index-key */
const styles = {
  card: {
    background: '#fff',
    border: '0 solid rgba(0,0,0,0.25)',
    boxShadow: '0 1px 4px rgba(0,0,0,0.25)',
    borderRadius: '4px',
    position: 'relative',
  },
  body: {
    padding: '15px 9px 9px 15px',
  },
  label: {
    fontSize: '18px',
    color: '#fff',
  },
  triangle: {
    display: 'none',
  },
  triangleShadow: {
    display: 'none',
  },
  hash: {
    background: '#F0F0F0',
    height: '30px',
    width: '30px',
    borderRadius: '4px 0 0 4px',
    float: 'left',
    color: '#98A1A4',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  input: {
    width: '100px',
    fontSize: '14px',
    color: '#666',
    border: '0px',
    outline: 'none',
    height: '28px',
    boxShadow: 'inset 0 0 0 1px #F0F0F0',
    boxSizing: 'content-box',
    borderRadius: '0 4px 4px 0',
    float: 'left',
    paddingLeft: '8px',
  },
  swatch: {
    width: '30px',
    height: '30px',
    float: 'left',
    borderRadius: '4px',
    margin: '0 6px 6px 0',
  },
  clear: {
    clear: 'both',
  },
};

const Twitter = ({
  width,
  onChange,
  colors,
}) => {
  const handleChange = (hexcode, e) => color.isValidHex(hexcode) && onChange({
    hex: hexcode,
    source: 'hex',
  }, e);

  return (
    <div style={{ ...styles.card, width }}>
      <div style={styles.triangleShadow} />
      <div style={styles.triangle} />

      <div style={styles.body}>
        { colors.map((c, i) => {
          return (
            <Swatch
              key={i}
              color={c}
              hex={c}
              style={styles.swatch}
              onClick={handleChange}
              focusStyle={{
                boxShadow: `0 0 4px ${c}`,
              }}
            />
          );
        }) }
        <div style={styles.clear} />
      </div>
    </div>
  );
};

Twitter.propTypes = {
  width: PropTypes.number,
  colors: PropTypes.arrayOf(PropTypes.string),
  onChange: PropTypes.func.isRequired,
};

Twitter.defaultProps = {
  width: 276,
  colors: ['#FF6900', '#FCB900', '#7BDCB5', '#00D084', '#8ED1FC', '#0693E3',
    '#ABB8C3', '#EB144C', '#F78DA7', '#9900EF'],
};

export default ColorWrap(Twitter);
