import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import TaskAssign from './TaskAssign';
import TaskLabel from './TaskLabel';
import TaskReport from './TaskReport';
import TaskShow from './TaskShow';

const ModalContainer = ({
  taskAssign,
  taskLabel,
  taskReport,
  taskEdit,
  taskDrop,
}) => {
  return (
    <Fragment>
      { taskAssign && <TaskAssign /> }
      { taskLabel && <TaskLabel /> }
      { taskReport && <TaskReport /> }
      { (taskEdit && !taskDrop) && <TaskShow /> }
    </Fragment>
  );
};

ModalContainer.propTypes = {
  taskAssign: PropTypes.bool.isRequired,
  taskLabel: PropTypes.bool.isRequired,
  taskReport: PropTypes.bool.isRequired,
  taskEdit: PropTypes.bool.isRequired,
  taskDrop: PropTypes.bool.isRequired,
};

const mapStateToProps = (state) => {
  return {
    taskAssign: state.board.showAssignTaskForm,
    taskLabel: state.board.showLabelTaskForm,
    taskReport: state.board.showReportTaskForm,
    taskEdit: state.board.showEditTaskForm,
    taskDrop: state.board.showDropDownTask,
  };
};

export default connect(mapStateToProps, {})(ModalContainer);
