import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Divider from '@material-ui/core/Divider';
import Checkbox from '@material-ui/core/Checkbox';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';

import LabelForm from './Form';
import Label from '../../../../../../lib/TaskComponents/Label';
import PrimaryButton from '../../../../../../lib/Button/PrimaryButton';

import { taskOpenLabels } from '../../../../../../../actions/task';
import { fetchAddLabelTask, fetchRemoveLabelTask } from '../../../../../../../actions/api/labels';

import { onlyLabels } from '../../../../../../../selectors/labels';

import language from '../../../../../../../constants/language';

const styles = {
  root: {
    display: 'flex',
    flexDirection: 'column',
    width: '300px',
  },
  item: {
    margin: '1px 0px',
  },
  name: {
    fontSize: 12,
    marginRight: 2,
    fontWeight: 'bold',
    textShadow: '-1px -1px 0 #fffd, 1px -1px 0 #fffd, -1px 1px 0 #fffd, 1px 1px 0 #fffd',
  },
  checkbox: {
    color: '#ea5941',
    marginLeft: 2,
    '&$checked': {
      color: '#ea5941',
    },
  },
  checked: {},
  icon: {
    fontSize: 12,
    color: '#2a3d41',
    padding: '0px 2px',
    '&:hover': {
      color: '#ca3921',
    },
  },
  reject: {
    color: '#e53935',
    cursor: 'pointer',
    '&:hover': {
      color: '#ff6f60',
    },
    marginLeft: 2,
  },
  listItem: {
    marginRight: 10,
  },
};

class LabelsModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      newLabel: false,
    };
    this.onClose = this.onClose.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
    this.showForm = this.showForm.bind(this);
    this.hideForm = this.hideForm.bind(this);
  }

  onClose() {
    this.props.taskOpenLabels();
  }

  handleToggle(event, labelId, assigned) {
    if (event) event.stopPropagation();
    const body = { task_id: this.props.taskId };
    if (assigned) {
      this.props.fetchRemoveLabelTask(labelId, this.props.taskId);
    } else {
      this.props.fetchAddLabelTask(labelId, body);
    }
  }

  showForm() {
    this.setState({ newLabel: true });
    this.props.popOverActions.updatePosition();
  }

  hideForm() {
    this.setState({ newLabel: false });
    this.props.popOverActions.updatePosition();
  }

  render() {
    const { classes, labels, selectedLanguage } = this.props;
    return (
      <div className={classes.root}>
        <List>
          {
            labels.map((label) => {
              return (
                <div key={label.id}>
                  <ListItem
                    button
                    onClick={event => this.handleToggle(event, label.id, label.assigned)}
                    className={classes.listItem}
                  >
                    <Label label={label} customCursor="pointer" customWidth="200px" />
                    <ListItemSecondaryAction>
                      <Checkbox
                        disableRipple
                        classes={{ root: classes.checkbox, checked: classes.checked }}
                        onChange={event => this.handleToggle(event, label.id, label.assigned)}
                        checked={label.assigned}
                      />
                    </ListItemSecondaryAction>
                  </ListItem>
                  <Divider />
                </div>
              );
            })
          }
          {
            this.state.newLabel
            && (
              <div className={classes.button}>
                <LabelForm onSubmit={this.hideForm} />
              </div>
            )
          }
        </List>
        {
          !this.state.newLabel
          && (
            <ListItem>
              <div>
                <PrimaryButton
                  onClick={this.showForm}
                >
                  {language[selectedLanguage].modals.taskModalEditLabelModalCreate}
                </PrimaryButton>
              </div>
            </ListItem>
          )
        }
      </div>
    );
  }
}

LabelsModal.propTypes = {
  taskId: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired,
  labels: PropTypes.array.isRequired,
  taskOpenLabels: PropTypes.func.isRequired,
  fetchAddLabelTask: PropTypes.func.isRequired,
  fetchRemoveLabelTask: PropTypes.func.isRequired,
  popOverActions: PropTypes.object.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    labels: onlyLabels(ownProps, state),
    taskId: state.board.selectedTask,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenLabels,
    fetchAddLabelTask,
    fetchRemoveLabelTask,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(LabelsModal));
