import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Logo from '../../../../../../lib/Logo/Label';
import Item from '../SidebarItem';

import { taskOpenLabels } from '../../../../../../../actions/task';
import PopOver from '../PopOver';

import LabelsContainer from './Container';

const styles = ({
  title: {
    alignSelf: 'center',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
});

class LabelsPopover extends Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
    };
    this.ref = React.createRef();
    this.popOverActions = {};
    this.onClickLabels = this.onClickLabels.bind(this);
    this.bindPopOverActions = this.bindPopOverActions.bind(this);
  }

  componentDidMount() {
    this.setState({ anchorEl: this.ref.current });
  }

  onClickLabels() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenLabels();
    }
  }

  bindPopOverActions(actions) {
    this.popOverActions = actions;
  }

  render() {
    const { open, classes } = this.props;
    const { anchorEl } = this.state;
    return (
      <Fragment>
        <div ref={this.ref} style={{ marginTop: '10px' }}>
          <Item
            onClick={this.onClickLabels}
          >
            <Logo size="23px" />
            <Typography
              className={classes.title}
              style={
               this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}}
            >

              Labels
            </Typography>
          </Item>
        </div>
        {
          anchorEl !== null
          && (
            <PopOver
              action={this.bindPopOverActions}
              onClose={this.onClickLabels}
              anchorEl={anchorEl}
              open={open}
            >
              <LabelsContainer popOverActions={this.popOverActions} />
            </PopOver>
          )
        }
      </Fragment>
    );
  }
}

LabelsPopover.propTypes = {
  classes: PropTypes.object.isRequired,
  open: PropTypes.bool.isRequired,
  taskOpenLabels: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    open: state.task.openLabels,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenLabels,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(LabelsPopover));
