import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Logo from '../../../../../../lib/Logo/Checklist';
import Item from '../SidebarItem';

import { taskOpenChecklist } from '../../../../../../../actions/task';

const styles = ({
  title: {
    alignSelf: 'center',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
});

class ChecklistPopover extends Component {
  constructor(props) {
    super(props);
    this.onClickChecklist = this.onClickChecklist.bind(this);
  }

  onClickChecklist() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenChecklist();
    }
  }

  render() {
    const { classes } = this.props;
    return (
      <div style={{ marginTop: '10px' }}>
        <Item
          onClick={this.onClickChecklist}
        >
          <Logo size="23px" />
          <Typography
            className={classes.title}
            style={
               this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}}
          >

            Checklist
          </Typography>
        </Item>
      </div>
    );
  }
}

ChecklistPopover.propTypes = {
  taskOpenChecklist: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    open: state.task.openChecklist,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenChecklist,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ChecklistPopover));
