import {
  TOGGLE_ASSIGN_MODAL_TASK,
  TOGGLE_LABEL_MODAL_TASK,
  TOGGLE_CREATE_MODAL_TASK,
  TOGGLE_EDIT_MODAL_TASK,
  TOGGLE_CREATE_MODAL_COLUMN,
  TOGGLE_EDIT_MODAL_COLUMN,
  TOGGLE_DELETE_MODAL_COLUMN,
  START_DRAG,
  TOGGLE_DROPDOWN_TASK,
  TOGGLE_DROPDOWN_COLUMN,
  TOGGLE_CREATE_MODAL_BOARD,
  TOGGLE_REPORT_MODAL_TASK,
} from '../constants/board';

import {
  HIDE_ALL_BOARD,
} from '../constants/user';

export const hideAll = () => ({
  type: HIDE_ALL_BOARD,
});

export const startDrag = () => ({
  type: START_DRAG,
});

export const toggleDropDownTask = id => ({
  type: TOGGLE_DROPDOWN_TASK,
  payload: {
    body: {
      id,
    },
  },
});

export const toggleDropDownColumn = id => ({
  type: TOGGLE_DROPDOWN_COLUMN,
  payload: {
    body: {
      id,
    },
  },
});

