import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import History from '@material-ui/icons/History';
import { onlyTaskComments } from '../../../../../selectors/comments';
import { onlyTaskAttachments } from '../../../../../selectors/attachments';

import ReportHourIcon from '../../../../lib/Logo/Report';
import AttachmentIcon from '../../../../lib/Logo/Attachment';
import CommentIcon from '../../../../lib/Logo/Comment';

import {
  taskOpenLogs, taskOpenReportedHours, taskOpenComments, taskOpenAttachments,
} from '../../../../../actions/task';

const styles = theme => ({
  title: {
    color: '#ffffff',
    fontSize: theme.typography.pxToRem(14),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  item: {
    color: '#9b9b9b',
    fontSize: theme.typography.pxToRem(12),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    alignSelf: 'center',
  },
  option: {
    display: 'flex',
    alignItems: 'center',
    outline: 'none',
    cursor: 'pointer',
    marginTop: '10px',
  },
  wrapper: {
    display: 'grid',
    gridTemplateColumns: '1fr 5fr',
    gridGap: '3px',
  },
  wrapperSelected: {
    display: 'grid',
    gridTemplateColumns: '1fr 5fr',
    gridGap: '3px',
    left: '-12px',
    position: 'relative',
  },
  triangle: {
    width: 0,
    height: 0,
    position: 'relative',
    left: '-24px',
    borderTop: '6px solid transparent',
    borderBottom: '6px solid transparent',
    borderLeft: '12px solid #f2f2f2',
  },
});

class Sidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      option: null,
    };
    this.onClickActivity = this.onClickActivity.bind(this);
    this.onClickReportHours = this.onClickReportHours.bind(this);
    this.onClickComments = this.onClickComments.bind(this);
    this.onClickAttachments = this.onClickAttachments.bind(this);
  }

  componentWillMount() {
    this.onClickComments();
  }

  onClickActivity() {
    const option = 'activity';
    if (this.state.option !== option) {
      this.props.taskOpenLogs();
      this.setState({ option });
    }
  }

  onClickReportHours() {
    const option = 'hours';
    if (this.state.option !== option) {
      this.props.taskOpenReportedHours();
      this.setState({ option });
    }
  }

  onClickComments() {
    const option = 'comments';
    if (this.state.option !== option) {
      this.props.taskOpenComments();
      this.setState({ option });
    }
  }

  onClickAttachments() {
    const option = 'attachment';
    if (this.state.option !== option) {
      this.props.taskOpenAttachments();
      this.setState({ option });
    }
  }

  render() {
    const { classes, comments, attachments } = this.props;
    const { option } = this.state;
    return (
      <Fragment>
        <Typography className={classes.title}>

          EXTRAS TO TASK:
        </Typography>

        <div
          role="button"
          tabIndex={0}
          onClick={this.onClickComments}
          onKeyPress={this.onClickComments}
          className={classes.option}
        >
          {option === 'comments' && <div className={classes.triangle} />}
          <div className={option === 'comments' ? classes.wrapperSelected : classes.wrapper}>
            <CommentIcon size="23px" />
            <Typography className={classes.item}>

              Comments&nbsp;
              (
              {comments.length}

              )
            </Typography>
          </div>
        </div>

        <div
          role="button"
          tabIndex={0}
          onClick={this.onClickActivity}
          onKeyPress={this.onClickActivity}
          className={classes.option}
        >
          {option === 'activity' && <div className={classes.triangle} />}
          <div className={option === 'activity' ? classes.wrapperSelected : classes.wrapper}>
            <History style={{ size: '20px', color: 'grey' }} />
            <Typography className={classes.item}>

              Activity
            </Typography>
          </div>
        </div>

        <div
          role="button"
          tabIndex={0}
          onClick={this.onClickReportHours}
          onKeyPress={this.onClickReportHours}
          className={classes.option}
        >
          {option === 'hours' && <div className={classes.triangle} />}
          <div className={option === 'hours' ? classes.wrapperSelected : classes.wrapper}>
            <ReportHourIcon size="23px" />
            <Typography className={classes.item}>

              Report Hours
            </Typography>
          </div>
        </div>

        <div
          role="button"
          tabIndex={0}
          onClick={this.onClickAttachments}
          onKeyPress={this.onClickAttachments}
          className={classes.option}
        >
          {option === 'attachment' && <div className={classes.triangle} />}
          <div className={option === 'attachment' ? classes.wrapperSelected : classes.wrapper}>
            <AttachmentIcon size="23px" />
            <Typography className={classes.item}>

              Attachments&nbsp;
              (
              {attachments ? attachments.length : '0'}

              )
            </Typography>
          </div>
        </div>
      </Fragment>
    );
  }
}

Sidebar.propTypes = {
  classes: PropTypes.object.isRequired,
  comments: PropTypes.array.isRequired,
  attachments: PropTypes.array.isRequired,
  taskOpenLogs: PropTypes.func.isRequired,
  taskOpenReportedHours: PropTypes.func.isRequired,
  taskOpenComments: PropTypes.func.isRequired,
  taskOpenAttachments: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    comments: onlyTaskComments(ownProps, state),
    attachments: onlyTaskAttachments(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenLogs,
    taskOpenReportedHours,
    taskOpenComments,
    taskOpenAttachments,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Sidebar));
