import {
  FETCH_GET_ALL_COLUMNS,
  FETCH_CREATE_COLUMN,
  FETCH_UPDATE_COLUMN,
  FETCH_DELETE_COLUMN,
  FETCH_UPDATE_COLUMN_ORDER,
  FETCH_COLUMN_ORDER,
  FETCH_ARCHIVE_TASKS_BY_COLUMN,
  FETCH_COMPLETE_TASKS_BY_COLUMN,
} from '../../constants/api/columns';

export const fetchGetColumns = boardId => ({
  type: FETCH_GET_ALL_COLUMNS,
  fetch: true,
  url: `/boards/${boardId}/columns`,
  params: {
    method: 'GET',
  },
});

export const fetchCreateColumn = body => ({
  type: FETCH_CREATE_COLUMN,
  fetch: true,
  url: '/columns/',
  params: {
    method: 'POST',
    body,
  },
});

export const fetchUpdateColumn = (id, body) => ({
  type: FETCH_UPDATE_COLUMN,
  fetch: true,
  url: `/columns/${id}/`,
  params: {
    method: 'PUT',
    body,
  },
});

export const fetchDeleteColumn = id => ({
  type: FETCH_DELETE_COLUMN,
  fetch: true,
  url: `/columns/${id}/`,
  params: {
    method: 'DELETE',
  },
});

export const fetchUpdateColumnOrder = (columnId, body) => ({
  type: FETCH_UPDATE_COLUMN_ORDER,
  fetch: true,
  url: `/columns/${columnId}/order`,
  params: {
    method: 'PUT',
    body, // {prev, next}
  },
});

export const fetchColumnOrder = id => ({
  type: FETCH_COLUMN_ORDER,
  fetch: true,
  url: `/boards/${id}/columns_order`,
  params: {
    method: 'GET',
  },
});

export const fetchArchiveTasksByColumn = id => ({
  type: FETCH_ARCHIVE_TASKS_BY_COLUMN,
  fetch: true,
  url: `/columns/${id}/archive_tasks`,
  params: {
    method: 'PUT',
  },
});

export const fetchCompleteTasksByColumn = id => ({
  type: FETCH_COMPLETE_TASKS_BY_COLUMN,
  fetch: true,
  url: `/columns/${id}/complete_tasks`,
  params: {
    method: 'PUT',
  },
});
