import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import PersonIcon from '@material-ui/icons/PersonAddOutlined';
import LabelIcon from '@material-ui/icons/LabelOutlined';
import TimerIcon from '@material-ui/icons/TimerOutlined';
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlined';
import ArchiveIcon from '@material-ui/icons/ArchiveOutlined';
import CheckCircleIcon from '@material-ui/icons/CheckCircleOutlined';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircleOutline';

import CustomMenu from '../lib/Menu/index';
import CustomMenuItem from '../lib/Menu/MenuItem/index';
import language from '../../constants/language';
import { fetchDeleteTask, fetchArchiveTask, fetchUpdateTask } from '../../actions/api/tasks';
import { notifySuccess } from '../../actions/notification';

import {
  toggleTaskAssignForm,
  toggleTaskLabelForm,
  toggleDropDownTask,
  toggleTaskReportForm,
  hideAll,
} from '../../actions/board';

const styles = {
  icon: {
    color: '#2a3d41',
    fontSize: 20,
  },
};

class DropdownComponent extends Component {
  constructor(props) {
    super(props);
    this.handleAssign = this.handleAssign.bind(this);
    this.handleLabel = this.handleLabel.bind(this);
    this.handleReport = this.handleReport.bind(this);
    this.handleArchive = this.handleArchive.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleUpdateCompletion = this.handleUpdateCompletion.bind(this);
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.props.toggleDropDownTask(this.props.task.id);
  }

  handleAssign(ev) {
    ev.stopPropagation();
    this.props.toggleTaskAssignForm(this.props.task.id);
  }

  handleLabel(ev) {
    ev.stopPropagation();
    this.props.toggleTaskLabelForm(this.props.task.id);
  }

  handleReport(ev) {
    ev.stopPropagation();
    this.props.toggleTaskReportForm(this.props.task.id);
  }

  handleArchive(ev) {
    ev.stopPropagation();
    this.props.fetchArchiveTask(this.props.task.id).then((res) => {
      if (res.ok) {
        this.props.notifySuccess('Archived Task Successfully');
        this.props.hideAll();
      }
    });
  }

  handleDelete(ev) {
    ev.stopPropagation();
    this.props.fetchDeleteTask(this.props.task.id).then((res) => {
      if (res.ok) {
        this.props.notifySuccess('Deleted Task Successfully');
      }
    });
  }

  handleUpdateCompletion(ev) {
    ev.stopPropagation();
    this.props.fetchUpdateTask(
      this.props.task.id,
      { completed: !this.props.task.completed },
    )
      .then((res) => {
        if (res.ok) {
          this.props.notifySuccess('Task Completion Updated Successfully');
        }
      });
  }

  render() {
    const {
      classes, task, selectedTask, showDropDownTask, selectedLanguage,
    } = this.props;
    const open = (showDropDownTask) && (task.id === selectedTask);
    return (
      <Fragment>
        <CustomMenu open={open} onClick={this.onClick}>
          <CustomMenuItem id="Assign" onClick={this.handleAssign}>
            <PersonIcon className={classes.icon} />
            &nbsp;{language[selectedLanguage].tasks.assign}
          </CustomMenuItem>
          <CustomMenuItem id="Label" onClick={this.handleLabel}>
            <LabelIcon className={classes.icon} />
            &nbsp;{language[selectedLanguage].tasks.label}
          </CustomMenuItem>
          <CustomMenuItem id="Report" onClick={this.handleReport}>
            <TimerIcon className={classes.icon} />
            &nbsp;{language[selectedLanguage].tasks.report}
          </CustomMenuItem>
          <CustomMenuItem id="Archive" onClick={this.handleArchive}>
            <ArchiveIcon className={classes.icon} />
            &nbsp;{language[selectedLanguage].tasks.archive}
          </CustomMenuItem>
          <CustomMenuItem id="Delete" onClick={this.handleDelete}>
            <DeleteOutlinedIcon className={classes.icon} />
            &nbsp;{language[selectedLanguage].tasks.delete}
          </CustomMenuItem>
          <CustomMenuItem id="Complete" onClick={this.handleUpdateCompletion}>
            {task.completed
              ? <RemoveCircleIcon className={classes.icon} />
              : <CheckCircleIcon className={classes.icon} />
            }
            &nbsp;{task.completed
              ? language[selectedLanguage].tasks.markUncompletedShort
              : language[selectedLanguage].tasks.markCompletedShort
            }
          </CustomMenuItem>
        </CustomMenu>
      </Fragment>
    );
  }
}

DropdownComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  task: PropTypes.any.isRequired,
  fetchDeleteTask: PropTypes.func.isRequired,
  fetchArchiveTask: PropTypes.func.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  toggleTaskAssignForm: PropTypes.func.isRequired,
  fetchUpdateTask: PropTypes.func.isRequired,
  toggleTaskLabelForm: PropTypes.func.isRequired,
  toggleDropDownTask: PropTypes.func.isRequired,
  hideAll: PropTypes.func.isRequired,
  selectedTask: PropTypes.string.isRequired,
  showDropDownTask: PropTypes.bool.isRequired,
  toggleTaskReportForm: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
    showDropDownTask: state.board.showDropDownTask,
    selectedTask: state.board.selectedTask,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      notifySuccess,
      fetchDeleteTask,
      fetchArchiveTask,
      fetchUpdateTask,
      toggleTaskAssignForm,
      toggleTaskLabelForm,
      toggleDropDownTask,
      toggleTaskReportForm,
      hideAll,
    }, dispatch,
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(DropdownComponent));
