import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import InfiniteScroll from 'react-infinite-scroller';
import moment from 'moment';
import 'moment/locale/es';

import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import { truncateString } from '../../../../utils';

import Dialog from '../../../lib/Dialog';
import CircularProgress from '../../../lib/CircularProgress';

import toggleShowLogs from '../../../../actions/modals/logs';
import fetchGetLogs from '../../../../actions/api/logs';
import BlankProfile from '../../../../assets/blankprofile.png';
import language from '../../../../constants/language';

/* eslint-disable react/no-unused-state, no-unused-vars, max-len, react/no-did-update-set-state */
const styles = theme => ({
  container: {
    container: {
      backgroundColor: 'white',
      flex: 1,
      maxWidth: '50%',
    },
  },
  title: {
    color: '#2a3d41',
    fontWeight: 'bold',
    fontSize: 16,
    margin: '20px 0px',
  },
  heading: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(19),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 4,
  },
  log: {
    borderTop: '1px solid #ccc',
    width: '95%',
    minWidth: '360px',
    margin: 0,
    padding: 20,
  },
  info: {
    margin: 0,
  },
  loading: {
    textAlign: 'center',
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(12),
    marginTop: 10,
  },
  rightBlock: {
    display: 'inline-block',
    float: 'right',
  },
  leftBlock: {
    display: 'inline-block',
    color: '#2a3d41',
  },
  img: {
    display: 'inline-block',
  },
  name: {
    fontSize: theme.typography.pxToRem(14),
    fontWeight: 'bold',
    display: 'inline-block',
    marginTop: 3,
    marginLeft: 10,
  },
  description: {
    fontSize: theme.typography.pxToRem(12),
  },
  date: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(11),
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
});

class LogsShowing extends Component {
  constructor(props) {
    super(props);
    this.state = {
      logs: [],
      hasMoreItems: true,
      loading: true,
      oldDate: moment(),
      isFetching: false,
      first: true,
      projectId: '',
    };
    this.handleClose = this.handleClose.bind(this);
    this.fetchPage = this.fetchPage.bind(this);
  }

  componentWillMount() {
    this.setState({ projectId: this.props.projectId });
    this.fetchPage(1);
  }

  componentDidUpdate() {
    if (this.state.projectId !== this.props.projectId) {
      this.setState({
        projectId: this.props.projectId,
        logs: [],
        hasMoreItems: true,
        loading: true,
        oldDate: moment(),
        isFetching: false,
        first: true,
      });
      this.fetchPage(1);
    }
  }

  fetchPage(n) {
    this.setState({ isFetching: true });
    this.props.fetchGetLogs(this.props.projectId, n).then((logs) => {
      this.setState((prevState) => {
        const newLogs = JSON.parse(JSON.stringify(prevState.logs));
        const hasMoreItems = Boolean(logs.body.length);
        const loading = prevState.loading ? !prevState.loading : prevState.loading;
        const first = prevState.first ? !prevState.first : prevState.first;
        const { oldDate } = prevState;
        let tmpDate = prevState.oldDate;
        logs.body.forEach((l) => {
          const logDate = moment(l.created_at);
          if (logDate.isBefore(oldDate)) {
            newLogs.push(l);
          }
          if (logDate.isBefore(tmpDate)) {
            tmpDate = logDate;
          }
        });
        return {
          logs: newLogs, hasMoreItems, loading, oldDate: tmpDate, first,
        };
      });
      this.setState({ isFetching: false });
    }).catch(e => this.setState({ isFetching: false }));
  }

  handleClose() {
    this.props.toggleShowLogs();
  }

  render() {
    const {
      classes,
      isModal,
      selectedLanguage,
      userId,
    } = this.props;
    moment.locale(selectedLanguage);
    const title = (
      <Typography className={classes.heading}>
        {language[selectedLanguage].home.activity}
      </Typography>
    );
    const loader = (
      <Typography key="loading" className={classes.loading}>
        {language[selectedLanguage].home.loading}...
      </Typography>
    );
    const items = [];
    this.state.logs.forEach((l) => {
      let name = '';
      let msg = '';
      let img = '';
      items.push(
        <Fragment key={l.created_at}>
          <div
            className={classes.log}
            style={{ backgroundColor: l.message[0].id === userId ? '#ea5941B0' : 'white' }}
          >
            <div className={classes.info}>
              {
                l.message.forEach((obj, i) => {
                  let m = obj;
                  if (m in language[selectedLanguage].activity) {
                    m = language[selectedLanguage].activity[m];
                  }
                  if (i === 0) {
                    name += m.name;
                    img += m.image;
                  } else {
                    const str = typeof m === 'object' ? `<b>${truncateString(m.name, 40)}</b>` : m;
                    msg += str;
                    msg += ' ';
                  }
                })
              }
              <div className={classes.leftBlock}>
                <div style={{ display: 'flex', flexDirection: 'row', width: '100%' }}>
                  {(!img || img === 'null')
                  && (
                    <img alt="" className={classes.customIcon} src={BlankProfile} />
                  )}
                  {(img && img !== 'null')
                  && (
                    <img alt="" className={classes.customIcon} src={img} />
                  )}
                  <Typography className={classes.name}>{truncateString(name, 40)}</Typography>
                </div>
                <Typography className={classes.description} dangerouslySetInnerHTML={{ __html: msg }} />
              </div>
              <div className={classes.rightBlock}>
                <Typography className={classes.date}>{moment(l.created_at).fromNow()}</Typography>
              </div>
            </div>
          </div>
        </Fragment>,
      );
    });
    const loadingItem = (
      <div style={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        minWidth: 200,
        minHeight: 100,
      }}
      >
        <CircularProgress style={{ position: 'unset' }} />
      </div>
    );
    const activities = (
      <div
        style={{ overflowY: 'scroll', height: '80vh', overflowX: 'hidden' }}
        ref={(ref) => { this.scrollParentRef = ref; }}
      >
        <InfiniteScroll
          pageStart={1}
          loadMore={this.fetchPage}
          hasMore={!this.state.isFetching && this.state.hasMoreItems}
          getScrollParent={() => this.scrollParentRef}
          threshold={10}
          useWindow
        >
          <div>
            {items}
          </div>
          { this.state.isFetching && loader }
        </InfiniteScroll>
      </div>
    );
    if (this.state.first) {
      return (
        <Fragment>
          { isModal
            ? (
              <Dialog
                open
                title={title}
                onClose={this.handleClose}
                clickClose={this.handleClose}
                onExited={this.handleClose}
                contentStyle={{ padding: 0 }}
              >
                <div>{loadingItem}</div>
              </Dialog>
            )
            : (
              <Fragment>
                <Typography className={classes.title}>
                  {language[selectedLanguage].home.activity}:
                </Typography>
                <div className={classes.container}>
                  {loadingItem}
                </div>
              </Fragment>
            )
          }
        </Fragment>
      );
    }
    return (
      <Fragment>
        { isModal
          ? (
            <Dialog
              open
              title={title}
              onClose={this.handleClose}
              clickClose={this.handleClose}
              onExited={this.handleClose}
              contentStyle={{ padding: 0 }}
            >
              {
              this.state.logs.length > 0
              && (
                <div>{activities}</div>
              )
              }
              {
                !this.state.logs.length
                && <Typography style={{ margin: 10 }}>{language[selectedLanguage].home.noActivities}.</Typography>
                }
            </Dialog>
          )
          : (
            <Fragment>
              <Typography className={classes.title}>
                {language[selectedLanguage].home.activity}:
              </Typography>
              <div className={classes.container}>
                {activities}
              </div>
            </Fragment>
          )
          }
      </Fragment>
    );
  }
}
LogsShowing.propTypes = {
  classes: PropTypes.object.isRequired,
  toggleShowLogs: PropTypes.func.isRequired,
  fetchGetLogs: PropTypes.func.isRequired,
  projectId: PropTypes.string.isRequired,
  isModal: PropTypes.bool.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  userId: PropTypes.string.isRequired,
};
const mapStateToProps = (state) => {
  return {
    projectId: state.modal.logs.selectedProject,
    selectedLanguage: state.user.language,
    userId: state.auth.id,
  };
};
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleShowLogs,
    fetchGetLogs,
  }, dispatch);
};
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(LogsShowing));
