import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import PrimaryButton from '../../../../../../lib/Button/PrimaryButton';
import Form from '../../../../../../lib/Form';
import { fetchCreateComment } from '../../../../../../../actions/api/comments';
import { notifySuccess } from '../../../../../../../actions/notification';
import language from '../../../../../../../constants/language';

const styles = theme => ({
  input: {
    fontSize: theme.typography.pxToRem(12),
    color: '#2a3d41',
    display: 'flex',
  },
  icon: {
    marginRight: 5,
    color: '#ccc',
  },
  textField: {
    backgroundColor: 'white',
    borderColor: 'black',
    flex: 1,
    width: '100%',
  },
  button: {
    marginTop: '5px',
    boxShadow: 'unset',
    marginRight: 10,
    color: 'white',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  notchedOutline: {
    borderColor: '#b0b5b7 !important',
  },
});

class CommentsForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: '',
      isFetching: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.commentChange = this.commentChange.bind(this);
  }

  commentChange(value) {
    this.setState({ text: value });
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    const { text } = this.state;
    const { task } = this.props;
    this.setState({ isFetching: true });
    this.props.fetchCreateComment(task.id, { text })
      .then((response) => {
        this.setState({ isFetching: false });
        if (response.ok) {
          this.setState({ text: '' });
          this.props.notifySuccess('The comment was added succesfully');
        }
      });
  }

  render() {
    const { classes, selectedLanguage } = this.props;
    const { text, isFetching } = this.state;
    return (
      <Fragment>
        <Form>
          <div className={classes.input}>
            <TextField
              placeholder="Write a comment..."
              value={text}
              onChange={e => this.commentChange(e.target.value)}
              variant="outlined"
              multiline
              className={classes.textField}
              rows="3"
              InputProps={{
                classes: {
                  notchedOutline: classes.notchedOutline,
                },
              }}
            />
          </div>
          <div style={{ display: 'flex', alignSelf: 'flex-end' }}>
            <PrimaryButton
              type="submit"
              loading={isFetching}
              onClick={this.handleSubmit}
              className={classes.button}
            >
              {language[selectedLanguage].tasks.comment}
            </PrimaryButton>
          </div>
        </Form>
      </Fragment>
    );
  }
}

CommentsForm.propTypes = {
  classes: PropTypes.object.isRequired,
  fetchCreateComment: PropTypes.func.isRequired,
  task: PropTypes.object.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchCreateComment,
    notifySuccess,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(CommentsForm));
