import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import moment from 'moment';

import DeadlineIcon from '../../../../../../lib/Logo/Deadline';

import { taskOpenDeadline } from '../../../../../../../actions/task';

const styles = {
  container: {
    display: 'flex',
    alignItems: 'center',
    outline: 'none',
  },
  maintext: {
    fontSize: '14px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
  secondarytext: {
    fontSize: '12px',
    fontWeight: 'normal',
    color: '#2a3d41',
    paddingRight: '10px',
  },
};

class Deadline extends Component {
  constructor(props) {
    super(props);
    this.clickBtn = this.clickBtn.bind(this);
  }

  clickBtn() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenDeadline();
    }
  }

  render() {
    const { deadline, classes } = this.props;
    let date = moment(deadline, 'DD/MM/YYYY');
    if (!date.isValid()) {
      date = moment(deadline, 'YYYY-MM-DDTHH:mm:ss.SSSSZ');
    }

    return (
      <div
        role="button"
        tabIndex={0}
        onKeyPress={this.clickBtn}
        onClick={this.clickBtn}
        className={classes.container}
        style={
         this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}}
      >
        <DeadlineIcon size="23px" style={{ paddingRight: 5, paddingBottom: 3 }} />
        <Typography className={classes.maintext}>
          <b>Deadline: </b>
&nbsp;
        </Typography>
        <Typography className={classes.secondarytext}>
          {date.zone(0).format('DD MMM YYYY')}
        </Typography>
      </div>
    );
  }
}

Deadline.propTypes = {
  classes: PropTypes.object.isRequired,
  deadline: PropTypes.string.isRequired,
  taskOpenDeadline: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenDeadline,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Deadline));
