import { createSelector } from 'redux-orm';

import orm from '../reducers/orm';

import { groupByArray } from '../utils';

const getRef = obj => (obj != null ? obj.ref : null);

export const onlyTaskHours = createSelector(
  orm,
  (_, state) => state.orm,
  (props, state) => state.board.selectedTask,
  (session, taskId) => {
    const task = session.Task.get(o => o.id === taskId).ref;
    if (task === null) return null;
    const taskHours = session.Hour.filter((o) => {
      return o.task_id === taskId;
    }).toRefArray();
    const arr = groupByArray(taskHours, 'user_id');
    arr.map(obj => Object.assign(obj, {
      hours: obj.values.map(obj2 => obj2.hours).reduce((a, b) => +a + +b, 0),
    }));
    const taskgrouped = arr.map((user) => {
      return Object.assign({}, user, {
        user: getRef(session.Member
          .get(o => o.id === user.key)),
      });
    });
    return taskgrouped;
  },
);

function getUser(session, hour) {
  return getRef(session.Member.get(o => o.id === hour.user_id));
}

export const getHours = createSelector(
  orm,
  (_, state) => state.orm,
  (props, state) => state.board.selectedTask,
  (session, taskId) => {
    const task = session.Task.get(o => o.id === taskId).ref;
    if (task === null) return null;
    const taskHours = session.Hour.filter((o) => {
      return o.task_id === taskId;
    }).toRefArray();
    const hours = taskHours.map((hour) => {
      const user = getUser(session, hour);
      return Object.assign({}, hour,
        { user });
    });
    return hours;
  },
);
