import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import Typography from '@material-ui/core/Typography';
import Logo from '../../../../../../lib/Logo/Member';
import Item from '../SidebarItem';
import BlankProfile from '../../../../../../../assets/blankprofile.png';
import { taskOpenMembers } from '../../../../../../../actions/task';
import { onlyMembers } from '../../../../../../../selectors/members';
import { fetchTaskAssign, fetchTaskUnassign } from '../../../../../../../actions/api/members';

import PopOver from '../PopOver';

const styles = ({
  title: {
    alignSelf: 'center',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
  item: {
    color: '#ea5941',
    '&$checked': {
      color: '#ea5941',
    },
  },
  checked: {},
  icon: {
    color: '#2a3d41',
    fontSize: 20,
  },
});

class MembersPopover extends Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
    };
    this.ref = React.createRef();
    this.onClickMembers = this.onClickMembers.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
  }

  componentDidMount() {
    this.setState({ anchorEl: this.ref.current });
  }

  onClickMembers() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenMembers();
    }
  }

  handleToggle(event, userId, assigned) {
    if (event) event.stopPropagation();
    const { taskId } = this.props;
    if (assigned) {
      this.props.fetchTaskUnassign(taskId, userId);
    } else {
      this.props.fetchTaskAssign(taskId, { user_id: userId });
    }
  }

  render() {
    const { open, classes, members } = this.props;
    const { anchorEl } = this.state;
    return (
      <Fragment>
        <div ref={this.ref} style={{ marginTop: '10px' }}>
          <Item
            onClick={this.onClickMembers}
          >
            <Logo size="23px" />
            <Typography
              className={classes.title}
              style={
                this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}}
            >

              Members
            </Typography>
          </Item>
        </div>
        {
          anchorEl !== null
          && (
            <PopOver onClose={this.onClickMembers} anchorEl={anchorEl} open={open}>
              <List>
                {members.map(mem => (
                  <ListItem
                    key={mem.id}
                    dense
                    button
                    onClick={event => this.handleToggle(event, mem.id, mem.assigned)}
                  >
                    {(!mem.image || mem.image === 'null')
                      && (
                        <img alt="" className={classes.customIcon} src={BlankProfile} />
                      )}
                    {(mem.image && mem.image !== 'null')
                      && (
                        <img alt="" className={classes.customIcon} src={mem.image} />
                      )}
                    {mem.name
                      && (
                        <ListItemText primary={`${mem.name} - ${mem.role.charAt(0).toUpperCase() + mem.role.slice(1)}`} />
                      )
                    }
                    {!mem.name
                      && (
                        <ListItemText primary={`${mem.email.split('@')[0].charAt(0).toUpperCase() + mem.email.split('@')[0].slice(1)} - ${mem.role.charAt(0).toUpperCase() + mem.role.slice(1)}`} />
                      )
                    }
                    <ListItemSecondaryAction>
                      <Checkbox
                        classes={{ root: classes.item, checked: classes.checked }}
                        onChange={event => this.handleToggle(event, mem.id, mem.assigned)}
                        checked={mem.assigned}
                      />
                    </ListItemSecondaryAction>
                  </ListItem>
                ))}
              </List>
            </PopOver>
          )
        }
      </Fragment>
    );
  }
}

MembersPopover.propTypes = {
  open: PropTypes.bool.isRequired,
  taskOpenMembers: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  members: PropTypes.array.isRequired,
  taskId: PropTypes.string.isRequired,
  fetchTaskAssign: PropTypes.func.isRequired,
  fetchTaskUnassign: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    open: state.task.openMembers,
    members: onlyMembers(ownProps, state),
    taskId: state.board.selectedTask,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenMembers,
    fetchTaskAssign,
    fetchTaskUnassign,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(MembersPopover));
