import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { GoGitCommit } from 'react-icons/go';
import RejectIcon from '@material-ui/icons/Clear';

import { fetchDeleteCommit } from '../../../../../../../actions/api/tasks';

const styles = {
  container: {
    display: 'flex',
    alignItems: 'center',
    outline: 'none',
    minHeight: 15,
  },
  maintext: {
    fontSize: '14px',
    fontWeight: 'bold',
    color: '#2a3d41',
    marginLeft: 10,
    display: 'flex',
    alignItems: 'center',
    minHeight: 15,
  },
  secondarytext: {
    fontSize: '12px',
    fontWeight: 'normal',
    alignSelf: 'center',
    color: '#298fca',
    fontFamily: 'Roboto,arial,sans-serif',
    textDecoration: 'none',
  },
  icon: {
    color: '#CBCBCB',
    marginLeft: 3,
  },
  reject: {
    cursor: 'pointer',
    maxHeight: 15,
    maxWidth: 15,
    float: 'right',
    color: '#2e4144',
    '&:hover': {
      color: 'black',
    },
  },
};

class Commit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mouse: false,
    };
    this.handleMouseEnter = this.handleMouseEnter.bind(this);
    this.handleMouseLeave = this.handleMouseLeave.bind(this);
  }

  handleMouseEnter() {
    this.setState({ mouse: true });
  }

  handleMouseLeave() {
    this.setState({ mouse: false });
  }

  deleteCommit(id) {
    this.props.fetchDeleteCommit(id);
  }

  render() {
    const { commit, classes } = this.props;
    const { mouse } = this.state;
    return (
      <Fragment>
        <div
          className={classes.container}
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
        >
          <GoGitCommit className={classes.icon} />
          <Typography className={classes.maintext}>
            <b>Commit: </b>

            &nbsp;
            <a target="_blank" rel="noopener noreferrer" className={classes.secondarytext} href={commit.url}>{commit.message}</a>
          </Typography>
          <div style={{ minHeight: 15, minWidth: 15, marginLeft: 3 }}>
            {(mouse)
              && (
                <RejectIcon
                  onClick={() => this.deleteCommit(commit.id)}
                  className={classes.reject}
                />
              )
            }
          </div>
        </div>
      </Fragment>
    );
  }
}

Commit.propTypes = {
  classes: PropTypes.object.isRequired,
  commit: PropTypes.object.isRequired,
  fetchDeleteCommit: PropTypes.func.isRequired,
};

const mapStateToProps = () => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchDeleteCommit,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Commit));
