import {
  TASK_OPEN_NAME,
  TASK_OPEN_DESCRIPTION,
  TASK_OPEN_MEMBERS,
  TASK_OPEN_MILESTONE,
  TASK_OPEN_LABELS,
  TASK_OPEN_STARTING_DATE,
  TASK_OPEN_DEADLINE,
  TASK_OPEN_PRIORITY,
  TASK_OPEN_ROADBLOCK,
  TASK_OPEN_CHECKLIST,
  TASK_OPEN_EXPECTED_HOURS,
  TASK_SEARCH,
  TASK_OPEN_REPORT_HOURS,
  TASK_OPEN_REPORTED_HOURS,
  TASK_OPEN_COMMENTS,
  TASK_OPEN_ATTACHMENT,
  TASK_OPEN_LOGS,
  TASK_OPEN_COMMITS,
} from '../constants/task';

export const taskOpenName = () => ({
  type: TASK_OPEN_NAME,
});

export const taskOpenDescription = () => ({
  type: TASK_OPEN_DESCRIPTION,
});

export const taskOpenMembers = () => ({
  type: TASK_OPEN_MEMBERS,
});

export const taskOpenMilestone = () => ({
  type: TASK_OPEN_MILESTONE,
});

export const taskOpenLabels = () => ({
  type: TASK_OPEN_LABELS,
});

export const taskOpenCommits = () => ({
  type: TASK_OPEN_COMMITS,
});

export const taskOpenStartingDate = () => ({
  type: TASK_OPEN_STARTING_DATE,
});

export const taskOpenDeadline = () => ({
  type: TASK_OPEN_DEADLINE,
});

export const taskOpenPriority = () => ({
  type: TASK_OPEN_PRIORITY,
});

export const taskOpenAttachment = () => ({
  type: TASK_OPEN_ATTACHMENT,
});

export const taskOpenRoadblock = () => ({
  type: TASK_OPEN_ROADBLOCK,
});

export const taskOpenChecklist = () => ({
  type: TASK_OPEN_CHECKLIST,
});

export const taskOpenExpectedHours = () => ({
  type: TASK_OPEN_EXPECTED_HOURS,
});

export const taskSearch = body => ({
  type: TASK_SEARCH,
  payload: {
    body,
  },
});

export const taskOpenLogs = () => ({
  type: TASK_OPEN_LOGS,
});

export const taskOpenReportHours = () => ({
  type: TASK_OPEN_REPORT_HOURS,
});

export const taskOpenReportedHours = () => ({
  type: TASK_OPEN_REPORTED_HOURS,
});

export const taskOpenComments = () => ({
  type: TASK_OPEN_COMMENTS,
});

export const taskOpenAttachments = () => ({
  type: TASK_OPEN_ATTACHMENT,
});
