import { createSelector } from 'redux-orm';

import orm from '../reducers/orm';

// const getRef = obj => (obj != null ? obj.ref : null);

// Return all the labels of a board
export const allLabels = createSelector(
  orm,
  (_, state) => state.orm,
  (props, state) => state.modal.labels.selectedBoardId,
  (session, boardId) => {
    const boardLabels = session.Board.get(o => o.id === boardId).ref.labels;
    const labels = session.Label
      .filter(o => boardLabels.includes(o.id))
      .orderBy(o => o.name.toUpperCase())
      .toRefArray();
    return labels;
  },
);

/*
Returns all the labels of a board. Each label comes with an attribute
assigned<bool>, which is used to know if the label is assigned to the task
or not.
*/
export const onlyLabels = createSelector(
  orm,
  (_, state) => state.orm,
  (props, state) => state.board.selectedTask,
  (props, state) => state.user.selectedBoardId,
  (session, taskId, boardId) => {
    const task = session.Task.get(o => o.id === taskId).ref;
    if (task === null) return null;
    const taskLabels = task.labels;
    const boardLabels = session.Board.get(o => o.id === boardId).ref.labels;
    const labels = session.Label
      .filter(o => boardLabels.includes(o.id))
      .orderBy(o => o.name.toUpperCase())
      .toRefArray();
    return labels.map((label) => {
      return Object.assign({}, label, {
        assigned: taskLabels.includes(label.id),
      });
    });
  },
);

export const onlyTaskLabels = createSelector(
  orm,
  (_, state) => state.orm,
  (prop, state) => state.board.selectedTask,
  (session, taskId) => {
    const task = session.Task.get(o => o.id === taskId).ref;
    if (task === null) return null;
    const taskLabels = task.labels;
    return session.Label
      .filter(o => taskLabels.includes(o.id))
      .toRefArray();
  },
);
