import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Form from '../../lib/Form';
import TextField from '../../lib/TextField';
import PrimaryButton from '../../lib/Button/PrimaryButton';

import { fetchCreateColumn } from '../../../actions/api/columns';
import { toggleCreateColumnForm } from '../../../actions/board';

const styles = {
  container: {
    margin: '10px 5px',
    border: '1px solid #2a3d41',
    height: 600,
    minWidth: 280,
    backgroundColor: 'white',
    boxShadow: '2px 2px 8px 0 rgba(0, 0, 0, 0.2)',
    display: 'flex',
    flexDirection: 'column',
  },
  header: {
    display: 'flex',
    flexDirection: 'column',
    padding: 20,
    flexShrink: 0,
    borderBottom: '1px solid #2a3d41',
  },
};

class ColumnComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(name) {
    this.setState({ name });
  }

  handleCancel(event) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
      event.nativeEvent.stopImmediatePropagation();
    }
    this.props.toggleCreateColumnForm();
  }

  handleSubmit(event) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
      event.nativeEvent.stopImmediatePropagation();
    }
    this.props.fetchCreateColumn(
      { board_id: this.props.boardId, name: this.state.name.trim() },
    ).then((response) => {
      if (response.ok) {
        this.props.toggleCreateColumnForm();
      }
    });
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.container}>
        <Form onSubmit={this.handleSubmit}>
          <div className={classes.header}>
            <TextField
              onChange={this.handleChange}
              type="text"
              name={this.state.name}
              label="Column Name"
              id="name"
              autoFocus
            />
            <div style={{ display: 'flex', flex: 1 }}>
              <PrimaryButton
                onClick={this.handleCancel}
              >

                Cancel
              </PrimaryButton>
              <div style={{ flexGrow: 1 }} />
              <PrimaryButton
                type="submit"
              >

                Create
              </PrimaryButton>
            </div>
          </div>
        </Form>
      </div>
    );
  }
}

ColumnComponent.propTypes = {
  boardId: PropTypes.string.isRequired,
  toggleCreateColumnForm: PropTypes.func.isRequired,
  fetchCreateColumn: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = () => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleCreateColumnForm,
    fetchCreateColumn,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ColumnComponent));
