const f = (l) => {};

const Header = ({
  classes,
  label,
  onClick,
  customCursor,
  customWidth,
  customHeight,
  displayName,
}) => {
  const hasBorder = label.color.toUpperCase() === '#FFFFFF' ? '1px solid' : '';
  return (
    <div
      role="button"
      tabIndex={0}
      onClick={() => onClick(label.id)}
      onKeyPress={() => onClick(label.id)}
      style={{
        backgroundColor: label.color,
        border: hasBorder,
        width: customWidth,
        height: customHeight,
        cursor: customCursor,
      }}
      className={classes.label}
    >
      {
        displayName && (
        <Typography className={classes.name} style={{ color: 'white' }}>
          {/*
            <Typography className={classes.name} style={{ color: colorContrast(label.color) }}>
          */}
          {label.name}
        </Typography>
        )
      }
    </div>
  );
};

Header.propTypes = {
  classes: PropTypes.object.isRequired,
  label: PropTypes.object.isRequired,
  onClick: PropTypes.func,
  customWidth: PropTypes.string,
  customHeight: PropTypes.string,
  customCursor: PropTypes.string,
  displayName: PropTypes.bool,
};

Header.defaultProps = {
  onClick: f,
  customWidth: '',
  customHeight: '',
  customCursor: 'auto',
  displayName: true,
};

const mapStateToProps = () => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Header));
