import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import AcceptIcon from '@material-ui/icons/CheckBox';
import RejectIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Dialog from '../../../lib/Dialog';
import CircularProgress from '../../../lib/CircularProgress';

import { toggleRepoAssignForm } from '../../../../actions/project';
import {
  fetchGithubRepo,
  fetchProjectRepo,
  fetchProjectRepoAssign,
  fetchProjectRepoUnassign,
} from '../../../../actions/api/projects';

const styles = theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  item: {
    color: '#ea5941',
    '&$checked': {
      color: '#ea5941',
    },
  },
  checked: {},
  icon: {
    color: '#2a3d41',
    fontSize: 20,
  },
  checkbox: {
    color: '#e53935',
    cursor: 'pointer',
    '&:hover': {
      color: '#ff6f60',
    },
  },
});

class GithubRepoForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      githubRepos: null,
      projectRepos: null,
    };
    this.handleClose = this.handleClose.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
    this.handleTotal = this.handleTotal.bind(this);
  }

  componentWillMount() {
    const { projectId } = this.props;
    this.props.fetchGithubRepo(projectId)
      .then((response) => {
        if (response.ok) {
          this.setState({ githubRepos: response.body });
          this.props.fetchProjectRepo(projectId)
            .then((response2) => {
              if (response2.ok) {
                this.setState({ projectRepos: response2.body });
              }
            });
        }
      });
  }

  handleTotal() {
    const { githubRepos, projectRepos } = this.state;
    const checkAndAdd = (name) => {
      const found = projectRepos.some((el) => {
        return (el.name === name.name && el.owner === name.owner);
      });
      if (!found) { projectRepos.push(name); }
    };
    githubRepos.forEach((githubrepo) => {
      checkAndAdd(githubrepo);
    });
    return projectRepos;
  }

  handleToggle(event, githubrepo) {
    const { projectRepos } = this.state;
    const newProjectRepos = projectRepos.filter((obj) => {
      return obj.id;
    });
    if (event) event.stopPropagation();
    const { projectId } = this.props;
    if (githubrepo.id) {
      this.props.fetchProjectRepoUnassign(projectId, githubrepo.id)
        .then((response) => {
          if (response.ok) {
            const newProjectRepos2 = projectRepos.filter((obj) => {
              return obj.id !== githubrepo.id;
            });
            this.setState({ projectRepos: newProjectRepos2 });
          }
        });
    } else {
      this.props.fetchProjectRepoAssign(
        projectId, { name: githubrepo.name, owner: githubrepo.owner },
      ).then((response) => {
        if (response.ok) {
          newProjectRepos.push(response.body);
          this.setState({ projectRepos: newProjectRepos });
        }
      });
    }
  }

  handleClose() {
    this.props.toggleRepoAssignForm();
  }

  render() {
    const { showAssignForm, classes } = this.props;
    const { githubRepos, projectRepos } = this.state;
    const title = 'Assign Repositories to Project';
    if (!githubRepos || !projectRepos) {
      return (
        <Dialog
          open={showAssignForm}
          title={title}
          onClose={this.handleClose}
          clickClose={this.handleClose}
          onExit={this.handleClose}
        >
          <div style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            minWidth: 200,
          }}
          >
            <CircularProgress style={{ position: 'unset' }} />
          </div>
        </Dialog>
      );
    }
    const allRepos = this.handleTotal();
    return (
      <Dialog
        open={showAssignForm}
        title={title}
        onClose={this.handleClose}
        clickClose={this.handleClose}
        onExit={this.handleClose}
      >
        <div className={classes.root}>
          <List>
            {allRepos.map(gitrepo => (
              <ListItem
                key={gitrepo.name}
                dense
                button
                onClick={event => this.handleToggle(event, gitrepo)}
              >
                <AccountCircle className={classes.icon} />
                <ListItemText primary={gitrepo.name.concat(' - ').concat(gitrepo.owner)} />
                <ListItemSecondaryAction>
                  {gitrepo.id
                  && (
                    <AcceptIcon
                      onClick={event => this.handleToggle(event, gitrepo)}
                      className={classes.checkbox}
                    />
                  )}
                  {!gitrepo.id
                  && (
                    <RejectIcon
                      onClick={event => this.handleToggle(event, gitrepo)}
                      className={classes.checkbox}
                    />
                  )}
                </ListItemSecondaryAction>
              </ListItem>
            ))}
          </List>
        </div>
      </Dialog>
    );
  }
}

GithubRepoForm.propTypes = {
  classes: PropTypes.object.isRequired,
  showAssignForm: PropTypes.bool.isRequired,
  toggleRepoAssignForm: PropTypes.func.isRequired,
  projectId: PropTypes.string.isRequired,
  fetchGithubRepo: PropTypes.func.isRequired,
  fetchProjectRepo: PropTypes.func.isRequired,
  fetchProjectRepoAssign: PropTypes.func.isRequired,
  fetchProjectRepoUnassign: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  return {
    isFetching: state.app.isFetching,
    showAssignForm: state.project.showAssignRepo,
    projectId: state.project.selectedProject,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleRepoAssignForm,
    fetchGithubRepo,
    fetchProjectRepo,
    fetchProjectRepoAssign,
    fetchProjectRepoUnassign,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(GithubRepoForm));
