import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import TwitterPicker from '../../../../../../lib/TwitterPicker';

import TextField from '../../../../../../lib/TextField';
import Form from '../../../../../../lib/Form';
import PrimaryButton from '../../../../../../lib/Button/PrimaryButton';

import { fetchCreateLabel } from '../../../../../../../actions/api/labels';

import language from '../../../../../../../constants/language';

const styles = {
  root: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    margin: 10,
  },
  button: {
    marginTop: 10,
    display: 'flex',
    alignSelf: 'flex-end',
  },
  input: {
    width: '100%',
  },
};

class LabelsModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      color: '#2c7cff',
    };
    this.onCreate = this.onCreate.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onColorChange = this.onColorChange.bind(this);
  }

  onCreate(e) {
    e.preventDefault();
    const body = {
      name: this.state.name,
      color: this.state.color,
      board_id: this.props.boardId,
    };
    this.props.fetchCreateLabel(body)
      .then((res) => {
        if (res.ok) {
          this.props.onSubmit();
        }
      });
  }

  onChange(value) {
    this.setState({ name: value });
  }

  onColorChange(newColor) {
    this.setState({ color: newColor.hex });
  }

  render() {
    const { classes, selectedLanguage } = this.props;
    const { name, color } = this.state;
    return (
      <div className={classes.root}>
        <Form className={classes.input}>
          <TextField
            value={name}
            label={language[selectedLanguage].modals.taskModalEditLabelModalCreateNameLabel}
            onChange={this.onChange}
            fullWidth
            id="name"
            onKeyDown={(e) => {
              if (e.key === 'Enter') this.onCreate(e);
            }}
          />
        </Form>
        <div className={classes.picker}>
          <TwitterPicker onChangeComplete={this.onColorChange} color={color} />
        </div>
        <div className={classes.button}>
          <PrimaryButton
            type="button"
            onClick={this.onCreate}
          >
            {language[selectedLanguage].modals.taskModalEditLabelModalCreateButton}
          </PrimaryButton>
        </div>
      </div>
    );
  }
}

LabelsModal.propTypes = {
  classes: PropTypes.object.isRequired,
  fetchCreateLabel: PropTypes.func.isRequired,
  boardId: PropTypes.string.isRequired,
  onSubmit: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    boardId: state.user.selectedBoardId,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchCreateLabel,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(LabelsModal));
