import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Logo from '../../../../../../lib/Logo/Priority';
import Item from '../SidebarItem';

import Form from './Form';

import { taskOpenPriority } from '../../../../../../../actions/task';

import PopOver from '../PopOver';

const styles = ({
  title: {
    alignSelf: 'center',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
});

class PriorityPopOver extends Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
    };
    this.ref = React.createRef();
    this.onClickPriority = this.onClickPriority.bind(this);
  }

  componentDidMount() {
    this.setState({ anchorEl: this.ref.current });
  }

  onClickPriority() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenPriority();
    }
  }

  render() {
    const { open, classes } = this.props;
    const { anchorEl } = this.state;
    return (
      <Fragment>
        <div ref={this.ref} style={{ marginTop: '10px' }}>
          <Item
            onClick={this.onClickPriority}
          >
            <Logo size="23px" />
            <Typography
              className={classes.title}
              style={
                this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}}
            >

              Priority
            </Typography>
          </Item>
        </div>
        {
          anchorEl !== null
          && (
            <PopOver onClose={this.onClickPriority} anchorEl={anchorEl} open={open}>
              <Form />
            </PopOver>
          )
        }
      </Fragment>
    );
  }
}

PriorityPopOver.propTypes = {
  open: PropTypes.bool.isRequired,
  taskOpenPriority: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    open: state.task.openPriority,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenPriority,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(PriorityPopOver));
