import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import moment from 'moment';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Form from '../../../lib/Form';
import PrimaryButton from '../../../lib/Button/PrimaryButton';
import Dialog from '../../../lib/Dialog';
import language from '../../../../constants/language';
import { toggleTaskReportForm } from '../../../../actions/board';
import { notifySuccess, notifyError } from '../../../../actions/notification';
import { fetchAddHours } from '../../../../actions/api/hours';

const styles = theme => ({
  form: {
    display: 'flex',
    flexDirection: 'column',
  },
  input: {
    justifySelf: 'center',
    alignSelf: 'center',
    width: '100%',
  },
  grid: {
    display: 'flex',
    flexDirection: 'row',
  },
  text: {
    fontSize: '16px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
    alignSelf: 'center',
  },
  button: {
    boxShadow: 'unset',
    width: '130px',
    color: 'white',
    fontSize: theme.typography.pxToRem(12),
    alignSelf: 'center',
    float: 'center',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  cssUnderline: {
    '&:after': {
      borderBottomColor: '#ea5941',
    },
  },
});

class TaskReportForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      workDay: null,
      reportedHours: null,
      isFetching: false,
    };
    this.onClose = this.onClose.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.hoursChange = this.hoursChange.bind(this);
    this.workDayChange = this.workDayChange.bind(this);
    this.denyChange = this.denyChange.bind(this);
  }

  componentDidMount() {
    this.setState({ workDay: moment().format('YYYY-MM-DD') });
  }

  onClose() {
    this.props.toggleTaskReportForm();
  }

  handleSubmit(event) {
    if (event) event.stopPropagation();
    const {
      workDay,
      reportedHours,
    } = this.state;
    const { taskId } = this.props;
    if (workDay && reportedHours) {
      this.setState({ isFetching: true });
      this.props.fetchAddHours(taskId,
        { hours: reportedHours, date: workDay })
        .then((response) => {
          this.setState({ isFetching: false });
          if (response.ok) {
            this.props.toggleTaskReportForm();
            this.props.notifySuccess('Hours updated');
          }
        });
    } else {
      this.props.toggleTaskReportForm();
      this.props.notifyError('Fields can not be empty');
    }
  }

  denyChange(event) {
    if (event.key === 'Enter') {
      this.handleSubmit();
    } else if (event.key.match(/^[0-9.]+$/) === null) {
      event.preventDefault();
    } else if ((event.target.value.match(/\./) && event.target.value.match(/[.]$/) === null)) {
      event.preventDefault();
    }
  }

  hoursChange(value) {
    this.setState({ reportedHours: value });
  }

  workDayChange(value) {
    this.setState({ workDay: value });
  }

  render() {
    const { classes, showReportForm, selectedLanguage } = this.props;
    const { isFetching } = this.state;
    const {
      workDay, reportedHours,
    } = this.state;
    return (
      <Dialog
        open={showReportForm}
        onClose={this.onClose}
        clickClose={this.onClose}
        onExit={this.onClose}
      >
        <Form onSubmit={this.handleSubmit}>
          <div className={classes.grid}>
            <div className={classes.input}>
              <Typography className={classes.text}>

                {language[selectedLanguage].tasks.hours}
              </Typography>
              <TextField
                value={reportedHours || ''}
                type="number"
                onKeyPress={(e) => {
                  this.denyChange(e);
                }}
                onChange={e => this.hoursChange(e.target.value)}
                style={{ width: '35px' }}
                InputProps={{
                  inputProps: {
                    min: 0,
                    step: '1',
                    classes: {
                      underline: classes.cssUnderline,
                    },
                  },
                }}
              />
            </div>
            <div className={classes.input} style={{ marginLeft: '30px' }}>
              <Typography className={classes.text}>

                {language[selectedLanguage].tasks.workDay}
              </Typography>
              <TextField
                value={workDay}
                type="date"
                onChange={e => this.workDayChange(e.target.value)}
                style={{ width: '150px' }}
                InputProps={{
                  classes: {
                    underline: classes.cssUnderline,
                  },
                }}
              />
            </div>
          </div>
          <div style={{ float: 'right', marginTop: '10px' }}>
            <PrimaryButton
              type="submit"
              loading={isFetching}
              onClick={this.handleSubmit}
              className={classes.button}
            >

              {language[selectedLanguage].tasks.addHours}
            </PrimaryButton>
          </div>
        </Form>
      </Dialog>
    );
  }
}

TaskReportForm.propTypes = {
  classes: PropTypes.object.isRequired,
  showReportForm: PropTypes.bool.isRequired,
  toggleTaskReportForm: PropTypes.func.isRequired,
  taskId: PropTypes.string.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  fetchAddHours: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
    showReportForm: state.board.showReportTaskForm,
    taskId: state.board.selectedTask,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleTaskReportForm,
    notifySuccess,
    notifyError,
    fetchAddHours,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(TaskReportForm));
