import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';

import PinIcon from '../../../../lib/Logo/OrangePin';

import { boardName } from '../../../../../selectors/boards';
import { fetchUpdateTask } from '../../../../../actions/api/tasks';
import { taskOpenName } from '../../../../../actions/task';
import language from '../../../../../constants/language';

/* eslint-disable jsx-a11y/interactive-supports-focus */
const styles = theme => ({
  grid: {
    display: 'flex',
    alignItems: 'center',
  },
  padding: {
    padding: 10,
  },
  title: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(24),
    fontWeight: 'bold',
    maxWidth: 600,
  },
  board: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(12),
  },
  boardName: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(12),
    fontWeight: 'bold',
  },
  closeIcon: {
    color: '#ea5941',
  },
  inputContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  input: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(24),
    fontWeight: 'bold',
    padding: 10,
  },
  button: {
    height: 20,
    boxShadow: 'unset',
    marginLeft: 10,
    color: 'white',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  completionButton: {
    height: 20,
    boxShadow: 'unset',
    color: 'white',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  uncompletionButton: {
    height: 20,
    boxShadow: 'unset',
    color: 'white',
    backgroundColor: '#7a7a7a',
    '&:hover': {
      backgroundColor: '#606060',
    },
  },
  notchedOutline: {
    borderColor: '#ea5941 !important',
  },
});

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: props.taskName,
    };
    this.handleChange = this.handleChange.bind(this);
    this.openInput = this.openInput.bind(this);
    this.closeInput = this.closeInput.bind(this);
    this.fetchUpdateTaskName = this.fetchUpdateTaskName.bind(this);
    this.fetchUpdateTaskCompletion = this.fetchUpdateTaskCompletion.bind(this);
  }

  handleChange(e) {
    if (e.target.value && e.target.value.length) {
      if (e.target.value.length > 142) return;
    }
    this.setState({ text: e.target.value });
  }

  openInput() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenName();
    }
  }

  closeInput() {
    this.props.taskOpenName();
    this.setState({ text: this.props.taskName });
  }

  fetchUpdateTaskName() {
    this.props.fetchUpdateTask(this.props.taskId, { name: this.state.text })
      .then(() => {
        this.props.taskOpenName();
      });
  }

  fetchUpdateTaskCompletion() {
    this.props.fetchUpdateTask(this.props.taskId, { completed: !this.props.taskCompleted });
  }

  render() {
    const {
      open, classes, bName, selectedLanguage, taskCompleted,
    } = this.props;
    const placeholder = 'Task name...';
    const taskName = this.state.text;

    return (
      <DialogTitle className={classes.padding}>
        <div className={classes.grid}>
          <PinIcon size="50px" />
          <div style={{ flex: 1 }}>
            {
              !open
              && (
                <div>
                  <div
                    role="button"
                    onClick={this.openInput}
                    onKeyPress={this.openInput}
                    style={this.props.projectRole === 'viewer' ? {} : { cursor: 'pointer' }}
                  >
                    <Typography className={classes.title}>
                      {taskName}
                    </Typography>
                  </div>
                  <div style={{ display: 'flex' }}>
                    <Typography className={classes.board}>

                      BOARD:&nbsp;
                    </Typography>
                    <Typography className={classes.boardName}>
                      {bName.toUpperCase()}
                    </Typography>
                  </div>
                  <div>
                    <Button
                      className={taskCompleted
                        ? classes.uncompletionButton
                        : classes.completionButton}
                      size="small"
                      variant="contained"
                      onClick={this.fetchUpdateTaskCompletion}
                    >
                      {taskCompleted && language[selectedLanguage].tasks.markUncompleted}
                      {!taskCompleted && language[selectedLanguage].tasks.markCompleted}
                    </Button>
                  </div>
                </div>
              )
            }
            {
              open
              && (
                <div className={classes.inputContainer}>
                  <TextField
                    id="outlined-full-width"
                    placeholder={placeholder}
                    autoFocus
                    margin="none"
                    variant="outlined"
                    value={taskName}
                    onChange={this.handleChange}
                    style={{ flex: 1 }}
                    InputProps={{
                      classes: {
                        input: classes.input,
                        notchedOutline: classes.notchedOutline,
                      },
                    }}
                  />
                  <Button
                    className={classes.button}
                    size="small"
                    variant="contained"
                    onClick={this.fetchUpdateTaskName}
                  >

                    Save
                  </Button>
                  <Button
                    className={classes.button}
                    size="small"
                    variant="contained"
                    onClick={this.closeInput}
                  >

                    Cancel
                  </Button>
                  <div style={{ margin: 5 }} />
                </div>
              )
            }
          </div>
          <div>
            <IconButton
              onClick={this.props.clickClose}
            >
              <CloseIcon className={classes.closeIcon} />
            </IconButton>
          </div>
        </div>
      </DialogTitle>
    );
  }
}

Header.propTypes = {
  taskId: PropTypes.string.isRequired,
  open: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired,
  taskName: PropTypes.string.isRequired,
  taskCompleted: PropTypes.bool.isRequired,
  bName: PropTypes.string.isRequired,
  fetchUpdateTask: PropTypes.func.isRequired,
  taskOpenName: PropTypes.func.isRequired,
  clickClose: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    bName: boardName(ownProps, state),
    open: state.task.openName,
    taskId: state.board.selectedTask,
    projectRole: state.board.selectedRole,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateTask,
    taskOpenName,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Header));
