import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import moment from 'moment';
import PrimaryButton from '../../../../../../lib/Button/PrimaryButton';
import Form from '../../../../../../lib/Form';
import { notifySuccess, notifyError } from '../../../../../../../actions/notification';
import { taskOpenReportHours } from '../../../../../../../actions/task';
import { fetchAddHours } from '../../../../../../../actions/api/hours';

import PopOver from '../../../MainContent/SidebarPopOvers/PopOver';

const styles = theme => ({
  titleText: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(15),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  input: {
    justifySelf: 'center',
    alignSelf: 'center',
    width: '100%',
  },
  grid: {
    display: 'flex',
    flexDirection: 'row',
  },
  text: {
    fontSize: '16px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
    alignSelf: 'center',
  },
  button: {
    boxShadow: 'unset',
    width: '130px',
    color: 'white',
    fontSize: theme.typography.pxToRem(12),
    alignSelf: 'center',
    float: 'center',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  cssUnderline: {
    '&:after': {
      borderBottomColor: '#ea5941',
    },
  },
});

class AddHour extends Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
      workDay: '',
      reportedHours: '',
    };
    this.ref = React.createRef();
    this.addHour = this.addHour.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.hoursChange = this.hoursChange.bind(this);
    this.workDayChange = this.workDayChange.bind(this);
    this.denyChange = this.denyChange.bind(this);
  }

  componentDidMount() {
    this.setState({ anchorEl: this.ref.current, workDay: moment().format('YYYY-MM-DD') });
  }

  addHour() {
    this.props.taskOpenReportHours();
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    if (event) event.stopPropagation();
    const {
      workDay,
      reportedHours,
    } = this.state;
    const { taskId } = this.props;
    if (reportedHours) {
      this.props.fetchAddHours(taskId, { hours: reportedHours, date: workDay })
        .then((response) => {
          if (response.ok) {
            this.props.taskOpenReportHours();
            this.props.notifySuccess('Hours updated');
          }
        });
    } else {
      this.props.taskOpenReportHours();
      this.props.notifyError('Fields can not be empty');
    }
  }

  hoursChange(value) {
    this.setState({ reportedHours: value });
  }

  denyChange(event) {
    if (event.key === 'Enter') {
      this.handleSubmit();
    } else if (event.key.match(/^[0-9.]+$/) === null) {
      event.preventDefault();
    } else if ((event.target.value.match(/\./) && event.target.value.match(/[.]$/) === null)) {
      event.preventDefault();
    }
  }

  workDayChange(value) {
    this.setState({ workDay: value });
  }

  render() {
    const { classes, open, isFetching } = this.props;
    const { anchorEl, reportedHours, workDay } = this.state;
    return (
      <Fragment>
        <div ref={this.ref}>
          <PrimaryButton className={classes.button} type="button" onClick={this.addHour}>

            Report Hours
          </PrimaryButton>
        </div>
        {
          anchorEl !== null
          && (
            <PopOver onClose={this.addHour} anchorEl={anchorEl} open={open}>
              <div style={{ margin: '10px 20px 40px 20px', paddingBottom: '20px' }}>
                <Form onSubmit={this.handleSubmit}>
                  <div className={classes.grid}>
                    <div className={classes.input}>
                      <Typography className={classes.text}>

                        Hours
                      </Typography>
                      <TextField
                        value={reportedHours || ''}
                        type="number"
                        min="0"
                        InputProps={{
                          inputProps: {
                            min: 0,
                            step: '1',
                            classes: {
                              underline: classes.cssUnderline,
                            },
                          },
                        }}
                        onKeyPress={e => this.denyChange(e)}
                        onChange={e => this.hoursChange(e.target.value)}
                        style={{ width: 35 }}
                      />
                    </div>
                    <div className={classes.input} style={{ marginLeft: '30px' }}>
                      <Typography className={classes.text}>

                        Work day
                      </Typography>
                      <TextField
                        value={workDay}
                        type="date"
                        onChange={e => this.workDayChange(e.target.value)}
                        style={{ width: '150px' }}
                        InputProps={{
                          classes: {
                            underline: classes.cssUnderline,
                          },
                        }}
                      />
                    </div>
                  </div>
                  <div style={{ float: 'right', marginTop: '10px' }}>
                    <PrimaryButton
                      type="submit"
                      loading={isFetching}
                      onClick={this.handleSubmit}
                      className={classes.button}
                    >

                      Add Hours
                    </PrimaryButton>
                  </div>
                </Form>
              </div>
            </PopOver>
          )
        }
      </Fragment>
    );
  }
}

AddHour.propTypes = {
  classes: PropTypes.object.isRequired,
  taskOpenReportHours: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  taskId: PropTypes.string.isRequired,
  isFetching: PropTypes.bool.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  fetchAddHours: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  return {
    open: state.subTask.openReportHours,
    taskId: state.board.selectedTask,
    isFetching: state.app.isFetching,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenReportHours,
    notifySuccess,
    notifyError,
    fetchAddHours,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(AddHour));
