import { createSelector } from 'redux-orm';

import orm from '../reducers/orm';

const getRef = obj => (obj != null ? obj.ref : null);

export const onlyTaskComments = createSelector(
  orm,
  (_, state) => state.orm,
  (prop, state) => state.board.selectedTask,
  (session, taskId) => {
    const task = session.Task.get(o => o.id === taskId).ref;
    if (task === null) return null;
    const taskComments = session.Comment.filter((o) => {
      return o.task_id === taskId;
    }).toRefArray();
    const commentgrouped = taskComments.map((user) => {
      return Object.assign({}, user, {
        user: getRef(session.Member
          .get(o => o.id === user.user_id)),
      });
    });
    const sortedComments = commentgrouped.sort((a, b) => new Date(a.created_at)
      - new Date(b.created_at)).reverse();
    return sortedComments;
  },
);

export const taskComments = null;
