import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

import PriorityIcon from '../../../../../../lib/Logo/Priority';
import Priority from '../../../../../../lib/Priority';

import { taskOpenPriority } from '../../../../../../../actions/task';

const styles = {
  container: {
    display: 'flex',
    alignItems: 'center',
    outline: 'none',
  },
  maintext: {
    fontSize: '14px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
  secondarytext: {
    fontSize: '12px',
    fontWeight: 'normal',
    color: '#2a3d41',
    paddingRight: '10px',
  },
};

class ExpectedHours extends Component {
  constructor(props) {
    super(props);
    this.clickBtn = this.clickBtn.bind(this);
  }

  clickBtn() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenPriority();
    }
  }

  render() {
    const { priority, classes } = this.props;
    return (
      <div
        role="button"
        tabIndex={0}
        onKeyPress={this.clickBtn}
        onClick={this.clickBtn}
        className={classes.container}
        style={
         this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}}
      >
        <PriorityIcon size="28px" style={{ paddingRight: 2 }} />
        <Typography className={classes.maintext}>
          <b>Priority:</b>

          &nbsp;
        </Typography>
        {(priority > 0)
        && <Priority priority={priority} big />
        }
      </div>
    );
  }
}

ExpectedHours.propTypes = {
  classes: PropTypes.object.isRequired,
  priority: PropTypes.number.isRequired,
  taskOpenPriority: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenPriority,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ExpectedHours));
