import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';
import EditIcon from '@material-ui/icons/Edit';
import RejectIcon from '@material-ui/icons/Clear';
import FormControl from '@material-ui/core/FormControl';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import PopOver from '@material-ui/core/Popover';

import TwitterPicker from '../../../lib/TwitterPicker';

import Dialog from '../../../lib/Dialog';
import Form from '../../../lib/Form';
import TextField from '../../../lib/TextField';
import PrimaryButton from '../../../lib/Button/PrimaryButton';
import Label from '../../../lib/TaskComponents/Label';

import { toggleShowBoardLabels, toggleEditLabel, toggleCreateLabel } from '../../../../actions/modals/labels';
import { notifySuccess, notifyError } from '../../../../actions/notification';
import {
  fetchBoardLabels,
  fetchCreateLabel,
  fetchUpdateLabel,
  fetchDeleteLabel,
} from '../../../../actions/api/labels';

import { allLabels } from '../../../../selectors/labels';

import CircularProgress from '../../../lib/CircularProgress';

const styles = {
  container: {
    display: 'flex',
    margin: '3px 0px',
    listStyleType: 'none',
  },
  title: {
    fontSize: 18,
    color: '#2a3d41',
    marginRight: 30,
  },
  name: {
    fontSize: 20,
    marginRight: 20,
    fontWeight: 'bold',
    textShadow: '-1px -1px 0 #fffd, 1px -1px 0 #fffd, -1px 1px 0 #fffd, 1px 1px 0 #fffd',
  },
  reject: {
    color: '#e53935',
    cursor: 'pointer',
    '&:hover': {
      color: '#ff6f60',
    },
    marginLeft: 1,
  },
  role: {
    fontSize: 18,
    color: '#2a3d41',
    marginRight: 30,
  },
  item: {
    margin: '1px 0px',
  },
  icon: {
    color: '#2a3d41',
    marginLeft: 100,
    marginRight: 3,
    '&:hover': {
      color: '#ca3921',
    },
  },
  picker: {
    alignSelf: 'center',
  },
  blockLabels: {
    display: 'flex',
    flexDirection: 'column',
  },
  blockEdit: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-end',
    justifyContent: 'center',
    width: 200,
    marginLeft: 30,
    marginTop: 0,
  },
  labelsList: {
    width: 300,
    height: 300,
    marginBottom: 10,
    paddingRight: 10,
    overflow: 'auto',
  },
  titlePicker: {
    color: '#ea5941',
    marginBottom: 10,
    fontSize: 13,
  },
  editButton: {
    marginTop: 10,
  },
  options: {
    float: 'right',
  },
  listItem: {
    padding: 0,
  },
  popover: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    margin: 10,
  },
  popoverButtons: {
    marginTop: 10,
    display: 'flex',
    justifyContent: 'space-between',
  },
  nameText: {
    fontSize: '12px',
    fontWeight: 'bold',
    color: '#2a3d41',
  },
};

class EditLabelModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      name: '',
      color: '',
      deleting: '',
      loading: true,
      anchorEl: null,
    };
    this.handleClose = this.handleClose.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleCreate = this.handleCreate.bind(this);
    this.nameChange = this.nameChange.bind(this);
    this.colorChange = this.colorChange.bind(this);
    this.handleDeleteOpen = this.handleDeleteOpen.bind(this);
    this.handleDeleteClose = this.handleDeleteClose.bind(this);
    this.handleToggleCreate = this.handleToggleCreate.bind(this);
  }

  componentWillMount() {
    this.props.fetchBoardLabels(this.props.boardId).then(() => {
      this.setState({ loading: false });
    });
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    this.props.fetchUpdateLabel(this.props.labelId, {
      name: this.state.name,
      color: this.state.color,
    }).then((response) => {
      if (response.ok) {
        this.props.toggleEditLabel(this.state.id);
        this.props.notifySuccess('Label updated');
      }
    });
  }

  handleCreate(event) {
    if (event) event.preventDefault();
    const body = {
      board_id: this.props.boardId,
      name: this.state.name,
      color: this.state.color,
    };
    this.props.fetchCreateLabel(body).then((response) => {
      if (response.ok) {
        this.props.toggleCreateLabel();
        this.props.notifySuccess('Label created');
      }
    });
  }

  handleToggleCreate(event) {
    if (event) event.preventDefault();
    this.setState({ name: '', color: '' });
    this.props.toggleCreateLabel();
  }

  colorChange(newColor) {
    this.setState({ color: newColor.hex });
  }

  nameChange(value) {
    this.setState({ name: value });
  }

  handleEdit(label) {
    this.setState({ name: label.name, color: label.color, id: label.id });
    this.props.toggleEditLabel(label.id);
  }

  handleDeleteOpen(event, id) {
    if (event) event.preventDefault();
    this.setState({ anchorEl: event.currentTarget, deleting: id });
  }

  handleDeleteClose() {
    this.setState({ anchorEl: null, deleting: '' });
  }

  handleDelete(label) {
    this.props.fetchDeleteLabel(label.id).then((response) => {
      if (response.ok) {
        this.handleDeleteClose();
        if (this.props.labelEditing) {
          this.props.toggleEditLabel();
        }
        this.props.notifySuccess('Label deleted');
      }
    });
  }

  handleClose() {
    this.props.toggleShowBoardLabels();
  }

  render() {
    if (this.state.loading) {
      return <CircularProgress />;
    }
    const {
      labelEditing, labels, classes, labelCreating,
    } = this.props;
    const {
      name,
      color,
      anchorEl,
      deleting,
    } = this.state;
    const title = 'Manage Labels';
    return (
      <Dialog
        open
        title={title}
        onClose={this.handleClose}
        clickClose={this.handleClose}
      >
        <Form>
          <FormControl style={{ minWidth: 300 }}>
            <div className={classes.container}>
              <div className={classes.blockLabels}>
                <div className={classes.labelsList}>
                  { labels.map((l) => {
                    return (
                      <ListItem
                        key={l.id}
                        className={classes.listItem}
                      >
                        <Label label={l} customWidth="200px" />
                        <ListItemSecondaryAction>
                          <EditIcon
                            className={classes.icon}
                            onClick={() => this.handleEdit(l)}
                          />
                          <RejectIcon
                            className={classes.reject}
                            onClick={e => this.handleDeleteOpen(e, l)}
                          />
                        </ListItemSecondaryAction>
                      </ListItem>
                    );
                  }) }
                </div>
                <div style={{ flexGrow: 1 }} />
                <PrimaryButton
                  type="button"
                  onClick={this.handleToggleCreate}
                  style={{ float: 'center' }}
                >

                  CREATE NEW LABEL
                </PrimaryButton>
              </div>
              {
                (labelEditing || labelCreating) && (
                <div className={classes.blockEdit}>
                  <TextField
                    onChange={this.nameChange}
                    type="name"
                    name={name}
                    value={name}
                    fullWidth
                    label="Name"
                    id="name"
                    autoFocus
                    onKeyDown={(e) => {
                      if (e.key === 'Enter') e.preventDefault();
                    }}
                  />
                  <div className={classes.picker}>
                    <Typography className={classes.titlePicker}>

                      Color
                    </Typography>
                    <TwitterPicker
                      width={204}
                      onChangeComplete={this.colorChange}
                      color={color}
                    />
                  </div>
                  <div className={classes.editButton}>
                    {labelEditing && (
                      <PrimaryButton
                        type="submit"
                        onClick={this.handleSubmit}
                        style={{ float: 'center' }}
                      >

                        Save
                      </PrimaryButton>
                    )}
                    {labelCreating && (
                      <PrimaryButton
                        type="button"
                        onClick={this.handleCreate}
                        style={{ float: 'center' }}
                      >

                        CREATE
                      </PrimaryButton>
                    )}
                  </div>
                </div>
                )
              }
            </div>
          </FormControl>
        </Form>
        {
          anchorEl !== null
          && (
            <PopOver
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={this.handleDeleteClose}
              anchorOrigin={{ horizontal: 'left', vertical: 'top' }}
              transformOrigin={{ vertical: 'bottom', horizontal: 'left' }}
            >
              <div className={classes.popover}>
                <Typography className={classes.nameText}>

                  Are you sure?
                </Typography>
                <div className={classes.popoverButtons}>
                  <PrimaryButton
                    size="small"
                    onClick={() => this.handleDelete(deleting)}
                  >

                    DELETE
                  </PrimaryButton>
                </div>
              </div>
            </PopOver>
          )
        }
      </Dialog>
    );
  }
}

EditLabelModal.propTypes = {
  notifySuccess: PropTypes.any.isRequired,
  toggleShowBoardLabels: PropTypes.func.isRequired,
  labels: PropTypes.array.isRequired,
  fetchBoardLabels: PropTypes.func.isRequired,
  boardId: PropTypes.string.isRequired,
  labelEditing: PropTypes.bool.isRequired,
  labelCreating: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired,
  toggleEditLabel: PropTypes.func.isRequired,
  toggleCreateLabel: PropTypes.func.isRequired,
  fetchCreateLabel: PropTypes.func.isRequired,
  fetchUpdateLabel: PropTypes.func.isRequired,
  fetchDeleteLabel: PropTypes.func.isRequired,
  labelId: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    isFetching: state.app.isFetching,
    labels: allLabels(ownProps, state),
    boardId: state.modal.labels.selectedBoardId,
    labelEditing: state.modal.labels.labelEditing,
    labelCreating: state.modal.labels.labelCreating,
    labelId: state.modal.labels.selectedLabelId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchDeleteLabel,
    fetchCreateLabel,
    fetchUpdateLabel,
    fetchBoardLabels,
    toggleEditLabel,
    toggleCreateLabel,
    toggleShowBoardLabels,
    notifySuccess,
    notifyError,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(EditLabelModal));
