import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import Checkbox from '@material-ui/core/Checkbox';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';

import LabelForm from '../TaskShow/MainContent/SidebarPopOvers/Labels/Form';
import Label from '../../../lib/TaskComponents/Label';
import PrimaryButton from '../../../lib/Button/PrimaryButton';
import Dialog from '../../../lib/Dialog';
import language from '../../../../constants/language';
import { toggleTaskLabelForm } from '../../../../actions/board';
import { fetchAddLabelTask, fetchRemoveLabelTask } from '../../../../actions/api/labels';

import { onlyLabels } from '../../../../selectors/labels';

const styles = theme => ({
  root: {
    width: '300px',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  item: {
    color: '#ea5941',
    '&$checked': {
      color: '#ea5941',
    },
  },
  checked: {},
  icon: {
    color: '#2a3d41',
    fontSize: 20,
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
});

class TaskLabelForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      newLabel: false,
    };
    this.handleClose = this.handleClose.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
    this.showForm = this.showForm.bind(this);
    this.hideForm = this.hideForm.bind(this);
  }

  handleClose() {
    this.props.toggleTaskLabelForm();
  }

  handleToggle(event, labelId, assigned) {
    if (event) event.stopPropagation();
    const body = { task_id: this.props.taskId };
    if (assigned) {
      this.props.fetchRemoveLabelTask(labelId, this.props.taskId);
    } else {
      this.props.fetchAddLabelTask(labelId, body);
    }
  }

  showForm() {
    this.setState({ newLabel: true });
  }

  hideForm() {
    this.setState({ newLabel: false });
  }

  render() {
    const {
      showLabelForm, classes, labels, selectedLanguage,
    } = this.props;
    const title = language[selectedLanguage].tasks.label;
    return (
      <Dialog
        open={showLabelForm}
        title={title}
        onClose={this.handleClose}
        clickClose={this.handleClose}
        onExit={this.handleClose}
      >
        <div className={classes.root}>
          <List>
            {
            labels.map((label) => {
              return (
                <div key={label.id}>
                  <ListItem
                    button
                    onClick={event => this.handleToggle(event, label.id, label.assigned)}
                    className={classes.listItem}
                  >
                    <Label label={label} customCursor="pointer" customWidth="200px" />
                    <ListItemSecondaryAction>
                      <Checkbox
                        disableRipple
                        classes={{ root: classes.checkbox, checked: classes.checked }}
                        onChange={event => this.handleToggle(event, label.id, label.assigned)}
                        checked={label.assigned}
                      />
                    </ListItemSecondaryAction>
                  </ListItem>
                  <Divider />
                </div>
              );
            })
          }
            {
            this.state.newLabel
            && (
              <div className={classes.button}>
                <LabelForm onSubmit={this.hideForm} />
              </div>
            )
          }
          </List>
          {
          !this.state.newLabel
          && (
            <ListItem>
              <div>
                <PrimaryButton
                  onClick={this.showForm}
                >
                  {language[selectedLanguage].modals.taskModalEditLabelModalCreate}
                </PrimaryButton>
              </div>
            </ListItem>
          )
        }
        </div>
      </Dialog>
    );
  }
}

TaskLabelForm.propTypes = {
  classes: PropTypes.object.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  taskId: PropTypes.string.isRequired,
  showLabelForm: PropTypes.bool.isRequired,
  labels: PropTypes.array.isRequired,
  toggleTaskLabelForm: PropTypes.func.isRequired,
  fetchAddLabelTask: PropTypes.func.isRequired,
  fetchRemoveLabelTask: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    selectedLanguage: state.user.language,
    labels: onlyLabels(ownProps, state),
    isFetching: state.app.isFetching,
    showLabelForm: state.board.showLabelTaskForm,
    taskId: state.board.selectedTask,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleTaskLabelForm,
    fetchAddLabelTask,
    fetchRemoveLabelTask,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(TaskLabelForm));
