import React from 'react';
import PropTypes from 'prop-types';

import Highcharts from "highcharts/highcharts-gantt";
import HighchartsReact from 'highcharts-react-official';
// import draggable from "highcharts/modules/draggable-points";

import language from '../../constants/language';


// if (typeof Highcharts === "object") {
//   draggable(Highcharts);
// }


const GanttChart = (props) => {
  const tasks = props.boards.concat(props.tasks);
  const chartOptions = {
    chart: {
      spacingLeft: 1,
      backgroundColor: "transparent",
      style: { "fontFamily": "Roboto, Helvetica, Arial, sans-serif", "fontSize": 20 }
    },
    plotOptions: {
      series: {
        // dragDrop: {
        //       draggableX: false,
        //       dragPrecisionX: 360 // Snap to eight hours
        //   },
        dataLabels: {
          enabled: true,
          style: {
            cursor: 'default'
          }
        },
        allowPointSelect: false
      }
    },
    xAxis: {
      currentDateIndicator: true,
      dateTimeLabelFormats: {
        week: language[props.language].gantt.week
      }
    },
    tooltip: {
      xDateFormat: '%a %d %b, %H:%M',
      formatter: function () {
        return this.key + '<br />' + language[props.language].gantt.start +
          Highcharts.dateFormat('%a %d %b, %H:%M', this.x) + '<br />' + language[props.language].gantt.end +
          Highcharts.dateFormat('%a %d %b, %H:%M', this.x2)
      }
    },
    time: {
      timezone: 'America/Santiago'
    },
    series: [{
      name: 'Project Gantt',
      data: tasks,
      point: {
        events: {
          click: (e) => {
            const task = e.point.options;
          }
        }
      }
    }]
  }
  Highcharts.setOptions({
    global: {
      useUTC: false
    },
    lang: language[props.language].gantt.datetime
  });
  return (
    <div>
      <HighchartsReact
        highcharts={Highcharts}
        constructorType={"ganttChart"}
        options={chartOptions}
      />
    </div>
  );
};

GanttChart.propTypes = {
  tasks: PropTypes.array.isRequired
};

export default GanttChart;
