import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styled from 'styled-components';

import BoardForm from '../BoardForm';

/* eslint-disable react/prop-types */
const Container = styled.div`
  width: auto;
  height: auto;
`;

const DropContext = ({
  children, selectedBoardId, onDragEnd, showCreateBoardForm, onDragStart,
}) => {
  if (!selectedBoardId) {
    return (
      <DragDropContext onDragStart={onDragStart} onDragEnd={result => onDragEnd(result)}>
        <Droppable
          droppableId="all-boards"
          type="board"
        >
          {provided => (
            <Container
              {...provided.droppableProps}
              innerRef={provided.innerRef}
            >
              {children}
              {
                !showCreateBoardForm
                && provided.placeholder
              }
              {
                showCreateBoardForm
                && <BoardForm />
              }
            </Container>
          )}
        </Droppable>
      </DragDropContext>
    );
  }
  return (
    <Fragment>
      {children}
    </Fragment>
  );
};

DropContext.propTypes = {
  children: PropTypes.node.isRequired,
  selectedBoardId: PropTypes.string.isRequired,
  onDragEnd: PropTypes.func.isRequired,
  onDragStart: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedBoardId: state.user.selectedBoardId,
    showCreateBoardForm: state.board.showCreateBoardForm,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(DropContext);
