import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlined';
import ArrowLeftIcon from '@material-ui/icons/SubdirectoryArrowLeft';
import Divider from '@material-ui/core/Divider';
import PinIcon from '../../../app/Pin';

import { fetchDeleteTask, fetchArchiveTask } from '../../../../actions/api/tasks';
import { notifySuccess } from '../../../../actions/notification';

/* eslint-disable no-unused-vars */
const styles = {
  container: {
    flex: 1,
    display: 'flex',
    margin: '5px 10px',
  },
  header: {
    display: 'flex',
  },
  title: {
    width: '100%',
    fontSize: 16,
    fontWeight: 'bold',
    color: '#2a3d41',
    wordWrap: 'break-word',
    paddingLeft: '5',
  },
  iconDivider: {
    width: 1,
    height: 25,
    backgroundColor: '#f0f0f0',
    margin: '0px 5px',
  },
  icon: {
    color: '#2a3d41',
    fontSize: 25,
    marginLeft: 3,
    '&:hover': {
      color: '#ca3921',
    },
  },
};

class TaskComponent extends Component {
  constructor(props) {
    super(props);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleRevert = this.handleRevert.bind(this);
  }

  handleDelete(ev) {
    ev.stopPropagation();
    this.props.fetchDeleteTask(this.props.task.id).then((res) => {
      if (res.ok) {
        this.props.notifySuccess('Deleted Task Successfully');
      }
    });
  }

  handleRevert(ev) {
    ev.stopPropagation();
    this.props.fetchArchiveTask(this.props.task.id).then((res) => {
      if (res.ok) {
        this.props.notifySuccess('Task Restored Successfully');
      }
    });
  }

  render() {
    const { task, classes } = this.props;
    if (task === null) {
      return <div />;
    }
    return (
      <div className={classes.container}>
        <div className={classes.header}>
          <PinIcon />
          <Typography className={classes.title}>
            {task.name}
          </Typography>
        </div>
        <div style={{ flexGrow: 1 }} />
        <Tooltip placement="top" title="Restore">
          <ArrowLeftIcon onClick={this.handleRevert} className={classes.icon} />
        </Tooltip>
        <Divider className={classes.iconDivider} />
        <Tooltip placement="top" title="Delete">
          <DeleteOutlinedIcon onClick={this.handleDelete} className={classes.icon} />
        </Tooltip>
      </div>
    );
  }
}

TaskComponent.propTypes = {
  task: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired,
  fetchDeleteTask: PropTypes.func.isRequired,
  fetchArchiveTask: PropTypes.func.isRequired,
  notifySuccess: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchDeleteTask,
    fetchArchiveTask,
    notifySuccess,
  }, dispatch);
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(withStyles(styles)(TaskComponent)),
);
