import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import shortid from 'shortid';
import { withStyles } from '@material-ui/core/styles';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import RoadblockIcon from '../../../../../../lib/Logo/OrangeRoadblock';

import { fetchUpdateTask } from '../../../../../../../actions/api/tasks';
import { taskOpenRoadblock } from '../../../../../../../actions/task';

const styles = theme => ({
  title: {
    display: 'flex',
    marginBottom: 3,
  },
  titleText: {
    color: '#eb5d46',
    fontSize: theme.typography.pxToRem(15),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  text: {
    fontSize: theme.typography.pxToRem(12),
    color: '#eb5d46',
    padding: 10,
    paddingLeft: 28,
  },
  inputContainer: {
    display: 'flex',
    flexDirection: 'column',
    marginLeft: 28,
  },
  button: {
    boxShadow: 'unset',
    marginRight: 10,
    color: 'white',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  input: {
    fontSize: theme.typography.pxToRem(12),
    color: '#eb5d46',
  },
  notchedOutline: {
    borderColor: '#ea5941 !important',
  },
});

class Roadblock extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: props.roadblock || '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.openInput = this.openInput.bind(this);
    this.closeInput = this.closeInput.bind(this);
    this.updateRoadblock = this.updateRoadblock.bind(this);
    this.updateSolved = this.updateSolved.bind(this);
  }

  handleChange(e) {
    this.setState({ text: e.target.value });
  }

  openInput() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenRoadblock();
    }
  }

  closeInput() {
    this.props.taskOpenRoadblock();
    this.setState({ text: this.props.roadblock || '' });
  }

  updateRoadblock() {
    this.props.fetchUpdateTask(this.props.taskId, { roadblock: this.state.text })
      .then((r) => {
        if (r.ok) {
          this.closeInput();
        }
      });
  }

  updateSolved() {
    this.props.fetchUpdateTask(this.props.taskId, { roadblock: '' })
      .then((r) => {
        if (r.ok) {
          this.setState({ text: '' });
          this.closeInput();
        }
      });
  }

  render() {
    const { classes, open } = this.props;
    const { text } = this.state;
    const placeholder = 'Add a description for the roadblock...';
    const showText = text;
    return (
      <div className={classes.wrapper}>
        <div className={classes.textWrapper}>
          {
            (!open && showText !== '')
            && (
              <Fragment>
                <div className={classes.title}>
                  <RoadblockIcon size="23px" style={{ paddingRight: 5 }} />
                  <Typography className={classes.titleText}> Roadblock </Typography>
                </div>
                <div
                  role="button"
                  tabIndex={0}
                  onClick={this.openInput}
                  onKeyPress={this.openInput}
                >

                  {showText.split('\n').map((paragraph) => {
                    return (
                      <Typography className={classes.text} key={shortid.generate()}>
                        {paragraph}
                      </Typography>
                    );
                  })}
                </div>
              </Fragment>

            )
          }
          {
            open
            && (
              <div>
                <div className={classes.title}>
                  <RoadblockIcon size="23px" style={{ paddingRight: 5 }} />
                  <Typography className={classes.titleText}> Roadblock </Typography>
                </div>
                <div className={classes.inputContainer}>
                  <TextField
                    id="outlined-full-width"
                    placeholder={placeholder}
                    multiline
                    autoFocus
                    margin="none"
                    variant="outlined"
                    value={text}
                    onChange={this.handleChange}
                    InputProps={{
                      classes: {
                        input: classes.input,
                        notchedOutline: classes.notchedOutline,
                      },
                    }}
                  />
                  <div style={{ marginTop: 10, display: 'flex' }}>
                    <Button
                      className={classes.button}
                      size="small"
                      variant="contained"
                      onClick={this.updateRoadblock}
                    >

                      Save
                    </Button>
                    <Button
                      className={classes.button}
                      size="small"
                      variant="contained"
                      onClick={this.closeInput}
                    >

                      Cancel
                    </Button>
                    {this.props.roadblock
                      && (
                      <Button
                        className={classes.button}
                        size="small"
                        variant="contained"
                        onClick={this.updateSolved}
                      >

                        Remove
                      </Button>
                      )}
                  </div>
                </div>
              </div>
            )
          }
        </div>
      </div>
    );
  }
}

Roadblock.propTypes = {
  taskId: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired,
  roadblock: PropTypes.string,
  open: PropTypes.bool.isRequired,
  fetchUpdateTask: PropTypes.func.isRequired,
  taskOpenRoadblock: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

Roadblock.defaultProps = {
  roadblock: '',
};

const mapStateToProps = (state) => {
  return {
    taskId: state.board.selectedTask,
    open: state.task.openRoadblock,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateTask,
    taskOpenRoadblock,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Roadblock));
