import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Typography from '@material-ui/core/Typography';

import CircularProgress from '../../../../../../lib/CircularProgress';
import { taskOpenCommits } from '../../../../../../../actions/task';
import { fetchGithubCommits, fetchTaskCommit } from '../../../../../../../actions/api/tasks';

const styles = {
  root: {
    display: 'flex',
    flexDirection: 'column',
    margin: 10,
  },
  text: {
    margin: 10,
    fontWeight: 'bold',
    color: '#2a3d41',
  },
  item: {
    margin: '1px 0px',
  },
  name: {
    fontSize: 12,
    marginRight: 2,
    fontWeight: 'bold',
    textShadow: '-1px -1px 0 #fffd, 1px -1px 0 #fffd, -1px 1px 0 #fffd, 1px 1px 0 #fffd',
  },
  checkbox: {
    color: '#ea5941',
    marginLeft: 2,
    '&$checked': {
      color: '#ea5941',
    },
  },
  checked: {},
  icon: {
    fontSize: 12,
    color: '#2a3d41',
    padding: '0px 2px',
    '&:hover': {
      color: '#ca3921',
    },
  },
  reject: {
    color: '#e53935',
    cursor: 'pointer',
    '&:hover': {
      color: '#ff6f60',
    },
    marginLeft: 2,
  },
  listItem: {
    marginRight: 10,
    cursor: 'pointer',
    width: '450px',
  },
  names: {
    fontSize: 14,
    color: '#2a3d41',
  },
  title: {
    fontWeight: 'bold',
    margin: '10px 5px',
    fontSize: 16,
    color: '#2a3d41',
  },
};

class CommitModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      githubCommits: null,
    };
    this.handleToggle = this.handleToggle.bind(this);
  }

  componentWillMount() {
    const { taskId } = this.props;
    this.props.fetchGithubCommits(taskId)
      .then((response) => {
        if (response.ok) {
          this.setState({ githubCommits: response.body });
        }
      });
  }

  handleToggle(event, commitUrl, message) {
    const { taskId } = this.props;
    this.props.fetchTaskCommit(taskId, { url: commitUrl, message }).then((res) => {
      if (res.ok) {
        this.props.taskOpenCommits();
      }
    });
  }

  render() {
    const { classes, isFetching } = this.props;
    const { githubCommits } = this.state;
    if (githubCommits && githubCommits.length > 0) {
      return (
        <div className={classes.root}>
          <List>
            {
              githubCommits.map((commit) => {
                return (
                  <Fragment
                    key={commit}
                  >
                    <Typography className={classes.title}>{commit.repo.name}</Typography>
                    {commit.commits.map((com) => {
                      return (
                        <Fragment
                          key={com}
                        >
                          <ListItem
                            button
                            onClick={event => this.handleToggle(event, com.url, com.message)}
                            className={classes.listItem}
                          >
                            <div style={{ display: 'flex', flexDirection: 'column' }}>
                              <Typography className={classes.names}>
                                <b>
                                  {com.message}
                                </b>
                              </Typography>
                              <Typography className={classes.names} style={{ fontSize: 12 }}>

                                commited by
                                &nbsp;
                                <i>
                                  {com.author}
                                </i>
                              </Typography>
                            </div>
                          </ListItem>
                          <Divider />
                        </Fragment>
                      );
                    })}
                  </Fragment>
                );
              })
            }
          </List>
        </div>
      );
    }
    if (isFetching) return <CircularProgress style={{ position: 'relative', left: 'auto', margin: 10 }} />;
    return (
      <Typography className={classes.text}>

        No commits found.
      </Typography>
    );
  }
}

CommitModal.propTypes = {
  isFetching: PropTypes.bool.isRequired,
  taskId: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired,
  taskOpenCommits: PropTypes.func.isRequired,
  fetchTaskCommit: PropTypes.func.isRequired,
  fetchGithubCommits: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  return {
    isFetching: state.app.isFetching,
    taskId: state.board.selectedTask,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenCommits,
    fetchGithubCommits,
    fetchTaskCommit,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(CommitModal));
