import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import moment from 'moment';

import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';

import { taskOpenStartingDate } from '../../../../../../../actions/task';
import { fetchUpdateTask } from '../../../../../../../actions/api/tasks';
import { onlyTask } from '../../../../../../../selectors/tasks';

const styles = {
  container: {
    margin: 10,
  },
  input: {
    padding: 5,
    color: '#2a3d41',
  },
  underline: {
    '&:after': {
      borderBottomColor: '#ea5941',
    },
  },
};

class Form extends Component {
  constructor(props) {
    super(props);
    this.onSubmit = this.onSubmit.bind(this);
    this.onClick = this.onClick.bind(this);
    this.state = {
      starting_date: null,
    };
  }

  onSubmit(value) {
    console.log(value);
    let date = moment(value, 'YYYY-MM-DD');
    date = date.isValid() ? date.format('YYYY-MM-DD') : null;
    this.setState({ starting_date: date });
  }

  onClick() {
    const starting_date = moment(this.state.starting_date).startOf('day').format('YYYY-MM-DD');
    this.props.fetchUpdateTask(this.props.task.id, { starting_date });
    this.props.taskOpenStartingDate();
  }

  render() {
    const { classes, task } = this.props;
    let date = moment(task.starting_date, 'YYYY-MM-DD');
    if (!date.isValid()) {
      date = moment().format('YYYY-MM-DDTHH:mm:ss.SSSSZ');
    } else {
      date = date.format('YYYY-MM-DD');
    }
    return (
      <form className={classes.container} noValidate>
        <TextField
          id="date"
          label="StartingDate"
          type="date"
          defaultValue={date}
          className={classes.textField}
          InputLabelProps={{
            shrink: true,
          }}
          onChange={e => this.onSubmit(e.target.value)}
        />
        <Button onClick={this.onClick} variant="contained" id="new-deadline-button">
          Enviar
        </Button>
      </form>
    );
  }
}

Form.propTypes = {
  classes: PropTypes.object.isRequired,
  task: PropTypes.object.isRequired,
  fetchUpdateTask: PropTypes.func.isRequired,
  taskOpenStartingDate: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    task: onlyTask(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchUpdateTask,
      taskOpenStartingDate,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(Form));
