import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import CheckItem from './checkItem';
import Logo from '../../../../../../lib/Logo/Checklist';
import { fetchCreateChecklist } from '../../../../../../../actions/api/checklist';
import { taskOpenChecklist } from '../../../../../../../actions/task';

const styles = theme => ({
  wrapper: {
    gridTemplateColumns: 'repeat(8, 1fr)',
    gridGap: '0px',
  },
  titleText: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(15),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  title: {
    display: 'flex',
    marginBottom: 3,
  },
  grid: {
    marginLeft: 28,
    display: 'grid',
    gridTemplateColumns: 'repeat(1, 1fr)',
    gridGap: '3px',
    gridColumnGap: '10px',
  },
  new: {
    fontSize: theme.typography.pxToRem(12),
    color: '#ea5941',
    cursor: 'pointer',
    marginTop: 3,
    marginLeft: 28,
  },
  inputContainer: {
    display: 'flex',
    flexDirection: 'column',
    marginLeft: 28,
    marginTop: 5,
  },
  button: {
    boxShadow: 'unset',
    marginRight: 10,
    color: 'white',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  input: {
    fontSize: theme.typography.pxToRem(12),
    color: '#2a3d41',
  },
  notchedOutline: {
    borderColor: '#ea5941 !important',
  },
});

class Checklist extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.openInput = this.openInput.bind(this);
    this.closeInput = this.closeInput.bind(this);
    this.createChecklist = this.createChecklist.bind(this);
  }

  handleChange(e) {
    this.setState({ text: e.target.value });
  }

  openInput() {
    this.props.taskOpenChecklist();
  }

  closeInput() {
    this.props.taskOpenChecklist();
    this.setState({ text: '' });
  }

  createChecklist() {
    this.props.fetchCreateChecklist({ task_id: this.props.taskId, text: this.state.text })
      .then((r) => {
        if (r.ok) {
          this.setState({ text: '' });
        }
      });
  }

  render() {
    const {
      classes,
      checklist,
      open,
      projectRole,
    } = this.props;
    const { text } = this.state;
    return (
      <div className={classes.wrapper}>
        <div className={classes.title}>
          <Logo size="23px" style={{ paddingRight: 5 }} />
          <Typography className={classes.titleText}> Check List </Typography>
        </div>
        <div className={classes.grid}>
          {
            checklist.map((c) => {
              return (
                <CheckItem key={c.id} item={c} projectRole={projectRole} />
              );
            })
          }
        </div>
        {
          (open && projectRole !== 'viewer')
          && (
            <div className={classes.inputContainer}>
              <TextField
                onKeyPress={(e) => {
                  if (e.key === 'Enter') { this.createChecklist(); }
                }}
                error
                id="outlined-error"
                margin="normal"
                variant="outlined"
                value={text}
                onChange={this.handleChange}
                InputProps={{
                  classes: {
                    input: classes.text,
                    notchedOutline: classes.notchedOutline,
                  },
                }}
              />
              <div style={{ display: 'flex' }}>
                <Button
                  className={classes.button}
                  size="small"
                  variant="contained"
                  onClick={this.createChecklist}
                  autoFocus
                >

                  New item
                </Button>
                <Button
                  className={classes.button}
                  size="small"
                  variant="contained"
                  onClick={this.closeInput}
                >

                  Cancel
                </Button>
              </div>
            </div>
          )
        }
        {
          (!open && projectRole !== 'viewer')
          && (
            <Typography onClick={this.openInput} className={classes.new}>

              Add a new item...
            </Typography>
          )
        }
      </div>
    );
  }
}

Checklist.propTypes = {
  checklist: PropTypes.array.isRequired,
  classes: PropTypes.object.isRequired,
  taskOpenChecklist: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  fetchCreateChecklist: PropTypes.func.isRequired,
  taskId: PropTypes.string.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    taskId: state.board.selectedTask,
    open: state.task.openChecklist,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchCreateChecklist,
    taskOpenChecklist,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Checklist));
