import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Logo from '../../../../../../lib/Logo/Roadblock';
import Item from '../SidebarItem';

import { taskOpenRoadblock } from '../../../../../../../actions/task';

const styles = ({
  title: {
    alignSelf: 'center',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
});

class Roadblock extends Component {
  constructor(props) {
    super(props);
    this.onClickRoadblock = this.onClickRoadblock.bind(this);
  }

  onClickRoadblock() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenRoadblock();
    }
  }

  render() {
    const { classes } = this.props;
    return (
      <Fragment>
        <div style={{ marginTop: '10px' }}>
          <Item
            onClick={this.onClickRoadblock}
          >
            <Logo size="23px" />
            <Typography
              className={classes.title}
              style={
               this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}}
            >

              Roadblock
            </Typography>
          </Item>
        </div>
      </Fragment>
    );
  }
}

Roadblock.propTypes = {
  // open: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired,
  taskOpenRoadblock: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    projectRole: state.board.selectedRole,
    // open: state.task.openRoadblock,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenRoadblock,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Roadblock));
