import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import { GoGitCommit } from 'react-icons/go';
import Item from '../SidebarItem';
import CommitContainer from './Container';
import { taskOpenCommits } from '../../../../../../../actions/task';
import PopOver from '../PopOver';

const styles = ({
  title: {
    cursor: 'pointer',
    alignSelf: 'center',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
  icon: {
    color: '#CBCBCB',
    marginLeft: 3,
  },
});

class CommitsPopover extends Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
    };
    this.ref = React.createRef();
    this.popOverActions = {};
    this.onClickCommits = this.onClickCommits.bind(this);
    this.bindPopOverActions = this.bindPopOverActions.bind(this);
  }

  componentDidMount() {
    this.setState({ anchorEl: this.ref.current });
  }

  onClickCommits() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenCommits();
    }
  }

  bindPopOverActions(actions) {
    this.popOverActions = actions;
  }

  render() {
    const { open, classes } = this.props;
    const { anchorEl } = this.state;
    return (
      <Fragment>
        <div ref={this.ref} style={{ marginTop: '10px' }}>
          <Item
            onClick={this.onClickCommits}
          >
            <GoGitCommit className={classes.icon} />
            <Typography className={classes.title}>Commits</Typography>
          </Item>
        </div>
        {
          anchorEl !== null
          && (
            <PopOver
              action={this.bindPopOverActions}
              onClose={this.onClickCommits}
              anchorEl={anchorEl}
              open={open}
              style={{ maxHeight: 'calc(100vh - 350px)' }}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'right',
              }}
              transformOrigin={{
                vertical: 'bottom',
                horizontal: 'right',
              }}
            >
              <CommitContainer popOverActions={this.popOverActions} />
            </PopOver>
          )
        }
      </Fragment>
    );
  }
}

CommitsPopover.propTypes = {
  classes: PropTypes.object.isRequired,
  open: PropTypes.bool.isRequired,
  taskOpenCommits: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    open: state.task.openCommits,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenCommits,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(CommitsPopover));
