import {
  FETCH_GET_ATTACHMENT,
  FETCH_DOWNLOAD_ATTACHMENT,
  FETCH_ADD_ATTACHMENT,
  FETCH_DELETE_ATTACHMENT,
} from '../../constants/api/attachments';

export const getTaskAttachment = id => ({
  type: FETCH_GET_ATTACHMENT,
  fetch: true,
  url: `/tasks/${id}/attachments`,
  params: {
    method: 'GET',
  },
});

export const downloadAttachment = id => ({
  type: FETCH_DOWNLOAD_ATTACHMENT,
  fetch: true,
  url: `/attachment/${id}/`,
  params: {
    method: 'GET',
  },
});

export const deleteAttachment = id => ({
  type: FETCH_DELETE_ATTACHMENT,
  fetch: true,
  url: `/attachment/${id}/`,
  params: {
    method: 'DELETE',
  },
});

export const addTaskAttachment = (id, body) => ({
  type: FETCH_ADD_ATTACHMENT,
  fetch: true,
  url: `/tasks/${id}/attachments`,
  params: {
    method: 'POST',
    body,
  },
});
