import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import moment from 'moment';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Typography from '@material-ui/core/Typography';
import Input from '@material-ui/core/Input';
import RejectIcon from '@material-ui/icons/Clear';
import { truncateString } from '../../../../utils';
import Dialog from '../../../lib/Dialog';
import Form from '../../../lib/Form';
import PrimaryButton from '../../../lib/Button/PrimaryButton';

import { toggleEditInvitationForm } from '../../../../actions/modals/invitations';

import { notifySuccess } from '../../../../actions/notification';
import { fetchUpdateInvitation, fetchDeleteInvitation, fetchGetInvitationsSent } from '../../../../actions/api/invitations';

import { allInvitationsSent } from '../../../../selectors/invitations';

import CircularProgress from '../../../lib/CircularProgress';

/* eslint-disable no-param-reassign */
const styles = {
  container: {
    display: 'flex',
    alignItems: 'flex-end',
    margin: '3px 0px',
  },
  title: {
    fontSize: 18,
    color: '#2a3d41',
    marginRight: 30,
  },
  reject: {
    color: '#e53935',
    cursor: 'pointer',
    '&:hover': {
      color: '#ff6f60',
    },
    marginLeft: 10,
  },
  text: {
    fontSize: 18,
    color: '#2a3d41',
    marginLeft: 20,
  },
  date: {
    fontSize: 18,
    marginLeft: 20,
  },
};

class InvitationEditing extends Component {
  constructor(props) {
    super(props);
    this.state = {
      roles: [],
      loading: true,
    };
    this.handleClose = this.handleClose.bind(this);
    this.roleChange = this.roleChange.bind(this);
  }

  componentWillMount() {
    this.props.fetchGetInvitationsSent(this.props.projectId).then(() => {
      const invitationRoles = [];
      this.props.invitationsSent.forEach((invitation) => {
        invitationRoles.push({ id: invitation.id, role: invitation.role });
      });
      this.setState({ roles: invitationRoles, loading: false });
    });
  }

  roleChange = (event) => {
    const { name, value } = event.target;

    this.setState((prevState) => {
      const newRoles = JSON.parse(JSON.stringify(prevState.roles));
      newRoles.forEach((r) => {
        if (r.id === name) {
          if (r.role === value) {
            return;
          }
          r.role = value;
          this.props.fetchUpdateInvitation(r.id, {
            role: r.role,
          }).then((response) => {
            if (response.ok) {
              this.props.notifySuccess('Updated succesfully');
            }
          });
        }
      });
      return { roles: newRoles };
    });
  };

  handleClose() {
    this.props.toggleEditInvitationForm();
  }

  deleteInvitation(id) {
    this.props.fetchDeleteInvitation(id)
      .then((response) => {
        if (!response.body.accepted) {
          this.props.notifySuccess('You deleted the invitation succesfully');
        }
      });
  }

  render() {
    if (this.state.loading) {
      return <CircularProgress />;
    }
    const { isFetching, classes } = this.props;
    const title = 'Manage invitations';
    const empty = !this.props.invitationsSent.length;
    const footer = (
      <div>
        <PrimaryButton
          onClick={this.handleClose}
          loading={isFetching}
        >

          Close
        </PrimaryButton>
      </div>
    );
    return (
      <Dialog
        open
        title={title}
        footer={footer}
        onClose={this.handleClose}
        clickClose={this.handleClose}
        onExited={this.handleClose}
      >
        <Form>
          <FormControl style={{ minWidth: 200 }}>
            {
              empty && (
                <Typography> The are no pending invitations </Typography>
              )
            }
            {this.props.invitationsSent.map((invitation) => {
              return (
                <div key={invitation.id} className={classes.container}>
                  <Typography className={classes.title}>
                    {invitation.receiver_email}
                  </Typography>
                  <div style={{ flexGrow: 1 }} />
                  <div className={classes.roles}>
                    <Select
                      name={invitation.id}
                      value={this.state.roles.filter(m => m.id === invitation.id)[0].role}
                      onChange={this.roleChange}
                      input={<Input id="role" />}
                    >
                      <MenuItem value="viewer">Viewer</MenuItem>
                      <MenuItem value="developer">Developer</MenuItem>
                      <MenuItem value="manager">Manager</MenuItem>
                    </Select>
                  </div>
                  <Typography className={classes.text}>
                    {truncateString(invitation.sender_name, 100)}
                  </Typography>
                  <Typography className={classes.date}>
                    {moment(invitation.date_sent).format('D/M/YYYY')}
                  </Typography>
                  <RejectIcon
                    onClick={() => this.deleteInvitation(invitation.id)}
                    className={classes.reject}
                  />
                </div>
              );
            })}
          </FormControl>
        </Form>
      </Dialog>
    );
  }
}

InvitationEditing.propTypes = {
  isFetching: PropTypes.bool.isRequired,
  toggleEditInvitationForm: PropTypes.func.isRequired,
  notifySuccess: PropTypes.any.isRequired,
  projectId: PropTypes.string.isRequired,
  invitationsSent: PropTypes.array.isRequired,
  classes: PropTypes.object.isRequired,
  fetchUpdateInvitation: PropTypes.func.isRequired,
  fetchDeleteInvitation: PropTypes.func.isRequired,
  fetchGetInvitationsSent: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    isFetching: state.app.isFetching,
    projectId: state.modal.invitations.selectedProject,
    invitationsSent: allInvitationsSent(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchGetInvitationsSent,
    fetchUpdateInvitation,
    fetchDeleteInvitation,
    toggleEditInvitationForm,
    notifySuccess,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(InvitationEditing));
