import { createSelector } from 'redux-orm';

import orm from '../reducers/orm';

/* eslint-disable import/prefer-default-export */
export const onlyTaskAttachments = createSelector(
  orm,
  (_, state) => state.orm,
  (prop, state) => state.board.selectedTask,
  (session, taskId) => {
    const task = session.Task.get(o => o.id === taskId).ref;
    if (task === null) return null;
    return session.Attachment.filter((o) => {
      return o.task_id === taskId;
    }).toRefArray();
  },
);
