import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';
import Dialog from '../../../lib/Dialog';
import Form from '../../../lib/Form';
import PrimaryButton from '../../../lib/Button/PrimaryButton';
import CircularProgress from '../../../lib/CircularProgress';

import { notifySuccess } from '../../../../actions/notification';
import { fetchDeleteBoard } from '../../../../actions/api/boards';
import { fetchGetColumns } from '../../../../actions/api/columns';
import { fetchGetTasks } from '../../../../actions/api/tasks';

import { oneBoardFormModal } from '../../../../selectors/boards';
import { toggleDeleteBoardForm } from '../../../../actions/modals/boards';

class DeleteForm extends Component {
  constructor(props) {
    super(props);
    this.handleCloseBoard = this.handleCloseBoard.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleOnEnter = this.handleOnEnter.bind(this);
  }

  handleOnEnter() {
    this.props.fetchGetColumns(this.props.board.id)
      .then(() => this.props.fetchGetTasks(this.props.board.id))
      .then(() => {
        const { board } = this.props;
        const tasks = board.columns
          .map(c => c.tasks.length)
          .reduce((i, t) => t + i);
        if (tasks === 0) {
          this.handleSubmit();
        }
      });
  }

  handleCloseBoard() {
    this.props.toggleDeleteBoardForm();
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    const { board } = this.props;
    this.handleCloseBoard();
    this.props.fetchDeleteBoard(board.id)
      .then((response) => {
        if (response.ok) {
          this.props.notifySuccess('Board deleted');
        }
      });
  }

  render() {
    const { isFetching, board } = this.props;
    let title = `Delete Board ${board.name}`;
    const footer = (
      <div style={{ flex: 1, display: 'flex' }}>
        <div style={{ flex: 1 }} />
        <PrimaryButton
          type="submit"
          onClick={this.handleCloseBoard}
          style={{ marginRight: 10 }}
        >

          No
        </PrimaryButton>
        <PrimaryButton
          type="submit"
          onClick={this.handleSubmit}
          loading={isFetching}
        >

          Yes
        </PrimaryButton>
      </div>
    );
    if (isFetching) {
      title = `Deleting Board ${board.name}`;
      return (
        <Dialog
          open
          title={title}
          onClose={this.handleCloseBoard}
          hideClose
        >
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <CircularProgress style={{ position: 'relative', left: 'unset' }} />
          </div>
        </Dialog>
      );
    }
    return (
      <Dialog
        open
        title={title}
        footer={footer}
        onClose={this.handleCloseBoard}
        onEnter={this.handleOnEnter}
        hideClose
      >
        <Form
          onSubmit={this.handleSubmit}
        >
          <Typography>

            This board has tasks in it.
            <br />

            Are you sure you want to delete board &quot;
            {board.name}

            &quot;?
          </Typography>
        </Form>
      </Dialog>
    );
  }
}

DeleteForm.propTypes = {
  isFetching: PropTypes.bool,
  fetchDeleteBoard: PropTypes.func.isRequired,
  fetchGetColumns: PropTypes.func.isRequired,
  fetchGetTasks: PropTypes.func.isRequired,
  toggleDeleteBoardForm: PropTypes.func.isRequired,
  board: PropTypes.object.isRequired,
  notifySuccess: PropTypes.func.isRequired,
};

DeleteForm.defaultProps = {
  isFetching: false,
};

const mapStateToProps = (state, ownProps) => {
  return {
    isFetching: state.app.isFetching,
    board: oneBoardFormModal(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleDeleteBoardForm,
    fetchDeleteBoard,
    fetchGetColumns,
    fetchGetTasks,
    notifySuccess,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(DeleteForm);
