import { withStyles } from '@material-ui/core/styles';
import React from 'react';
import PropTypes from 'prop-types';

const styles = {
  container: {
    display: 'flex',
    margin: '6px 0px',
  },
  item: {
    display: 'flex',
    width: 6,
    height: 6,
    marginRight: 2,
  },
  itemBig: {
    display: 'flex',
    width: 8,
    height: 8,
    marginRight: 2,
  },
  orange: {
    flex: 1,
    backgroundColor: '#ea5941',
  },
  gray: {
    flex: 1,
    backgroundColor: '#d0d0d0',
  },
};

const size = 5;

const Priority = ({
  classes,
  big,
  priority,
  ...rest
}) => (
  <div {...rest} className={classes.container}>
    {[...Array(priority).keys()].map(i => (
      <div key={i} className={big ? classes.itemBig : classes.item}>
        <div className={classes.orange} />
      </div>
    ))}
    {[...Array(size - priority).keys()].map(i => (
      <div key={i} className={big ? classes.itemBig : classes.item}>
        <div className={classes.gray} />
      </div>
    ))}
  </div>
);

Priority.propTypes = {
  classes: PropTypes.object.isRequired,
  big: PropTypes.bool,
  priority: PropTypes.number.isRequired,
};

Priority.defaultProps = {
  big: false,
};

export default withStyles(styles)(Priority);
