import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import Typography from '@material-ui/core/Typography';
import Milestone from '../../../../../../lib/Logo/Milestone';
import Item from '../SidebarItem';
import { taskOpenMilestone } from '../../../../../../../actions/task';
//import { oneProject } from '../../../../../../../selectors/projects';
import { fetchMilestoneAssign, fetchMilestoneUnassign, fetchGetMilestones, fetchGetMilestonesTasks } from '../../../../../../../actions/api/milestones'; //cambiar

import PopOver from '../PopOver';

const styles = ({
  title: {
    alignSelf: 'center',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
  item: {
    color: '#ea5941',
    '&$checked': {
      color: '#ea5941',
    },
  },
  checked: {},
  icon: {
    color: '#2a3d41',
    fontSize: 20,
  },
});

class MilestonePopover extends Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
      milestones: [],
      milestone_tasks: [],
    };
    this.ref = React.createRef();
    this.onClickMilestone = this.onClickMilestone.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
  }

  async componentDidMount() {
    await this.props.fetchGetMilestones(this.props.project)
      .then(
        (response) => {
          this.setState({ anchorEl: this.ref.current, milestones: response.body.milestone_list });
        }
      );

    this.setState({ milestone_tasks: [] });
    this.state.milestones.map(mil => {
      this.props.fetchGetMilestonesTasks({ milestone_id: mil.milestone_id })
        .then(
          (response) => {
            response.body.tasks_list.map(task => {
              this.setState({ milestone_tasks: [...this.state.milestone_tasks, task] });
            });
          });
    });
  }



  onClickMilestone() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenMilestone();
    }
  }

  handleToggle(event, milestoneId) {
    if (event) event.stopPropagation();
    const { taskId } = this.props;
    var assigned = false
    this.state.milestone_tasks.map(mil => {
      if (mil.milestone_id === milestoneId && mil.id === taskId) {
        assigned = true
      }
    })
    if (assigned) {
      this.props.fetchMilestoneUnassign({ task_id: taskId, milestone_id: milestoneId });
    } else {
      this.props.fetchMilestoneAssign({ task_id: taskId, milestone_id: milestoneId });
    }
  }

  render() {
    var assigned = false;
    const { open, classes, taskId } = this.props;
    const { anchorEl, milestones, milestone_tasks } = this.state;
    return (
      <Fragment>
        <div ref={this.ref} style={{ marginTop: '10px' }}>
          <Item
            onClick={this.onClickMilestone}
          >
            <Milestone size="23px" />
            <Typography
              className={classes.title}
              style={
                this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}}
            >
              Milestone
            </Typography>
          </Item>
        </div>
        {
          anchorEl !== null
          && (
            <PopOver onClose={this.onClickMilestone} anchorEl={anchorEl} open={open}>
              <List>
                {milestones.map(mil => (
                  <ListItem
                    key={mil.milestone_id}
                    dense
                    button
                    onClick={event => this.handleToggle(event, mil.milestone_id)}
                  >
                    {mil.milestone_name
                      && (
                        <ListItemText primary={`${mil.milestone_name}`} />
                      )
                    }
                    {assigned = false}
                    {milestone_tasks.map(task => {
                      if (task.milestone_id === mil.milestoneId && task.id === taskId) {
                        assigned = true
                      }
                    })}
                    <ListItemSecondaryAction>
                      <Checkbox
                        classes={{ root: classes.item, checked: classes.checked }}
                        onChange={event => this.handleToggle(event, mil.milestone_id)}
                        checked={assigned}
                      />
                    </ListItemSecondaryAction>
                  </ListItem>
                ))}
              </List>
            </PopOver>
          )
        }
      </Fragment>
    );
  }
}

MilestonePopover.propTypes = {
  open: PropTypes.bool.isRequired,
  taskOpenMilestone: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  taskId: PropTypes.string.isRequired,
  fetchMilestoneAssign: PropTypes.func.isRequired,
  fetchMilestoneUnassign: PropTypes.func.isRequired,
  fetchGetMilestones: PropTypes.func.isRequired,
  fetchGetMilestonesTasks: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    open: state.task.openMilestone,
    taskId: state.board.selectedTask,
    projectRole: state.board.selectedRole,
    project: state.user.selectedProjectId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenMilestone,
    fetchMilestoneAssign,
    fetchMilestoneUnassign,
    fetchGetMilestones,
    fetchGetMilestonesTasks,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(MilestonePopover));
