import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

import Labels from './SidebarPopOvers/Labels';
import Deadline from './SidebarPopOvers/Deadline';
import StartingDate from './SidebarPopOvers/StartingDate';
import ExpectedHours from './SidebarPopOvers/ExpectedHours';
import Priority from './SidebarPopOvers/Priority';
import Members from './SidebarPopOvers/Members';
import Milestone from './SidebarPopOvers/Milestone';
import Roadblock from './SidebarPopOvers/Roadblock';
import Checklist from './SidebarPopOvers/Checklist';
import Commit from './SidebarPopOvers/Commit';
import { oneProjectUser } from '../../../../../selectors/projects';
import {
  taskOpenMembers,
  taskOpenMilestone,
  taskOpenRoadblock,
  taskOpenExpectedHours,
  taskOpenPriority,
} from '../../../../../actions/task';

const styles = theme => ({
  title: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(14),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  item: {
    outline: 'none',
  },
});

class RightSidebar extends Component {
  constructor(props) {
    super(props);
    this.onClickExpectedHours = this.onClickExpectedHours.bind(this);
    this.onClickPriority = this.onClickPriority.bind(this);
    this.onClickMembers = this.onClickMembers.bind(this);
    this.onClickMilestone = this.onClickMilestone.bind(this);
    this.onClickRoadblock = this.onClickRoadblock.bind(this);
  }

  onClickExpectedHours() {
    this.props.taskOpenExpectedHours();
  }

  onClickPriority() {
    this.props.taskOpenPriority();
  }

  onClickMembers() {
    this.props.taskOpenMembers();
  }

  onClickMilestone() {
    this.props.taskOpenMilestone();
  }

  onClickRoadblock() {
    this.props.taskOpenRoadblock();
  }

  render() {
    const { classes, project } = this.props;
    return (
      <Fragment>
        <Typography className={classes.title}>

          ADD TO TASK:
        </Typography>
        <div className={classes.item}>
          <Labels />
        </div>
        <div className={classes.item}>
          <StartingDate />
        </div>
        <div className={classes.item}>
          <Deadline />
        </div>
        <div className={classes.item}>
          <ExpectedHours />
        </div>
        <div className={classes.item}>
          <Priority />
        </div>
        <div className={classes.item}>
          <Members />
        </div>
        <div className={classes.item}>
          <Roadblock />
        </div>
        <div className={classes.item}>
          <Checklist />
        </div>
        <div className={classes.item}>
          <Milestone />
        </div>
        {project.has_github
        && (
        <div className={classes.item}>
          <Commit />
        </div>
        )}
      </Fragment>
    );
  }
}

RightSidebar.propTypes = {
  classes: PropTypes.object.isRequired,
  taskOpenMembers: PropTypes.func.isRequired,
  taskOpenMilestone: PropTypes.func.isRequired,
  taskOpenRoadblock: PropTypes.func.isRequired,
  taskOpenExpectedHours: PropTypes.func.isRequired,
  taskOpenPriority: PropTypes.func.isRequired,
  project: PropTypes.any.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    project: oneProjectUser(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenMembers,
    taskOpenMilestone,
    taskOpenRoadblock,
    taskOpenExpectedHours,
    taskOpenPriority,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(RightSidebar));
