import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import Typography from '@material-ui/core/Typography';
import { Button } from '@material-ui/core';
import CircularProgress from '../lib/CircularProgress';
import ColumnComponent from './Column';
import ColumnForm from './ColumnForm';
import Modals from './Modals';
import { fetchGetColumns, fetchUpdateColumnOrder } from '../../actions/api/columns';
import { fetchGetTasks, fetchUpdateTaskOrder, fetchUpdateTaskOrderAndColumn } from '../../actions/api/tasks';
import { modelUpdateColumnOrder, modelUpdateTaskOrder, modelUpdateTaskOrderWithColumn } from '../../actions/order';
import { fetchBoardLabels } from '../../actions/api/labels';
import { startDrag, toggleCreateColumnForm } from '../../actions/board';

import { getProjectRole } from '../../selectors/projects';
import { oneBoard } from '../../selectors/boards';
import language from '../../constants/language';

import {
  move, getObjectIdOrNull, checkBorder, getPositions, calcPosition,
} from '../../utils/order';

const styles = {
  container: {
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'row',

  },
  whiteSpace: {
    margin: '30px 0px',
    border: '1px solid white',
    height: 600,
  },
  Button: {
    borderRadius: 0,
    margin: '10px 5px',
    border: '1px dashed #555555',
    height: 600,
    minWidth: 300,
  },
  heading: {
    color: '#555555',
    fontSize: 20,
    fontWeight: 'bold',
  },
};

class BoardComponent extends Component {
  constructor(props) {
    super(props);
    this.onDragEnd = this.onDragEnd.bind(this);
    this.onDragStart = this.onDragStart.bind(this);
    this.handleAddColumn = this.handleAddColumn.bind(this);
  }

  componentWillMount() {
    const { selectedBoardId } = this.props;
    this.props.fetchGetColumns(selectedBoardId)
      .then(() => {
        this.props.fetchBoardLabels(selectedBoardId);
        this.props.fetchGetTasks(selectedBoardId);
      });
  }

  onDragStart() {
    this.props.startDrag();
  }

  onDragEnd(result) {
    const { destination, source, type } = result;
    const { columns } = this.props.board;

    if (!destination) return;
    if (
      destination.droppableId === source.droppableId && destination.index === source.index
    ) {
      return;
    }

    let previous; let current; let next; let prevPos; let nextPos;
    if (type === 'column') {
      [prevPos, nextPos] = getPositions(destination, source);
      previous = getObjectIdOrNull(columns[prevPos]);
      current = getObjectIdOrNull(columns[source.index]);
      next = getObjectIdOrNull(columns[nextPos]);
      [previous, next] = checkBorder(previous, current, next);
      this.props.fetchUpdateColumnOrder(current, { previous, next });
      this.props.modelUpdateColumnOrder({
        id: current,
        position: calcPosition(columns[prevPos], columns[nextPos]),
      });
    } else {
      const startColumn = columns.filter(column => column.id === source.droppableId)[0];
      const finishColumn = columns.filter(column => column.id === destination.droppableId)[0];
      if (startColumn.id === finishColumn.id) {
        const { tasks } = startColumn;
        [prevPos, nextPos] = getPositions(destination, source);
        previous = getObjectIdOrNull(tasks[prevPos]);
        current = getObjectIdOrNull(tasks[source.index]);
        next = getObjectIdOrNull(tasks[nextPos]);
        [previous, next] = checkBorder(previous, current, next);
        this.props.fetchUpdateTaskOrder(current, { previous, next });
        this.props.modelUpdateTaskOrder({
          id: current,
          position: calcPosition(tasks[prevPos], tasks[nextPos]),
        });
      } else {
        [current, prevPos, nextPos] = move(startColumn, finishColumn, source, destination);
        const { tasks } = finishColumn;
        previous = getObjectIdOrNull(tasks[prevPos]);
        next = getObjectIdOrNull(tasks[nextPos]);
        this.props.fetchUpdateTaskOrderAndColumn(
          current, { previous, next, column_id: finishColumn.id },
        );
        this.props.modelUpdateTaskOrderWithColumn({
          id: current,
          position: calcPosition(tasks[prevPos], tasks[nextPos]),
          column_id: finishColumn.id,
        });
      }
    }
  }

  handleAddColumn() {
    this.props.toggleCreateColumnForm();
  }

  render() {
    const {
      board, classes, projectRole, selectedLanguage,
    } = this.props;
    const createRoles = ['owner', 'manager'];

    if (!board) {
      return <CircularProgress style={{ margin: '20px auto', position: 'block' }} />;
    }
    return (
      <Fragment>
        <Modals />
        <DragDropContext
          onDragStart={this.onDragStart}
          onDragEnd={result => this.onDragEnd(result)}
        >
          <Droppable
            droppableId="all-columns"
            direction="horizontal"
            type="column"
          >
            {provided => (
              <div
                {...provided.droppableProps}
                ref={provided.innerRef}
                className={classes.container}
              >
                {board && board.columns.map((column, index) => {
                  this.loadingTask = true;
                  return (
                    <ColumnComponent
                      key={column.id}
                      column={column}
                      index={index}
                    />
                  );
                })}
                {
                  this.props.columnCreating
                  && (
                    <ColumnForm boardId={board.id} />
                  )
                }
                <div className={classes.whiteSpace} />
                {provided.placeholder}

                { !this.props.columnCreating
                  && createRoles.includes(projectRole)
                   && this.loadingTask && (
                   <Button className={classes.Button} onClick={this.handleAddColumn}>
                     <Typography className={classes.heading}>
                       {language[selectedLanguage].board.addColumn}
                     </Typography>
                   </Button>
                )
                }
              </div>
            )}

          </Droppable>
        </DragDropContext>
      </Fragment>
    );
  }
}

BoardComponent.propTypes = {
  board: PropTypes.object.isRequired,
  fetchUpdateColumnOrder: PropTypes.func.isRequired,
  fetchUpdateTaskOrder: PropTypes.func.isRequired,
  fetchUpdateTaskOrderAndColumn: PropTypes.func.isRequired,
  fetchBoardLabels: PropTypes.func.isRequired,
  fetchGetColumns: PropTypes.func.isRequired,
  fetchGetTasks: PropTypes.func.isRequired,
  columnCreating: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired,
  startDrag: PropTypes.func.isRequired,
  modelUpdateColumnOrder: PropTypes.func.isRequired,
  modelUpdateTaskOrder: PropTypes.func.isRequired,
  modelUpdateTaskOrderWithColumn: PropTypes.func.isRequired,
  selectedBoardId: PropTypes.string.isRequired,
  projectRole: PropTypes.string.isRequired,
  toggleCreateColumnForm: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    selectedBoardId: state.user.selectedBoardId,
    columnCreating: state.board.columnCreating,
    selectedLanguage: state.user.language,
    board: oneBoard(ownProps, state),
    projectRole: getProjectRole(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleCreateColumnForm,
    fetchGetColumns,
    fetchGetTasks,
    fetchBoardLabels,
    fetchUpdateColumnOrder,
    fetchUpdateTaskOrder,
    fetchUpdateTaskOrderAndColumn,
    startDrag,
    modelUpdateColumnOrder,
    modelUpdateTaskOrder,
    modelUpdateTaskOrderWithColumn,
  }, dispatch);
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(BoardComponent)));
