import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';
import ExpansionPanel from '../../../lib/ExpansionPanel';
import AppLogo from '../../../app/AppLogo/index';
import PinIcon from '../../../app/Pin';

import { allArchivedTasks } from '../../../../selectors/tasks';
import { selectArchivedBoard } from '../../../../actions/modals/archived';

import Task from './Task';

const styles = {
  summary: {
    alignItems: 'center',
    display: 'flex',
    flexDirection: 'row',
    flex: 1,
  },
  heading: {
    color: '#2a3d41',
    fontSize: 15,
    fontWeight: 'bold',
  },
  subHeader: {
    fontSize: 12,
    marginLeft: 10,
    color: '#a4a4a4',
    display: 'flex',
  },
  empty: {
    display: 'flex',
    fontSize: 12,
    color: '#a4a4a4',
    justifyContent: 'center',
    margin: 10,
  },
};

class Board extends Component {
  constructor(props) {
    super(props);
    this.handleChangeBoard = this.handleChangeBoard.bind(this);
  }

  handleChangeBoard() {
    const { board } = this.props;
    this.props.selectArchivedBoard(board.id);
  }

  render() {
    const {
      classes,
      tasks,
      board,
      selectedBoardId,
    } = this.props;
    return (
      <Fragment>
        <ExpansionPanel
          expanded={selectedBoardId === board.id}
          onChange={this.handleChangeBoard}
          summaryStyle={{ padding: 0 }}
          summary={(
            <Fragment>
              <div className={classes.summary}>
                <AppLogo style={{ paddingRight: 8 }} />
                <Typography className={classes.heading}>{board.name}</Typography>
                <Typography className={classes.subHeader}>
                  <PinIcon white size="18px" />
                  {tasks.length}
&nbsp;
                  {tasks.length === 1 ? 'Task' : 'Tasks'}
                </Typography>
                <div className={classes.grow} />
              </div>
            </Fragment>
          )}
        >
          <div style={{ flex: 1 }}>
            {tasks.length === 0 ? (
              <Typography className={classes.empty}>

                No tasks archived
              </Typography>
            ) : (
              tasks.map(t => (
                <Task key={t.id} task={t} />
              ))
            )}
          </div>
        </ExpansionPanel>
      </Fragment>
    );
  }
}

Board.propTypes = {
  tasks: PropTypes.array.isRequired,
  board: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired,
  selectedBoardId: PropTypes.string.isRequired,
  selectArchivedBoard: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    tasks: allArchivedTasks(ownProps, state),
    selectedBoardId: state.modal.archived.selectedBoardId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    selectArchivedBoard,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Board));
