import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import RejectIcon from '@material-ui/icons/Clear';
import Divider from '@material-ui/core/Divider';
import filesize from 'filesize';
import {
  downloadAttachment,
  deleteAttachment,
} from '../../../../../../../actions/api/attachments';

const styles = {
  grid: {
    display: 'grid',
    gridTemplateColumns: '9fr 1fr',
    gridGap: 0,
  },
  customIcon: {
    height: '24px',
    width: '24px',
    marginRight: '3px',
    display: 'block',
    'border-radius': '50%',
  },
  text: {
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
    alignSelf: 'center',

  },
  reject: {
    color: '#2e4144',
    height: '20px',
    cursor: 'pointer',
    justifySelf: 'right',
    '&:hover': {
      color: 'black',
    },
  },
  user: {
    display: 'flex',
    flex: 1,
  },

  name: {
    color: '#298fca',
    fontSize: '14px',
    fontFamily: 'Roboto,arial,sans-serif',
    '&:hover': {
      textDecoration: 'underline',
    },
  },
  size: {
    color: '#298fca',
    fontSize: '14px',
    fontFamily: 'Roboto,arial,sans-serif',
  },
  item: {
    marginTop: '10px',
    width: '500px',
    height: '20px',
    cursor: 'pointer',
  },
};

class AttachmentItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mouse: false,
      redirect: false,
    };
    this.handleMouseEnter = this.handleMouseEnter.bind(this);
    this.handleMouseLeave = this.handleMouseLeave.bind(this);
    this.download = this.download.bind(this);
    this.deleteTaskAttachment = this.deleteTaskAttachment.bind(this);
  }

  handleMouseEnter() {
    this.setState({ mouse: true });
  }

  handleMouseLeave() {
    this.setState({ mouse: false });
  }

  download(id) {
    this.props.downloadAttachment(id)
      .then((response) => {
        if (response.ok) {
          this.setState({ redirect: response.body.url });
        }
      });
  }

  deleteTaskAttachment(id) {
    this.props.deleteAttachment(id);
  }

  render() {
    const {
      classes,
      item,
      projectRole,
    } = this.props;
    const { mouse, redirect } = this.state;
    if (redirect) {
      window.location.assign(redirect);
    }
    return (
      <div
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
        key={item.id}
        style={{ marginBottom: 15 }}
      >
        <div className={classes.item}>
          <div className={classes.grid}>
            <div style={{ display: 'flex', flexDirection: 'columns' }}>
              <Typography onClick={() => this.download(item.id)} className={classes.name}>
                {item.name}

                &nbsp;
              </Typography>
              <Typography onClick={() => this.download(item.id)} className={classes.size}>

                (
                {filesize(item.size)}

                )
              </Typography>
            </div>
            {((projectRole !== 'viewer') && (mouse))
            && (
            <RejectIcon
              className={classes.reject}
              onClick={() => this.deleteTaskAttachment(item.id)}
            />
            )}
          </div>
          <Divider />
        </div>
      </div>
    );
  }
}

AttachmentItem.propTypes = {
  classes: PropTypes.object.isRequired,
  item: PropTypes.object.isRequired,
  projectRole: PropTypes.string.isRequired,
  downloadAttachment: PropTypes.func.isRequired,
  deleteAttachment: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  return {
    currentUserEmail: state.auth.uid,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    downloadAttachment,
    deleteAttachment,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(AttachmentItem));
