import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';

import { fetchUpdateTask } from '../../../../../../../actions/api/tasks';
import { onlyTask } from '../../../../../../../selectors/tasks';
import FormComponent from '../../../../../../lib/Form';

import { denyChange } from '../../../../../../../utils';

const styles = {
  container: {
    margin: 10,
  },
  input: {
    padding: 5,
    color: '#2a3d41',
  },
  underline: {
    '&:after': {
      borderBottomColor: '#ea5941',
    },
  },
};

class Form extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hours: this.props.task.estimated_hours,
    };
    this.onSubmit = this.onSubmit.bind(this);
    this.debounce = null;
  }

  onSubmit(value) {
    let hours = value.toString();
    this.setState({ hours });
    if (hours === '') hours = '0';
    clearTimeout(this.debounce);
    this.debounce = setTimeout(
      () => this.props.fetchUpdateTask(this.props.task.id, { estimated_hours: hours }),
      500,
    );
  }

  render() {
    const { classes } = this.props;
    const { hours } = this.state;
    return (
      <FormComponent onSubmit={this.onSubmit}>
        <div className={classes.container}>
          <TextField
            style={{ width: 80 }}
            value={hours}
            type="number"
            onKeyPress={e => denyChange(e)}
            onChange={e => this.onSubmit(e.target.value)}
            InputProps={{
              inputProps: {
                min: 0,
                step: '1',
                classes: {
                  input: classes.input,
                  underline: classes.cssUnderline,
                },
              },
            }}
          />
        </div>
      </FormComponent>
    );
  }
}

Form.propTypes = {
  classes: PropTypes.object.isRequired,
  task: PropTypes.object.isRequired,
  fetchUpdateTask: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    task: onlyTask(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateTask,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Form));
