import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';

import { fetchUpdateTask } from '../../../../../../../actions/api/tasks';
import { onlyTask } from '../../../../../../../selectors/tasks';
import { taskOpenPriority } from '../../../../../../../actions/task';
import FormComponent from '../../../../../../lib/Form';

// import { denyChange } from '../../../../../../../utils';

/* eslint-disable jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions */
const styles = {
  root: {
    margin: 10,
    display: 'flex',
    alignItems: 'center',
  },
  item: {
    display: 'flex',
    width: 20,
    height: 20,
    marginRight: 3,
  },
  orange: {
    flex: 1,
    backgroundColor: '#ea5941',
  },
  gray: {
    flex: 1,
    backgroundColor: '#d0d0d0',
  },
  icon: {
    color: '#ea5941',
    fontSize: 20,
    lineHeight: 0,
  },
  iconLabel: {
    height: 0,
  },
};

class Form extends Component {
  constructor(props) {
    super(props);
    this.state = {
      priority: 0,
    };
    this.onSubmit = this.onSubmit.bind(this);
    this.handleHover = this.handleHover.bind(this);
  }

  componentDidMount() {
    this.setState({ priority: this.props.task.priority });
  }

  onSubmit() {
    this.props.fetchUpdateTask(this.props.task.id, { priority: this.state.priority });
    this.props.taskOpenPriority();
  }

  handleHover(event, index) {
    this.setState({ priority: index + 1 });
  }

  render() {
    const { classes } = this.props;
    return (
      <FormComponent onSubmit={this.onSubmit}>
        <div
          className={classes.root}
          onMouseLeave={ev => this.handleHover(ev, this.props.task.priority - 1)}
          onClick={this.onSubmit}
        >
          <IconButton
            className={classes.item}
            classes={{ label: classes.iconLabel }}
            onMouseOver={ev => this.handleHover(ev, -1)}
            onFocus={ev => this.handleHover(ev, -1)}
          >
            <CloseIcon className={classes.icon} />
          </IconButton>
          {[...Array(5).keys()].map(i => (
            <div
              key={i}
              className={classes.item}
              onMouseOver={ev => this.handleHover(ev, i)}
              onFocus={ev => this.handleHover(ev, i)}
            >
              <div className={this.state.priority > i ? classes.orange : classes.gray} />
            </div>
          ))}
        </div>
      </FormComponent>
    );
  }
}

Form.propTypes = {
  classes: PropTypes.object.isRequired,
  task: PropTypes.object.isRequired,
  fetchUpdateTask: PropTypes.func.isRequired,
  taskOpenPriority: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    task: onlyTask(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateTask,
    taskOpenPriority,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Form));
