import {
  TOGGLE_ARCHIVE_MODAL_PROJECT,
  SELECT_ARCHIVED_BOARD,
  SET_FETCHING_ARCHIVED,
} from '../../constants/modals/archived';

export const toggleManageArchiveModal = id => ({
  type: TOGGLE_ARCHIVE_MODAL_PROJECT,
  payload: {
    body: {
      id,
    },
  },
});

export const selectArchivedBoard = id => ({
  type: SELECT_ARCHIVED_BOARD,
  payload: {
    body: {
      id,
    },
  },
});

export const setFetchingArchived = bool => ({
  type: SET_FETCHING_ARCHIVED,
  payload: {
    body: {
      bool,
    },
  },
});
