import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import HourItem from './hourItem';
import { fetchDeleteHour } from '../../../../../../../actions/api/hours';
import { getHours } from '../../../../../../../selectors/hours';

const styles = theme => ({
  titleText: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(15),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  grid: {
    display: 'grid',
    gridTemplateColumns: '2fr 2fr 2fr 1fr',
    gridGap: 0,
    margin: '5px',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    marginRight: '3px',
    display: 'block',
    'border-radius': '50%',
  },
  text: {
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
    alignSelf: 'center',

  },
  reject: {
    color: '#2e4144',
    cursor: 'pointer',
    paddingRight: '2%',
    alignItems: 'center',
    float: 'right',
    '&:hover': {
      color: 'black',
    },
  },
  edit: {
    color: '#8cdcfd',
    cursor: 'pointer',
    '&:hover': {
      color: '#BCE8FA',
    },
  },
  header: {
    display: 'flex',
    flex: 1,
  },
  titleElement: {
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#eb5d46',
  },
  user: {
    display: 'flex',
    flex: 1,
  },
  icons: {
    display: 'flex',
    flex: 1,
    justifySelf: 'center',
  },
});

const ShowHours = ({ hours, classes, projectRole }) => {
  return (
    <Fragment>
      { !hours.length
        && (
          <Typography className={classes.warning}>

            There are no reported hours
          </Typography>
        )
      }
      { hours.length > 0
          && (
          <Fragment>
            <div className={classes.grid}>
              <Typography className={classes.titleElement}>

                Members
              </Typography>
              <Typography className={classes.titleElement}>

                Hours
              </Typography>
              <Typography className={classes.titleElement}>

                Date
              </Typography>
            </div>
            {
              hours.map((h) => {
                return (
                  <HourItem key={h.id} item={h} projectRole={projectRole} />
                );
              })
            }
          </Fragment>
          )
      }
    </Fragment>
  );
};

ShowHours.propTypes = {
  classes: PropTypes.object.isRequired,
  hours: PropTypes.array.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    hours: getHours(ownProps, state),
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchDeleteHour,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ShowHours));
