import {
  TOGGLE_SHOW_MODAL_LOGS,
  LAST_HOVER_PROJECT,
} from '../../constants/modals/logs';

const toggleShowLogs = id => ({
  type: TOGGLE_SHOW_MODAL_LOGS,
  payload: {
    body: {
      id,
    },
  },
});

export const changeHoverProject = body => ({
  type: LAST_HOVER_PROJECT,
  payload: {
    body,
  },
});

export { toggleShowLogs as default };
