import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { Draggable } from 'react-beautiful-dnd';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styled from 'styled-components';

/* eslint-disable react/prop-types, no-unused-vars */
const Container = styled.div`
  width: auto;
  height. auto;
`;

const DragContext = ({
  children, selectedBoardId, draggableId, index, position,
}) => {
  if (!selectedBoardId) {
    return (
      <Draggable draggableId={draggableId} index={index} position={position}>
        {provided => (
          <Container
            {...provided.draggableProps}
            innerRef={provided.innerRef}
            {...provided.dragHandleProps}
          >
            {children}
          </Container>
        )}
      </Draggable>
    );
  }
  return (
    <Fragment>
      {children}
    </Fragment>
  );
};

DragContext.propTypes = {
  children: PropTypes.node.isRequired,
  selectedBoardId: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    selectedBoardId: state.user.selectedBoardId,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({ }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(DragContext);
