import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Typography from '@material-ui/core/Typography';
import Input from '@material-ui/core/Input';
import RejectIcon from '@material-ui/icons/Clear';
import Tooltip from '@material-ui/core/Tooltip';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import BlankProfile from '../../../../assets/blankprofile.png';
import Dialog from '../../../lib/Dialog';
import Form from '../../../lib/Form';
import PrimaryButton from '../../../lib/Button/PrimaryButton';
import { oneProjectUserModal } from '../../../../selectors/projects';
import toggleEditMemberRole from '../../../../actions/modals/members';
import { notifySuccess, notifyError } from '../../../../actions/notification';
import { fetchUpdateMember, fetchDeleteMember, fetchQuitMember } from '../../../../actions/api/members';
import { removeDiacritics, truncateString } from '../../../../utils';

import { allMembers } from '../../../../selectors/members';
import language from '../../../../constants/language';

/* eslint-disable no-param-reassign, react/prop-types, max-len */
const styles = {
  container: {
    display: 'flex',
    alignItems: 'flex-end',
    margin: '3px 0px',
  },
  title: {
    fontSize: 18,
    color: '#2a3d41',
    marginRight: 30,
    marginLeft: 5,
  },
  reject: {
    color: '#e53935',
    cursor: 'pointer',
    '&:hover': {
      color: '#ff6f60',
    },
    marginLeft: 10,
  },
  role: {
    fontSize: 18,
    color: '#2a3d41',
    marginRight: 30,
  },
  myrole: {
    fontSize: 18,
    color: '#2a3d41',
  },
  roles: {
    display: 'flex',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
  removeModalButton: {
    backgroundColor: '#ea5941',
    borderRadius: 0,
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
};

class RoleEditModal extends Component {
  constructor(props) {
    super(props);
    const memberRoles = [];
    this.props.members.forEach((member) => {
      memberRoles.push({ id: member.id, role: member.role });
    });
    this.state = {
      roles: memberRoles,
      removeModal: false,
      selectedMember: null,
    };
    this.handleClose = this.handleClose.bind(this);
    this.roleChange = this.roleChange.bind(this);
    this.deleteMember = this.deleteMember.bind(this);
    this.removeModalHandleClose = this.removeModalHandleClose.bind(this);
    this.openDeleteMemberModal = this.openDeleteMemberModal.bind(this);
  }

  roleChange = (event) => {
    const { name, value } = event.target;

    this.setState((prevState) => {
      const newRoles = JSON.parse(JSON.stringify(prevState.roles));
      newRoles.forEach((r) => {
        if (r.id === name) {
          if (r.role === value) {
            return;
          }
          r.role = value;
          this.props.fetchUpdateMember(this.props.projectId, r.id, {
            role: r.role,
          }).then((response) => {
            if (response.ok) {
              this.props.notifySuccess('Updated succesfully');
            }
          });
        }
      });
      return { roles: newRoles };
    });
  };

  handleClose() {
    this.props.toggleEditMemberRole();
  }

  removeModalHandleClose() {
    this.setState({ removeModal: false });
  }

  deleteMember(memberId) {
    if (memberId === this.props.userId) {
      this.props.fetchQuitMember(this.props.projectId, memberId)
        .then((response) => {
          if (response.ok) {
            this.props.notifySuccess('Successfully left the from project');
            this.props.toggleEditMemberRole();
            this.props.history.push('../home');
          }
        });
    } else {
      this.props.fetchDeleteMember(this.props.projectId, memberId)
        .then((response) => {
          if (response.ok) {
            this.props.notifySuccess('User was removed successfully from project');
            this.props.history.go();
          }
        });
    }
    this.setState({ removeModal: false, selectedMember: null });
  }

  openDeleteMemberModal(memberId) {
    this.setState({ removeModal: true, selectedMember: memberId });
  }

  render() {
    const {
      isFetching, classes, members, selectedLanguage,
    } = this.props;
    const title = language[selectedLanguage].roleEdit.modalTitle;
    const empty = !this.props.members.length;
    const footer = (
      <div>
        <PrimaryButton
          onClick={this.handleClose}
          loading={isFetching}
        >

          {language[selectedLanguage].roleEdit.close}
        </PrimaryButton>
      </div>
    );
    const sortorder = ['owner', 'manager', 'developer', 'viewer'];
    members.sort((a, b) => {
      if (sortorder.indexOf(a.role) - sortorder.indexOf(b.role) === 0) {
        if (removeDiacritics(a.name) < removeDiacritics(b.name)) { return -1; }
        if (removeDiacritics(a.name) > removeDiacritics(b.name)) { return 1; }
        return 0;
      }
      return sortorder.indexOf(a.role) - sortorder.indexOf(b.role);
    });
    return (
      <div>
        <Dialog
          open
          title={title}
          footer={footer}
          onClose={this.handleClose}
          clickClose={this.handleClose}
          onExited={this.handleClose}
        >
          {
            empty && (
              <Typography> {language[selectedLanguage].roleEdit.noUsers} </Typography>
            )
          }
          <Form>
            <FormControl style={{ minWidth: 200 }}>
              {members.map((member) => {
                const isDisabled = (member.role === 'owner') || (this.props.project.role === 'manager' && (member.role === 'manager' || member.role === 'owner')) || (this.props.project.role !== 'owner' && this.props.project.role !== 'manager');
                const isRemovable = (member.role === 'owner') || (this.props.project.role === 'manager' && (member.role === 'manager' || member.role === 'owner') && (member.id !== this.props.userId)) || (member.id !== this.props.userId && this.props.project.role !== 'owner' && this.props.project.role !== 'manager');
                return (
                  <div key={member.id} className={classes.container}>
                    {(!member.image || member.image === 'null')
                    && (
                      <img alt="" className={classes.customIcon} src={BlankProfile} />
                    )}
                    {(member.image && member.image !== 'null')
                    && (
                      <img alt="" className={classes.customIcon} src={member.image} />
                    )}
                    <Typography noWrap className={classes.title}>
                      {truncateString(member.name, 25)}
                    </Typography>
                    <div style={{ flexGrow: 1 }} />
                    <div className={classes.roles}>
                      {
                        isDisabled && (
                          <Typography
                            className={(
                              ((member.id !== this.props.userId) || (member.role === 'owner'))
                                ? classes.role : classes.myrole)}
                          >
                            {member.role[0].toUpperCase() + member.role.slice(1)}
                          </Typography>
                        )

                      }
                      {
                      (!isDisabled) && (
                      <Fragment>
                        <Select
                          name={member.id}
                          value={this.state.roles.filter(m => m.id === member.id)[0].role}
                          onChange={this.roleChange}
                          input={<Input id="role" />}
                        >
                          <MenuItem value="viewer">Viewer</MenuItem>
                          <MenuItem value="developer">Developer</MenuItem>
                          <MenuItem value="manager">Manager</MenuItem>
                        </Select>
                      </Fragment>
                      )
                    }
                      {
                        (!isRemovable) && (
                        <Tooltip placement="top-start" title="Remove from project">
                          <RejectIcon
                            onClick={() => this.openDeleteMemberModal(member.id)}
                            className={classes.reject}
                          />
                        </Tooltip>
                        )
                      }
                    </div>
                  </div>
                );
              })}
            </FormControl>
          </Form>
        </Dialog>
        <Dialog
          open={this.state.removeModal}
          onClose={this.removeModalHandleClose}
          clickClose={this.removeModalHandleClose}
        >
          <DialogTitle id="alert-dialog-title">{language[selectedLanguage].roleEdit.removeMemberTitle}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {language[selectedLanguage].roleEdit.removeMemberDescription}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <PrimaryButton
              style={styles.removeModalButton}
              onClick={this.removeModalHandleClose}
            >
              {language[selectedLanguage].roleEdit.removeMemberCancel}
            </PrimaryButton>
            <PrimaryButton
              style={styles.removeModalButton}
              onClick={() => this.deleteMember(this.state.selectedMember)}
              autoFocus
            >
              {language[selectedLanguage].roleEdit.removeMemberAccept}
            </PrimaryButton>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

RoleEditModal.propTypes = {
  isFetching: PropTypes.bool.isRequired,
  toggleEditMemberRole: PropTypes.func.isRequired,
  project: PropTypes.object.isRequired,
  notifySuccess: PropTypes.any.isRequired,
  projectId: PropTypes.string.isRequired,
  members: PropTypes.array.isRequired,
  classes: PropTypes.object.isRequired,
  fetchUpdateMember: PropTypes.func.isRequired,
  fetchDeleteMember: PropTypes.func.isRequired,
  fetchQuitMember: PropTypes.func.isRequired,
  userId: PropTypes.string.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    isFetching: state.app.isFetching,
    projectId: state.modal.members.selectedProject,
    members: allMembers(ownProps, state),
    userId: state.auth.id,
    userEmail: state.auth.uid,
    project: oneProjectUserModal(ownProps, state),
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateMember,
    fetchDeleteMember,
    fetchQuitMember,
    toggleEditMemberRole,
    notifySuccess,
    notifyError,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter((withStyles(styles)(RoleEditModal))));
