import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import RejectIcon from '@material-ui/icons/Clear';
import Checkbox from '@material-ui/core/Checkbox';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import { fetchUpdateTask } from '../../../../../../../actions/api/tasks';
import { fetchDeleteChecklist, fetchToggleChecklist, fetchUpdateChecklist } from '../../../../../../../actions/api/checklist';

const styles = {
  checked: {},
  item: {
    transform: 'scale(0.8)',
    padding: '5px',
    color: '#979797',
    '&$checked': {
      color: '#979797',
    },
  },
  reject: {
    cursor: 'pointer',
    float: 'right',
    color: '#2e4144',
    '&:hover': {
      color: 'black',
    },
  },
  textformat: {
    display: 'flex',
    padding: '0px 5px',
    alignItems: 'center',
    '&:hover': {
      backgroundColor: '#e0e0e0',
    },
  },
  textformat2: {
    display: 'flex',
    padding: '0px 5px',
    alignItems: 'center',
  },
  itemText: {
    cursor: 'pointer',
    wordWrap: 'break-word',
    color: '#2a3d41',
  },
  checkedItemText: {
    cursor: 'pointer',
    wordWrap: 'break-word',
    color: '#8e8e8ee8',
    textDecoration: 'line-through',
  },
  button: {
    boxShadow: 'unset',
    marginLeft: 10,
    color: 'white',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  edit: {
    flex: 1,
    display: 'flex',
    alignItems: 'center',
  },
  input: {
    padding: 8,
    fontSize: 14,
    color: '#2a3d41',
  },
  notchedOutline: {
    borderColor: '#ea5941 !important',
  },
};

class CheckItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mouse: false,
      text: null,
    };
    this.handleMouseEnter = this.handleMouseEnter.bind(this);
    this.handleMouseLeave = this.handleMouseLeave.bind(this);
    this.updateChecklist = this.updateChecklist.bind(this);
    this.deleteComment = this.deleteComment.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
    this.closeEdit = this.closeEdit.bind(this);
  }

  handleMouseEnter() {
    this.setState({ mouse: true });
  }

  handleEdit() {
    this.setState({ text: this.props.item.text });
  }

  closeEdit() {
    this.setState({ text: null });
  }

  handleMouseLeave() {
    this.setState({ mouse: false });
  }

  handleChange(e) {
    this.setState({ text: e.target.value });
  }

  deleteComment(id) {
    this.props.fetchDeleteChecklist(id);
  }

  updateChecklist() {
    this.props.fetchUpdateChecklist(this.props.item.id, { text: this.state.text })
      .then((res) => {
        if (res.ok) {
          this.closeEdit();
        }
      });
  }

  handleToggle(id) {
    if (this.props.projectRole !== 'viewer') {
      this.props.fetchToggleChecklist(id);
    }
  }

  render() {
    const { classes, item, projectRole } = this.props;
    const { mouse, text } = this.state;
    if (text !== null) {
      return (
        <div className={classes.edit}>
          <TextField
            id="outlined-full-width"
            autoFocus
            margin="none"
            variant="outlined"
            value={text}
            onChange={this.handleChange}
            InputProps={{
              classes: {
                input: classes.input,
                notchedOutline: classes.notchedOutline,
              },
            }}
          >
            {text}
          </TextField>
          <div style={{ flexGrow: 1 }} />
          <Button
            className={classes.button}
            size="small"
            variant="contained"
            onClick={this.updateChecklist}
          >

            Edit
          </Button>
          <Button
            className={classes.button}
            size="small"
            variant="contained"
            onClick={this.closeEdit}
          >

            Cancel
          </Button>
        </div>
      );
    }
    return (
      <div
        className={classes.textformat}
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
      >
        <Checkbox
          classes={{ root: classes.item, checked: classes.checked }}
          onChange={() => this.handleToggle(item.id)}
          checked={item.status}
        />
        <Typography
          onClick={this.handleEdit}
          className={item.status ? classes.checkedItemText : classes.itemText}
        >
          {item.text}
        </Typography>
        <div style={{ flexGrow: 1 }} />
        {(mouse && projectRole !== 'viewer')
          && (
            <RejectIcon
              onClick={() => this.deleteComment(item.id)}
              className={classes.reject}
            />
          )
        }
      </div>
    );
  }
}

CheckItem.propTypes = {
  classes: PropTypes.object.isRequired,
  item: PropTypes.object.isRequired,
  fetchDeleteChecklist: PropTypes.func.isRequired,
  fetchToggleChecklist: PropTypes.func.isRequired,
  fetchUpdateChecklist: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = () => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateTask,
    fetchDeleteChecklist,
    fetchToggleChecklist,
    fetchUpdateChecklist,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(CheckItem));
