import {
  FETCH_GET_CHECKLIST,
  FETCH_CREATE_CHECKLIST,
  FETCH_TOGGLE_CHECKLIST,
  FETCH_UPDATE_CHECKLIST,
  FETCH_DELETE_CHECKLIST,
} from '../../constants/api/checklist';

export const fetchTaskChecklist = id => ({
  type: FETCH_GET_CHECKLIST,
  fetch: true,
  url: `/tasks/${id}/checklist/`,
  params: {
    method: 'GET',
  },
});

export const fetchCreateChecklist = body => ({
  type: FETCH_CREATE_CHECKLIST,
  fetch: true,
  url: '/checklists',
  params: {
    method: 'POST',
    body,
  },
});

export const fetchToggleChecklist = id => ({
  type: FETCH_TOGGLE_CHECKLIST,
  fetch: true,
  url: `/checklists/${id}/status`,
  params: {
    method: 'POST',
  },
});

export const fetchUpdateChecklist = (id, body) => ({
  type: FETCH_UPDATE_CHECKLIST,
  fetch: true,
  url: `/checklists/${id}/`,
  params: {
    method: 'PUT',
    body,
  },
});

export const fetchDeleteChecklist = id => ({
  type: FETCH_DELETE_CHECKLIST,
  fetch: true,
  url: `/checklists/${id}/`,
  params: {
    method: 'DELETE',
  },
});
