import {
  TOGGLE_CREATE_MODAL_LABEL,
  TOGGLE_EDIT_MODAL_LABEL,
  TOGGLE_GET_MODAL_BOARD_LABELS,
} from '../../constants/modals/labels';

export const toggleCreateLabel = () => ({
  type: TOGGLE_CREATE_MODAL_LABEL,
  payload: {
    body: {
    },
  },
});

export const toggleEditLabel = id => ({
  type: TOGGLE_EDIT_MODAL_LABEL,
  payload: {
    body: {
      id,
    },
  },
});

export const toggleShowBoardLabels = id => ({
  type: TOGGLE_GET_MODAL_BOARD_LABELS,
  payload: {
    body: {
      id,
    },
  },
});
