import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import Form from '../../lib/Form';
import TextField from '../../lib/TextField';
import PrimaryButton from '../../lib/Button/PrimaryButton';
import language from '../../../constants/language';
import { fetchCreateBoard } from '../../../actions/api/boards';

const styles = {
  container: {
    display: 'flex',
    flex: 1,
    flexDirection: 'column',
    backgroundColor: 'white',
    padding: 20,
    marginTop: 10,
  },
  buttonContainer: {
    display: 'flex',
    flex: 1,
    margin: '10px 0px',
  },
};

class BoardForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
  }

  handleChange(name) {
    this.setState({ name });
  }

  handleCancel(event) {
    if (event) event.preventDefault();
    this.props.addBoard();
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    const { name } = this.state;
    this.props.fetchCreateBoard({
      project_id: this.props.projectId, name,
    });
    this.props.addBoard();
  }

  render() {
    const { classes, selectedLanguage } = this.props;
    return (
      <Form onSubmit={this.handleSubmit}>
        <div className={classes.container}>
          <TextField
            onChange={this.handleChange}
            type="text"
            name={this.state.name}
            fullWidth
            label={language[selectedLanguage].project.boardName}
            required
            id="name"
            autoFocus
          />
          <div className={classes.buttonContainer}>
            <PrimaryButton
              type="button"
              style={{ width: 100 }}
              onClick={this.handleCancel}
            >
              {language[selectedLanguage].project.cancel}
            </PrimaryButton>
            <div style={{ marginRight: 20 }} />
            <PrimaryButton
              style={{ width: 100 }}
              type="button"
              onClick={this.handleSubmit}
            >
              {language[selectedLanguage].project.create}
            </PrimaryButton>
          </div>
        </div>
      </Form>
    );
  }
}

BoardForm.propTypes = {
  projectId: PropTypes.string.isRequired,
  fetchCreateBoard: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  addBoard: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
    projectId: state.user.selectedProjectId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchCreateBoard,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(BoardForm));
