import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { Droppable, Draggable } from 'react-beautiful-dnd';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import AddIcon from '@material-ui/icons/Add';
import Typography from '@material-ui/core/Typography';

import Tooltip from '@material-ui/core/Tooltip';
import InnerListColumnComponent from './InnerListColumnComponent';
import TaskForm from '../TaskForm';

import DropdownComponent from './DropdownComponent';
import language from '../../../constants/language';
import { toggleTaskCreateForm } from '../../../actions/board';
import { getProjectRole } from '../../../selectors/projects';

const styles = {
  container: {
    margin: '10px 5px',
    border: '1px solid #2a3d41',
    height: 600,
    backgroundColor: 'white',
    display: 'flex',
    flexDirection: 'column',
    boxShadow: '8px 10px 18px -15px rgba(0, 0, 0, 0.5)',
  },
  title: {
    fontSize: 18,
    fontWeight: 'bold',
    color: '#2a3d41',
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    padding: '10px 5px 10px 18px',
    flexShrink: 0,
    borderBottom: '1px solid #2a3d41',
  },
  taskList: {
    borderTop: '1 solid #2a3d41',
    flexGrow: 1,
    minHeight: 100,
    minWidth: 300,
    overflow: 'auto',
  },
  icon: {
    color: '#2a3d41',
    padding: '0px 4px',
    cursor: 'pointer',
    '&:hover': {
      color: '#ca3921',
    },
  },
  menuIcon: {
    color: '#2a3d41',
    fontSize: 20,
  },
};

class ColumnComponent extends Component {
  constructor(props) {
    super(props);
    this.showTaskForm = this.showTaskForm.bind(this);
  }

  showTaskForm() {
    const { taskForm, taskColumn, column } = this.props;
    if ((!taskForm) || ((taskColumn === '') || (taskColumn !== column.id))) {
      this.props.toggleTaskCreateForm(column.id);
    } else {
      this.props.toggleTaskCreateForm();
    }
  }

  render() {
    const {
      column, index, taskForm, taskColumn, projectRole, classes, search, selectedLanguage,
    } = this.props;
    const roles = ['owner', 'manager'];
    const createRoles = ['owner', 'manager', 'developer'];

    return (
      <Draggable
        draggableId={column.id}
        index={index}
        position={column.position}
        isDragDisabled={!roles.includes(projectRole) || search}
      >
        {provided => (
          <div
            className={classes.container}
            {...provided.draggableProps}
            ref={provided.innerRef}
          >
            <div className={classes.header} {...provided.dragHandleProps}>
              <Typography className={classes.title}>
                {column.name}
              </Typography>
              <div style={{ flexGrow: 1 }} />
              {
                createRoles.includes(projectRole)
                && (
                  <div style={{ display: 'flex', alignItems: 'center' }}>
                    <Tooltip placement="top-start" title={language[selectedLanguage].board.addTask}>
                      <AddIcon onClick={this.showTaskForm} className={classes.icon} />
                    </Tooltip>
                    {
                      roles.includes(projectRole)
                      && (
                        <DropdownComponent column={column} />
                      )
                    }
                  </div>

                )
              }
            </div>
            <Droppable droppableId={column.id} type="task">
              {(providedTask, snapshot) => (
                <div
                  className={classes.taskList}
                  style={{ backgroundColor: snapshot.isDraggingOver ? 'lightgrey' : 'white' }}
                  ref={providedTask.innerRef}
                  {...providedTask.droppableProps}
                >
                  <InnerListColumnComponent columnId={column.id} />
                  {
                    taskForm && taskColumn === column.id
                    && (
                      <TaskForm columnId={column.id} />
                    )
                  }
                  {providedTask.placeholder}
                </div>
              )}
            </Droppable>
          </div>
        )}
      </Draggable>
    );
  }
}

ColumnComponent.propTypes = {
  column: PropTypes.object.isRequired,
  index: PropTypes.number.isRequired,
  taskForm: PropTypes.bool.isRequired,
  toggleTaskCreateForm: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
  search: PropTypes.string.isRequired,
  taskColumn: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    selectedLanguage: state.user.language,
    taskForm: state.board.showCreateTaskForm,
    taskColumn: state.board.selectedColumn,
    projectRole: getProjectRole(ownProps, state),
    search: state.task.search,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleTaskCreateForm,
  }, dispatch);
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(withStyles(styles)(ColumnComponent)),
);
