import React, { Component, Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import ProjectContainer from '../../components/project';
import CircularProgress from '../../components/lib/CircularProgress';

import { fetchGetFolders } from '../../actions/api/folders';
import { fetchGetProject } from '../../actions/api/projects';
import { fetchGetProjectBoards } from '../../actions/api/boards';
import { fetchProjectMembers } from '../../actions/api/members';

import { selectProject } from '../../actions/user';
import { hideAll } from '../../actions/board';

import { fetchedFolders } from '../../selectors/folders';
import { fetchedProjects } from '../../selectors/projects';

class Project extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }

  componentWillMount() {
    const { projectId } = this.props;
    this.props.hideAll();
    this.props.selectProject(projectId);
    this.props.fetchProjectMembers(projectId);
    Promise.all([
      this.props.fetchGetProjectBoards(projectId),
      this.props.fetchGetFolders(projectId),
      this.props.fetchGetProject(projectId),
      this.props.fetchGetProjectBoards(projectId),
    ]).then(() => {
      this.setState({ loading: false });
    });
  }

  render() {
    if (this.state.loading) {
      return <CircularProgress />;
    }
    return (
      <Fragment>
        <ProjectContainer />
      </Fragment>
    );
  }
}

Project.propTypes = {
  projectId: PropTypes.string.isRequired,
  // didFetchProjects: PropTypes.bool.isRequired,
  // didFetchFolders: PropTypes.bool.isRequired,
  fetchGetProject: PropTypes.func.isRequired,
  fetchGetProjectBoards: PropTypes.func.isRequired,
  fetchGetFolders: PropTypes.func.isRequired,
  selectProject: PropTypes.func.isRequired,
  fetchProjectMembers: PropTypes.func.isRequired,
  hideAll: PropTypes.func.isRequired,
};

function mapStateToProps(state, ownProps) {
  return {
    projectId: String(ownProps.match.params.id),
    didFetchProjects: fetchedProjects(ownProps, state),
    didFetchFolders: fetchedFolders(ownProps, state),
  };
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchGetProject,
    fetchGetProjectBoards,
    fetchGetFolders,
    selectProject,
    fetchProjectMembers,
    hideAll,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Project);
