import React, { Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import InvitationSending from './InvitationSending';

const ModalContainer = ({ invitationSending }) => {
  return (
    <Fragment>
      { invitationSending && <InvitationSending /> }
    </Fragment>
  );
};

ModalContainer.propTypes = {
  invitationSending: PropTypes.bool.isRequired,
};

const mapStateToProps = (state) => {
  return {
    invitationSending: state.modal.invitations.invitationSending,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(ModalContainer);
