import React from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import CommentsForm from './CommentsForm';
import Comments from './Comments';
import { onlyTaskComments } from '../../../../../../../selectors/comments';

const styles = theme => ({
  titleText: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(14),
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  text: {
    width: '300px',
    fontSize: theme.typography.pxToRem(12),
    color: '#2a3d41',
    marginTop: '20px',
    marginLeft: '10px',
  },
});

const Content = ({
  classes,
  comments,
  projectRole,
  task,
}) => {
  return (
    <div>
      <Typography className={classes.titleText}>

        Comments
      </Typography>
      <div>
        <div>
          {(projectRole !== 'viewer') && <CommentsForm task={task} />}
          {
            comments.length > 0
            && <Comments comments={comments} role={projectRole} />
          }
          {
            comments.length === 0
            && (
              <Typography className={classes.text}>

                There are no comments.
              </Typography>
            )
          }
        </div>
      </div>
    </div>
  );
};

Content.propTypes = {
  classes: PropTypes.object.isRequired,
  task: PropTypes.object.isRequired,
  comments: PropTypes.array.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    comments: onlyTaskComments(ownProps, state),
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Content));
