import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';
import Dialog from '../../../lib/Dialog';
import Form from '../../../lib/Form';
import PrimaryButton from '../../../lib/Button/PrimaryButton';

import { notifySuccess } from '../../../../actions/notification';
import { fetchDeleteColumn } from '../../../../actions/api/columns';

import { oneColumnFormModal } from '../../../../selectors/boards';
import { toggleDeleteColumnForm } from '../../../../actions/modals/columns';

import language from '../../../../constants/language';

class DeleteForm extends Component {
  constructor(props) {
    super(props);
    this.handleCloseColumn = this.handleCloseColumn.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleOnEnter = this.handleOnEnter.bind(this);
  }

  handleOnEnter() {
    const { column } = this.props;
    if (column.tasks.length === 0) {
      this.handleSubmit();
    }
  }

  handleCloseColumn() {
    this.props.toggleDeleteColumnForm();
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    const { column } = this.props;
    this.handleCloseColumn();
    this.props.fetchDeleteColumn(column.id)
      .then((response) => {
        if (response.ok) {
          this.props.notifySuccess('Column deleted');
        }
      });
  }

  render() {
    const { isFetching, column } = this.props;
    const notArchivedTasks = column.tasks.filter(task => !task.is_archived);
    const {
      deleteColumn,
      yes, no,
      columnWithTasks,
      columnWithArchivedTasks,
      sureDeleteColumn,
    } = language[this.props.selectedLanguage].modals;
    const title = `${deleteColumn} ${column.name}`;
    const footer = (
      <div style={{ flex: 1, display: 'flex' }}>
        <div style={{ flex: 1 }} />
        <PrimaryButton
          type="submit"
          onClick={this.handleCloseColumn}
          style={{ marginRight: 10 }}
        >

          { no }
        </PrimaryButton>
        <PrimaryButton
          type="submit"
          onClick={this.handleSubmit}
          loading={isFetching}
        >

          { yes }
        </PrimaryButton>
      </div>
    );
    const modalMessage = notArchivedTasks.length > 0
      ? columnWithTasks : columnWithArchivedTasks;
    const modalConfirmation = `${sureDeleteColumn} '${column.name}'?`;
    return (
      <Dialog
        open
        title={title}
        footer={footer}
        onEnter={this.handleOnEnter}
        onClose={this.handleCloseColumn}
        clickClose={this.handleCloseColumn}
        hideClose
      >
        <Form
          onSubmit={this.handleSubmit}
        >
          <Typography>
            { modalMessage }
          </Typography>
          <Typography>
            { modalConfirmation }
          </Typography>
        </Form>
      </Dialog>
    );
  }
}

DeleteForm.propTypes = {
  isFetching: PropTypes.bool,
  fetchDeleteColumn: PropTypes.func.isRequired,
  toggleDeleteColumnForm: PropTypes.func.isRequired,
  column: PropTypes.object.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

DeleteForm.defaultProps = {
  isFetching: false,
};

const mapStateToProps = (state, ownProps) => {
  return {
    isFetching: state.app.isFetching,
    column: oneColumnFormModal(ownProps, state),
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleDeleteColumnForm,
    fetchDeleteColumn,
    notifySuccess,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(DeleteForm);
