import React, { Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import EditModal from './EditModal';
import DeleteModal from './DeleteModal';
import EditLabelModal from './EditLabelModal';

const ModalContainer = ({ boardEditing, boardDeleting, boardLabelsShowing }) => {
  return (
    <Fragment>
      { boardEditing && <EditModal /> }
      { boardDeleting && <DeleteModal /> }
      { boardLabelsShowing && <EditLabelModal /> }
    </Fragment>
  );
};

ModalContainer.propTypes = {
  boardEditing: PropTypes.bool.isRequired,
  boardDeleting: PropTypes.bool.isRequired,
  boardLabelsShowing: PropTypes.bool.isRequired,
};

const mapStateToProps = (state) => {
  return {
    boardEditing: state.modal.boards.boardEditing,
    boardDeleting: state.modal.boards.boardDeleting,
    boardLabelsShowing: state.modal.labels.boardLabelsShowing,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(ModalContainer);
