import {
  TOGGLE_ACCEPT_MODAL_INVITATION,
  TOGGLE_SEND_MODAL_INVITATION,
  TOGGLE_EDIT_MODAL_INVITATION,
} from '../../constants/modals/invitations';

export const toggleAcceptInvitationForm = (id, projectId) => ({
  type: TOGGLE_ACCEPT_MODAL_INVITATION,
  payload: {
    body: {
      id,
      projectId,
    },
  },
});

export const toggleSendInvitationForm = id => ({
  type: TOGGLE_SEND_MODAL_INVITATION,
  payload: {
    body: {
      id,
    },
  },
});

export const toggleEditInvitationForm = id => ({
  type: TOGGLE_EDIT_MODAL_INVITATION,
  payload: {
    body: {
      id,
    },
  },
});
