import { createSelector } from 'redux-orm';

import orm from '../reducers/orm';

/* eslint-disable import/prefer-default-export */
export const onlyTaskChecklist = createSelector(
  orm,
  (_, state) => state.orm,
  (prop, state) => state.board.selectedTask,
  (session, taskId) => {
    return session.Checklist.filter((o) => {
      return o.task_id === taskId;
    }).toRefArray();
  },
);
