import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import Form from '../../lib/Form';
import TextField from '../../lib/TextField';
import PrimaryButton from '../../lib/Button/PrimaryButton';

import { fetchCreateTask } from '../../../actions/api/tasks';
import { toggleTaskCreateForm } from '../../../actions/board';

import language from '../../../constants/language';

const styles = {
  container: {
    display: 'flex',
    flex: 1,
    flexDirection: 'column',
    borderBottom: '1px solid #d8d8d8',
    backgroundColor: 'white',
    padding: 10,
    maxWidth: '300px',
  },
};

class TaskForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
  }

  handleChange(name) {
    this.setState({ name });
  }

  handleCancel(event) {
    if (event) event.preventDefault();
    this.props.toggleTaskCreateForm();
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    const { name } = this.state;
    const { columnId } = this.props;
    this.props.fetchCreateTask({ column_id: columnId, name })
      .then((response) => {
        if (response.ok) {
          this.props.toggleTaskCreateForm();
        }
      });
  }

  render() {
    const { classes, selectedLanguage } = this.props;
    return (
      <Form onSubmit={this.handleSubmit}>
        <div className={classes.container}>
          <TextField
            onChange={this.handleChange}
            type="text"
            maxLength={142}
            value={this.state.name}
            label={language[selectedLanguage].board.addTaskLabel}
            id="name"
            autoFocus
          />
          <div style={{ display: 'flex', flex: 1 }}>
            <PrimaryButton
              type="button"
              style={{ flex: 1 }}
              onClick={this.handleCancel}
            >
              {language[selectedLanguage].board.addTaskCancel}
            </PrimaryButton>
            <div style={{ flexGrow: 1 }} />
            <PrimaryButton
              style={{ flex: 1 }}
              type="button"
              onClick={this.handleSubmit}
            >
              {language[selectedLanguage].board.addTaskAccept}
            </PrimaryButton>
          </div>
        </div>
      </Form>
    );
  }
}

TaskForm.propTypes = {
  columnId: PropTypes.string.isRequired,
  fetchCreateTask: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  toggleTaskCreateForm: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchCreateTask,
    toggleTaskCreateForm,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(TaskForm));
