import React, { Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import LogsShowing from './LogsShowing';

const LogsModal = ({ logsShowing }) => {
  return (
    <Fragment>
      { logsShowing && <LogsShowing isModal /> }
    </Fragment>
  );
};

LogsModal.propTypes = {
  logsShowing: PropTypes.bool.isRequired,
};

const mapStateToProps = (state) => {
  return {
    logsShowing: state.modal.logs.logsShowing,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(LogsModal);
