class BoardListContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      redirect: false,
      addBoardSwitch: false
    };
    this.onDragStart = this.onDragStart.bind(this);
    this.onDragEnd = this.onDragEnd.bind(this);
    this.handleDropdown = this.handleDropdown.bind(this);
    this.handleInviteUser = this.handleInviteUser.bind(this);
    this.handleDownloadProject = this.handleDownloadProject.bind(this);
    this.handleUploadBoard = this.handleUploadBoard.bind(this);
    this.handleManageUser = this.handleManageUser.bind(this);
    this.handleEditInvitation = this.handleEditInvitation.bind(this);
    this.handleManageArchive = this.handleManageArchive.bind(this);
    this.handleShowLogs = this.handleShowLogs.bind(this);
    this.addBoard = this.addBoard.bind(this);
    this.handleManageGithub = this.handleManageGithub.bind(this);
    this.toggleAssignRepo = this.toggleAssignRepo.bind(this);
    this.handleRemoveGithub = this.handleRemoveGithub.bind(this);
    this.changeShowId = this.changeShowId.bind(this);
    this.handleFilter = this.handleFilter.bind(this);
    this.fetchTasks = this.fetchTasks.bind(this);
    this.handlePassData = this.handlePassData.bind(this);
    this.state = {
      showId: undefined,
      value: 0,
      setValue: 0,
      series_data: [],
      loading_gantt: false
    };
  }

  handleDropdown(event, newValue) {
    if (newValue === 1) {
      this.fetchTasks();
    }
    else {
      this.setState({ value: newValue });
    }
  }

  componentWillMount() {
    if (this.props.boards.length === 1) this.props.selectBoard(this.props.boards[0].id);
  }

  shouldComponentUpdate(nextProps) {
    if (this.state.value === 0) {
      return true;
    }
    if (nextProps.selectedLanguage !== this.props.selectedLanguage) {
      return true;
    }
    return false;
  }

  fetchTasks() {
    this.setState({ loading_gantt: true });
    const array = [];
    this.props.fetchGetProjectTasks(this.props.project.id).then(response => {
      if (response.status === 200) {
        response.body.forEach((task) => {
          const feed = {
            parent: task.board_id,
            name: task.name,
            id: task.id,
            start: task.starting_date ? Date.parse(task.starting_date) : null,
            end: task.deadline ? Date.parse(task.deadline) : null,
            type: 'task',
            completed: task.completed
          }
          array.push(feed);
        })
        this.setState({ series_data: array, loading_gantt: false, value: 1 });
      }
    })
  }

  onDragStart() {
    this.props.startDrag();
  }

  onDragEnd(result) {
    const { destination, source } = result;
    const { boards, project } = this.props;

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.droppableId
      && destination.index === source.index
    ) {
      return;
    }

    const oldOrder = { order: {} };
    const newOrder = { order: {} };

    boards.forEach((board) => {
      oldOrder.order[board.id] = board.position;
    });

    const reorderedBoards = reorder(boards, source, destination);

    reorderedBoards.forEach((board) => {
      newOrder.order[board.id] = board.position;
    });

    Promise.all(
      boards
        .filter(board => board.position !== newOrder.order[board.id])
        .map((board) => {
          return this.props.modelUpdateBoardOrder(
            { id: board.id, position: newOrder.order[board.id] },
          );
        }),
    ).then(() => {
      this.props.fetchUpdateProjectOrder(project.id, newOrder)
        .then((response) => {
          if (!response.ok) {
            Promise.all(
              boards
                .map((board) => {
                  return this.props.modelUpdateBoardOrder(
                    { id: board.id, position: oldOrder.order[board.id] },
                  );
                }),
            ).then(() => this.forceUpdate());
          }
        });
    });
  }

  addBoard() {
    const { addBoardSwitch } = this.state;
    this.setState({ addBoardSwitch: !addBoardSwitch });
  }

  handleInviteUser() {
    this.props.toggleSendInvitationForm(this.props.project.id);
  }

  handleManageUser() {
    this.props.toggleEditMemberRole(this.props.project.id);
  }

  handleEditInvitation() {
    this.props.toggleEditInvitationForm(this.props.project.id);
  }

  handleDownloadProject() {
    this.props.fetchExportProject(this.props.project.id).then(response => {
      if (response.status === 200) {
        const element = document.createElement("a");
        const json = JSON.stringify(response.body, null, "\t");
        const file = new Blob([json], { type: 'application/json' });
        element.href = URL.createObjectURL(file);
        element.download = "export_project.json";
        document.body.appendChild(element);
        element.click();
      }
    })
  }

  handleUploadBoard(event) {
    if (event) event.stopPropagation();
    const {
      selectedLanguage,
    } = this.props;
    const fileReader = new FileReader();
    fileReader.readAsText(event.target.files[0], "UTF-8");
    fileReader.onload = e => {
      console.log(JSON.parse(e.target.result));
      this.props.fetchImportBoard(JSON.parse(e.target.result), "import_board", this.props.project.id).then(response => {
        console.log(response);
        if (response.status === 200) {
          this.props.notifySuccess(language[selectedLanguage].app.uploadComplete);
        }
      })
    };
  }


  handleManageGithub() {
    this.props.fetchGithubConnect(this.props.project.id)
      .then((response) => {
        if (response.ok) {
          this.setState({ redirect: response.body.url });
        }
      });
  }

  toggleAssignRepo() {
    this.props.toggleRepoAssignForm(this.props.project.id);
  }

  handleRemoveGithub() {
    this.props.fetchGithubRemove(this.props.project.id)
      .then((response) => {
        if (response.ok) {
          this.props.notifySuccess('You succesfully removed github to the project');
        }
      });
  }

  handleManageArchive() {
    this.props.toggleManageArchiveModal(this.props.project.id);
  }

  handleShowLogs() {
    this.props.toggleShowLogs(this.props.project.id);
  }

  changeShowId(boardId) {
    const { showId } = this.state;
    if (showId === boardId) {
      this.setState({ showId: undefined });
    } else {
      this.setState({ showId: boardId });
    }
  }

  handlePassData() {
    const array = this.fetchTasks();
    const result = [];
    this.props.boards.forEach((board) => {
      const feed = {
        name: board.name,
        id: board.id,
        pointWidth: 5,
        collapsed: true,
        type: 'board'
      }
      result.push(feed);
    });
    return result;
  }

  handleFilter() {
    this.props.toggleTaskFilter();
  }

  render() {
    const {
      project, boards, classes, selectedLanguage, filter,
    } = this.props;
    const {
      redirect, showId, addBoardSwitch, series_data, loading_gantt
    } = this.state;
    if (!project) {
      return <h1> Loading ... </h1>;
    }

    const roles = ['owner', 'manager'];
    if (redirect) {
      window.location.replace(redirect);
    }
    if (loading_gantt) {
      return <CircularProgress />;
    }
    return (
      <div className={classes.root}>
        <Modals />
        <TaskModal />
        <Grid container className={classes.header} alignItems="center">
          <Typography className={classes.title}>
            <FileIcon className={classes.fileIcon} />
            {project.name}
          </Typography>
          <Divider className={classes.iconDivider} />
          <Tooltip title={language[selectedLanguage].project.editRoles}>
            <Group onClick={this.handleManageUser} className={classes.icon} />
          </Tooltip>
          <Divider className={classes.iconDivider} />
          <Tooltip title={language[selectedLanguage].project.downloadProjectInfo}>
            <CloudDownloadIcon onClick={this.handleDownloadProject} className={classes.icon} />
          </Tooltip>
          <Divider className={classes.iconDivider} />
          <input
            accept="application/JSON"
            className={classes.input}
            style={{ display: 'none' }}
            id="raised-button-file"
            type="file"
            onChange={(e) => this.handleUploadBoard(e)}
          />
          <Tooltip title={language[selectedLanguage].project.uploadBoardInfo}>
            <label htmlFor="raised-button-file">
              <CloudUploadIcon className={classes.iconUpload} />
            </label>
          </Tooltip>
          {
            roles.includes(project.role)
            && (
              <Fragment>
                <Divider className={classes.iconDivider} />
                <Tooltip title={language[selectedLanguage].project.manageInvitations}>
                  <SentIcon onClick={this.handleEditInvitation} className={classes.icon} />
                </Tooltip>
                <Divider className={classes.iconDivider} />
                <Tooltip title={language[selectedLanguage].project.archivedTask}>
                  <ArchiveIcon onClick={this.handleManageArchive} className={classes.icon} />
                </Tooltip>
                <Divider className={classes.iconDivider} />
                <Tooltip title={language[selectedLanguage].project.notifications}>
                  <History onClick={this.handleShowLogs} className={classes.icon} />
                </Tooltip>
                <Divider className={classes.iconDivider} />
                {
                  !project.has_github
                  && (
                    <Tooltip title={language[selectedLanguage].project.linkGithub}>
                      <GoMarkGithub
                        onClick={this.handleManageGithub}
                        className={classes.iconSmaller}
                      />
                    </Tooltip>
                  )
                }
                {
                  project.has_github
                  && (
                    <Fragment>
                      <Tooltip title={language[selectedLanguage].project.assignRepo}>
                        <GoGitPullRequest
                          onClick={this.toggleAssignRepo}
                          className={classes.icon}
                        />
                      </Tooltip>
                      <Divider className={classes.iconDivider} />
                      <Tooltip title={language[selectedLanguage].project.unlinkGithub}>
                        <LinkOffIcon onClick={this.handleRemoveGithub} className={classes.icon} />
                      </Tooltip>
                    </Fragment>
                  )
                }
                <Divider className={classes.iconDivider} />
                <Tooltip title={language[selectedLanguage].project.linkSlack}>
                  <FaSlack
                    onClick={() => {
                      window.open(
                        `https://slack.com/oauth/v2/authorize?client_id=821156281879.1099291048486&scope=incoming-webhook,commands&state=${this.props.project.id}`,
                        '_blank',
                      );
                    }}
                    className={classes.icon}
                  />
                </Tooltip>
              </Fragment>
            )
          }
          <div style={{ flex: 1 }} />
          {
            roles.includes(project.role)
            && (
              <Fragment>
                <Button size="small" color="secondary" onClick={this.handleInviteUser} className={classes.addBoardsButton}>
                  <Typography className={classes.addBoardsText}>
                    {language[selectedLanguage].project.inviteUser}
                  </Typography>
                </Button>
              </Fragment>
            )
          }
          <Typography>
            {language[selectedLanguage].project.allTasks}
          </Typography>
          <ButtonSwitch
            disableRipple
            checked={filter}
            onChange={this.handleFilter}
          />
          <Typography>
            {language[selectedLanguage].project.ownTasks}
          </Typography>

        </Grid>
        <Tooltip title={language[selectedLanguage].project.type}>
          <Paper square
            elevation={0}>
            <Tabs TabIndicatorProps={{
              style: {
                backgroundColor: "#DD624B"
              }
            }}
              className={classes.tabs_container}
              value={this.state.value}
              indicatorColor="primary"
              textColor="primary"
              onChange={this.handleDropdown}
              aria-label="disabled tabs example"
            >
              <Tab label="Kanban" />
              <Tab label="Gantt" />
            </Tabs>
          </Paper>
        </Tooltip>
        <Divider style={{ marginTop: '19px' }} />
        <DropContext onDragStart={this.onDragStart} onDragEnd={this.onDragEnd}>
          <Grid
            className={classes.container}
          >
            {this.state.value === 0
              ? <div>{boards.map((b, index) => (
                <div>
                  <BoardListItem
                    key={b.id}
                    board={b}
                    index={index}
                    showId={showId}
                    changeShowId={this.changeShowId}
                    show_table={this.state.value}
                  />
                </div>
              ))}</div> : <div style={{ height: 1 }} />
            }
            {this.state.value === 1
              ? <div><GanttChart
                boards={this.handlePassData()}
                tasks={series_data}
                language={selectedLanguage}
                projectRole={project.role}
                task_modal={this.props.toggleTaskEditForm}
                notifySuccess={this.props.notifySuccess}
                selectTask={this.props.selectTask}
              />
              </div> : <div style={{ height: 1 }} />
            }
            {
              addBoardSwitch && (
                <BoardForm addBoard={this.addBoard} />
              )
            }
            {!addBoardSwitch && !this.state.value
              && roles.includes(project.role) && (
                <div className={classes.buttonBoard}>
                  <Button onClick={this.addBoard} style={{ width: '100%' }}>
                    <Typography className={classes.buttonName}>
                      {language[selectedLanguage].project.addBoard}
                    </Typography>
                  </Button>
                </div>
              )
            }
          </Grid>
        </DropContext>

      </div>
    );
  }
}

BoardListContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  boards: PropTypes.array.isRequired,
  project: PropTypes.object.isRequired,
  task: PropTypes.object.isRequired,
  fetchUpdateProjectOrder: PropTypes.func.isRequired,
  modelUpdateBoardOrder: PropTypes.func.isRequired,
  toggleShowLogs: PropTypes.func.isRequired,
  toggleSendInvitationForm: PropTypes.func.isRequired,
  toggleEditMemberRole: PropTypes.func.isRequired,
  toggleEditInvitationForm: PropTypes.func.isRequired,
  toggleManageArchiveModal: PropTypes.func.isRequired,
  startDrag: PropTypes.func.isRequired,
  fetchGithubConnect: PropTypes.func.isRequired,
  fetchGetProjectTasks: PropTypes.func.isRequired,
  fetchExportProject: PropTypes.func.isRequired,
  toggleTaskEditForm: PropTypes.func.isRequired,
  fetchImportBoard: PropTypes.func.isRequired,
  toggleRepoAssignForm: PropTypes.func.isRequired,
  selectTask: PropTypes.func.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  fetchGithubRemove: PropTypes.func.isRequired,
  selectBoard: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  toggleTaskFilter: PropTypes.func.isRequired,
  filter: PropTypes.bool.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    project: oneProject(ownProps, state),
    boards: allBoards(ownProps, state),
    task: oneTask(ownProps, state),
    selectedLanguage: state.user.language,
    filter: state.project.filter,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateProjectOrder,
    modelUpdateBoardOrder,
    toggleSendInvitationForm,
    toggleEditInvitationForm,
    toggleEditMemberRole,
    fetchGithubConnect,
    fetchGetProjectTasks,
    fetchExportProject,
    fetchImportBoard,
    toggleTaskEditForm,
    toggleManageArchiveModal,
    toggleShowLogs,
    selectTask,
    startDrag,
    fetchGithubRemove,
    toggleRepoAssignForm,
    notifySuccess,
    selectBoard,
    toggleTaskFilter,
  }, dispatch);
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(withStyles(styles)(BoardListContainer)),
);
