import { withStyles } from '@material-ui/core/styles';
import React from 'react';
import PropTypes from 'prop-types';

import Tooltip from '@material-ui/core/Tooltip';
import Label from '../TaskComponents/Label';

const styles = {
  container: {
    display: 'flex',
    flexWrap: 'wrap',
    paddingBottom: 5,
  },
};

const Labels = ({
  classes,
  labels,
  maxWidth,
  ...rest
}) => (
  <div {...rest} style={{ maxWidth }} className={classes.container}>
    {labels.map(l => (
      <Tooltip placement="top" title={l.name} key={l.id}>
        <div>
          <Label label={l} customCursor="grab" displayName={false} customWidth="15px" customHeight="10px" />
        </div>
      </Tooltip>
    ))}
  </div>
);

Labels.propTypes = {
  classes: PropTypes.object.isRequired,
  labels: PropTypes.array.isRequired,
  maxWidth: PropTypes.number,
};

Labels.defaultProps = {
  maxWidth: undefined,
};

export default withStyles(styles)(Labels);
