import {
  FETCH_IMPORT_BOARD,
  FETCH_IMPORT_PROYECT,
} from '../../constants/api/import';


export const fetchImportBoard = (body, fileName, projectId) => ({
  type: FETCH_IMPORT_BOARD,
  fetch: true,
  url: '/import/board',
  params: {
    method: 'POST',
    body,
  },
  queryParams: {
    project_id: projectId,
    file_name: fileName
  },
});

export const fetchImportProject = (body, fileName) => ({
  type: FETCH_IMPORT_PROYECT,
  fetch: true,
  url: `/import/project`,
  params: {
    method: 'POST',
    body,
  },
  queryParams: {
    file_name: fileName
  },
});

