import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import { truncateString } from '../../../../utils';
import BlankProfile from '../../../../assets/blankprofile.png';
import Dialog from '../../../lib/Dialog';
import language from '../../../../constants/language';
import { toggleTaskAssignForm } from '../../../../actions/board';
import { fetchTaskAssign, fetchTaskUnassign } from '../../../../actions/api/members';

import { onlyMembers } from '../../../../selectors/members';

const styles = theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  item: {
    color: '#ea5941',
    '&$checked': {
      color: '#ea5941',
    },
  },
  checked: {},
  icon: {
    color: '#2a3d41',
    fontSize: 20,
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
});

class TaskAssignForm extends Component {
  constructor(props) {
    super(props);
    this.handleClose = this.handleClose.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
  }

  handleToggle(event, userId, assigned) {
    if (event) event.stopPropagation();
    const { taskId } = this.props;
    if (assigned) {
      this.props.fetchTaskUnassign(taskId, userId);
    } else {
      this.props.fetchTaskAssign(taskId, { user_id: userId });
    }
  }

  handleClose() {
    this.props.toggleTaskAssignForm();
  }

  render() {
    const { showAssignForm, classes, selectedLanguage } = this.props;
    const title = language[selectedLanguage].tasks.assignUser;
    return (
      <Dialog
        open={showAssignForm}
        title={title}
        onClose={this.handleClose}
        clickClose={this.handleClose}
        onExit={this.handleClose}
      >
        <div className={classes.root}>
          <List>
            {this.props.members.map(mem => (
              <ListItem
                key={mem.id}
                dense
                button
                onClick={event => this.handleToggle(event, mem.id, mem.assigned)}
              >
                {(!mem.image || mem.image === 'null')
                && (
                  <img alt="" className={classes.customIcon} src={BlankProfile} />
                )}
                {(mem.image && mem.image !== 'null')
                && (
                  <img alt="" className={classes.customIcon} src={mem.image} />
                )}
                {mem.name
                  && (
                    <ListItemText>
                      <div style={{
                        display: 'flex',
                        alignItems: 'flex-end',
                        margin: '3px 0px',
                      }}
                      >
                        <Typography noWrap>
                          {truncateString(mem.name, 20)}
                        </Typography>
                        <div style={{ flexGrow: 1 }} />

                        <Typography style={{ marginLeft: 10 }}>
                          {mem.role.charAt(0).toUpperCase() + mem.role.slice(1)}
                        </Typography>
                      </div>
                    </ListItemText>
                  )
                }
                {!mem.name
                  && (
                    <ListItemText primary={`${mem.email.split('@')[0].charAt(0).toUpperCase() + mem.email.split('@')[0].slice(1)} - ${mem.role.charAt(0).toUpperCase() + mem.role.slice(1)}`} />
                  )
                }
                <ListItemSecondaryAction>
                  <Checkbox
                    classes={{ root: classes.item, checked: classes.checked }}
                    onChange={event => this.handleToggle(event, mem.id, mem.assigned)}
                    checked={mem.assigned}
                  />
                </ListItemSecondaryAction>
              </ListItem>
            ))}
          </List>
        </div>
      </Dialog>
    );
  }
}

TaskAssignForm.propTypes = {
  classes: PropTypes.object.isRequired,
  showAssignForm: PropTypes.bool.isRequired,
  toggleTaskAssignForm: PropTypes.func.isRequired,
  fetchTaskAssign: PropTypes.func.isRequired,
  fetchTaskUnassign: PropTypes.func.isRequired,
  taskId: PropTypes.string.isRequired,
  members: PropTypes.array.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    selectedLanguage: state.user.language,
    isFetching: state.app.isFetching,
    showAssignForm: state.board.showAssignTaskForm,
    taskId: state.board.selectedTask,
    members: onlyMembers(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleTaskAssignForm,
    fetchTaskAssign,
    fetchTaskUnassign,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(TaskAssignForm));
