import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import EditIcon from '@material-ui/icons/Edit';
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlined';
import ArchiveIcon from '@material-ui/icons/Archive';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';

import CustomMenu from '../../lib/Menu/index';
import CustomMenuItem from '../../lib/Menu/MenuItem/index';

import { toggleEditColumnForm, toggleDeleteColumnForm } from '../../../actions/modals/columns';
import { notifySuccess } from '../../../actions/notification';
import {
  fetchArchiveTasksByColumn,
  fetchCompleteTasksByColumn,
} from '../../../actions/api/columns';

import { toggleDropDownColumn } from '../../../actions/board';

import language from '../../../constants/language';

const styles = {
  menuIcon: {
    color: '#2a3d41',
    fontSize: 20,
  },
};

class DropdownComponent extends Component {
  constructor(props) {
    super(props);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
    this.handleArchiveTasks = this.handleArchiveTasks.bind(this);
    this.handleCompleteTasks = this.handleCompleteTasks.bind(this);
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.props.toggleDropDownColumn(this.props.column.id);
  }

  handleDelete(ev) {
    ev.stopPropagation();
    const { column } = this.props;
    this.props.toggleDeleteColumnForm(column.id);
    this.props.toggleDropDownColumn(column.id);
  }

  handleEdit(ev) {
    ev.stopPropagation();
    const { column } = this.props;
    this.props.toggleEditColumnForm(column.id);
    this.props.toggleDropDownColumn(column.id);
  }

  handleArchiveTasks(ev) {
    ev.stopPropagation();
    const { column } = this.props;
    this.props.fetchArchiveTasksByColumn(column.id)
      .then((response) => {
        if (response.ok) {
          this.props.notifySuccess('Tasks archived successfully');
        }
      });
    this.props.toggleDropDownColumn(column.id);
  }

  handleCompleteTasks(ev) {
    ev.stopPropagation();
    const { column } = this.props;
    this.props.fetchCompleteTasksByColumn(column.id)
      .then((response) => {
        if (response.ok) {
          this.props.notifySuccess('Tasks completed successfully');
        }
      });
    this.props.toggleDropDownColumn(column.id);
  }

  render() {
    const {
      classes, column, selectedColumn, selectedLanguage, showDropDownColumn,
    } = this.props;
    const open = (showDropDownColumn) && (column.id === selectedColumn);
    return (
      <Fragment>
        <div style={{ display: 'flex', alignItems: 'center', marginRight: 5 }}>
          <CustomMenu open={open} onClick={this.onClick} style={{ padding: 0 }}>
            <CustomMenuItem id="Edit" onClick={this.handleEdit}>
              <EditIcon className={classes.menuIcon} />

              &nbsp;{language[selectedLanguage].board.editColumn}
            </CustomMenuItem>
            <CustomMenuItem id="Delete" onClick={this.handleDelete}>
              <DeleteOutlinedIcon className={classes.menuIcon} />

              &nbsp;{language[selectedLanguage].board.deleteColumn}
            </CustomMenuItem>
            <CustomMenuItem id="ArchiveTasks" onClick={this.handleArchiveTasks}>
              <ArchiveIcon className={classes.menuIcon} />

              &nbsp;{language[selectedLanguage].board.archiveTasks}
            </CustomMenuItem>
            <CustomMenuItem id="CompleteTasks" onClick={this.handleCompleteTasks}>
              <CheckCircleOutlineIcon className={classes.menuIcon} />

              &nbsp;{language[selectedLanguage].board.completeTasks}
            </CustomMenuItem>
          </CustomMenu>
        </div>
      </Fragment>
    );
  }
}

DropdownComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  column: PropTypes.any.isRequired,
  toggleDropDownColumn: PropTypes.func.isRequired,
  toggleEditColumnForm: PropTypes.func.isRequired,
  toggleDeleteColumnForm: PropTypes.func.isRequired,
  fetchArchiveTasksByColumn: PropTypes.func.isRequired,
  fetchCompleteTasksByColumn: PropTypes.func.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  selectedColumn: PropTypes.string.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  showDropDownColumn: PropTypes.bool.isRequired,
};

const mapStateToProps = (state) => {
  return {
    showDropDownColumn: state.board.showDropDownColumn,
    selectedColumn: state.board.selectedColumn,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      toggleDropDownColumn,
      toggleEditColumnForm,
      toggleDeleteColumnForm,
      fetchArchiveTasksByColumn,
      fetchCompleteTasksByColumn,
      notifySuccess,
    }, dispatch,
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(DropdownComponent));
