import { createSelector } from 'redux-orm';

import orm from '../reducers/orm';

import { filterText, removeDiacritics } from '../utils';

const getRef = obj => (obj != null ? obj.ref : null);

export const oneTask = createSelector(
  orm,
  (_, state) => state.orm,
  props => props.taskId,
  (props, state) => props.boardId || state.user.selectedBoardId,
  (session, taskId, boardId) => {
    const task = getRef(session.Task.get(o => o.id === taskId));
    if (task === null) return null;
    const labelIds = session.Board.get(o => o.id === boardId).ref.labels;
    const labels = session.Label
      .filter(o => ((!labelIds || labelIds.includes(o.id))
      && task.labels.includes(o.id))).toRefArray();
    return Object.assign({}, task, {
      labels,
    });
  },
);

export const allTasks = createSelector(
  orm,
  (_, state) => state.orm,
  (session) => {
    return session.Task.filter(o => !o.is_archived).toRefArray();
  },
);

export const onlyColumnTasks = createSelector(
  orm,
  (_, state) => state.orm,
  (_, state) => state.task.search,
  (_, state) => state.auth.name,
  (_, state) => state.project.filter,
  props => props.columnId,
  (session, search, name, filter, columnId) => {
    const cleanName = filterText(removeDiacritics(name)).filter(Boolean).join("");
    if (!search && !filter) {
      return session.Task
        .filter(o => !(o.is_archived) && (o.column_id === columnId))
        .orderBy(o => o.position)
        .toRefArray();
    }
    const assignedToArray = (task) => {
      const assignedArray = [];
      task.assigned.forEach(member => assignedArray.push(
        removeDiacritics(session.Member.get(c => c.id === member).ref.name).replaceAll(" ", ""),
      ));
      return assignedArray;
    };
    if (search && filter) {
      const labelsToArray = (task) => {
        const labelArray = [];
        task.labels.forEach(label => labelArray.push(
          removeDiacritics(session.Label.get(c => c.id === label).ref.name),
        ));
        return labelArray;
      };
      const filteredSearch = filterText(removeDiacritics(search)).filter(Boolean);
      return session.Task
        .filter(o => !(o.is_archived) && (o.column_id === columnId)
          && (assignedToArray(o).includes(cleanName))
          && ((filteredSearch.every(a => removeDiacritics(o.name).includes(a)
            || !(!o.description || !removeDiacritics(o.description).includes(a))
            || (labelsToArray(o).filter(palabra => palabra.includes(a)).length > 0)
            || (assignedToArray(o).filter(palabra => palabra.includes(a)).length > 0)))))
        .orderBy(o => o.position)
        .toRefArray();
    }
    if (search && !filter) {
      const labelsToArray = (task) => {
        const labelArray = [];
        task.labels.forEach(label => labelArray.push(
          removeDiacritics(session.Label.get(c => c.id === label).ref.name),
        ));
        return labelArray;
      };
      const filteredSearch = filterText(removeDiacritics(search)).filter(Boolean);
      return session.Task
        .filter(o => !(o.is_archived) && (o.column_id === columnId)
          && ((filteredSearch.every(a => removeDiacritics(o.name).includes(a)
            || !(!o.description || !removeDiacritics(o.description).includes(a))
            || (labelsToArray(o).filter(palabra => palabra.includes(a)).length > 0)
            || (assignedToArray(o).filter(palabra => palabra.includes(a)).length > 0)))))
        .orderBy(o => o.position)
        .toRefArray();
    }
    // if (!search && filter)
    return session.Task
      .filter(o => !(o.is_archived) && (o.column_id === columnId)
        && (assignedToArray(o).includes(cleanName)))
      .orderBy(o => o.position)
      .toRefArray();
  },
);

export const allArchivedTasks = createSelector(
  orm,
  (_, state) => state.orm,
  (_, state) => state.project.search,
  props => props.board.id,
  (session, search, boardId) => {
    if (!search) {
      const columns = session.Column
        .filter(o => o.board_id === boardId)
        .toRefArray()
        .map(c => c.id);
      return session.Task
        .filter(o => columns.includes(o.column_id) && o.is_archived)
        .toRefArray();
    }
    const columns = session.Column
      .filter(o => o.board_id === boardId)
      .toRefArray()
      .map(c => c.id);
    const labelsToArray = (task) => {
      const labelArray = [];
      task.labels.forEach(label => labelArray.push(
        removeDiacritics(session.Label.get(c => c.id === label).ref.name),
      ));
      return labelArray;
    };
    const assignedToArray = (task) => {
      const assignedArray = [];
      task.assigned.forEach(member => assignedArray.push(
        removeDiacritics(session.Member.get(c => c.id === member).ref.name),
      ));
      return assignedArray;
    };
    const filteredSearch = filterText(removeDiacritics(search)).filter(Boolean);
    return session.Task
      .filter(o => columns.includes(o.column_id) && o.is_archived
      && ((filteredSearch.every(a => removeDiacritics(o.name).includes(a)
      || !(!o.description || !removeDiacritics(o.description).includes(a))
      || (labelsToArray(o).filter(palabra => palabra.includes(a)).length > 0)
      || (assignedToArray(o).filter(palabra => palabra.includes(a)).length > 0)))))
      .toRefArray();
  },
);

export const onlyTask = createSelector(
  orm,
  (_, state) => state.orm,
  (_, state) => state.board.selectedTask,
  (session, taskId) => {
    return getRef(session.Task.get(o => o.id === taskId));
  },
);

export const taskName = createSelector(
  orm,
  (_, state) => state.orm,
  (_, state) => state.board.selectedTask,
  (session, taskId) => {
    return Object.assign({}, getRef(session.Task.get(o => o.id === taskId))).name;
  },
);
