import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Input from '@material-ui/core/Input';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import Divider from '@material-ui/core/Divider';
import FileIcon from '@material-ui/icons/FileCopy';
import AttachmentItem from './attachmentItem';
import Form from '../../../../../../lib/Form';
import { onlyTaskAttachments } from '../../../../../../../selectors/attachments';
import { addTaskAttachment } from '../../../../../../../actions/api/attachments';
import {
  notifySuccess,
  notifyError,
} from '../../../../../../../actions/notification';
import PrimaryButton from '../../../../../../lib/Button/PrimaryButton';

/* eslint-disable jsx-a11y/label-has-associated-control, jsx-a11y/label-has-for */
// eslint-disable-next-line arrow-parens
const styles = (theme) => ({
  titleText: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(15),
    fontWeight: 'bold',
    margin: 0,
    paddingRight: 5,
    marginTop: '10px',
    marginBottom: '30px',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
  text: {
    fontSize: '12px',
    fontWeight: 'normal',
    color: '#2a3d41',
    paddingTop: '3px',
    paddingRight: '10px',
  },
  formContainer: {
    display: 'grid',
    gridTemplateRows: '1fr 1fr',
    gridGap: 0,
    width: '400px',
    backgroundColor: 'gainsboro',
    borderRadius: '10px',
    border: '2px solid lightgrey',
  },
  selectFileContainer: {
    display: 'grid',
    gridTemplateColumns: '1fr 1fr',
    gridGap: 0,
    justifyContent: 'center',
    outline: 'black',
    margin: '10px',
  },
  button: {
    boxShadow: 'unset',
    color: 'white',
    fontSize: theme.typography.pxToRem(12),
    alignSelf: 'center',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
    margin: '10px',
  },
  selectButton: {
    boxShadow: 'unset',
    color: 'white',
    fontSize: theme.typography.pxToRem(12),
    alignSelf: 'center',
    float: 'right',
    width: '150px',
    backgroundColor: 'grey',
    '&:hover': {
      backgroundColor: 'darkgrey',
    },
    justifySelf: 'right',
  },
  input: {
    display: 'none',
  },
  rightIcon: {
    marginLeft: theme.spacing.unit,
  },
});

class Content extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      file: null,
    };
    this.handleSelectedFile = this.handleSelectedFile.bind(this);
    this.handleFile = this.handleFile.bind(this);
  }

  handleSelectedFile(event) {
    const getBase64 = (file) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.setState({ file: reader.result });
      };
    };
    getBase64(event.target.files[0]);
    this.setState({ name: event.target.files[0].name });
  }

  handleFile(event) {
    event.preventDefault();
    const { taskId } = this.props;
    const { file, name } = this.state;
    if (file) {
      this.setState({ loading: true });
      this.props.addTaskAttachment(taskId, { file, name }).then((response) => {
        if (response.ok) {
          this.setState({
            loading: false,
            name: '',
            file: null,
          });
          this.props.notifySuccess('File was added successfully');
        }
      });
    } else {
      this.props.notifyError('Invalid File');
    }
  }

  render() {
    const { classes, attachments, projectRole } = this.props;
    const { name } = this.state;
    return (
      <Fragment>
        <Typography className={classes.titleText}>Attachments</Typography>
        <Fragment>
          {projectRole !== 'viewer' && (
            <Form encType="multipart/form-data" onSubmit={this.handleFile}>
              {attachments
                && (
                  attachments.map((h) => {
                    return (
                      <div style={{ display: 'flex', marginBottom: 10 }}>
                        <FileIcon style={{ marginRight: 10, marginBottom: 0 }} />
                        <AttachmentItem key={h.id} item={h} style={{ marginBottom: 10 }} />
                      </div>
                    );
                  }))}
              {!attachments && (
                <Typography className={classes.text}>
                  There are not attachments in this task.
                </Typography>
              )}
              <div className={classes.mainContainer}>
                <div className={classes.formContainer}>
                  <div className={classes.selectFileContainer}>
                    {name && (
                      <Typography
                        style={{ alignSelf: 'center', justifySelf: 'right' }}
                      >
                        {name}
                      </Typography>
                    )}
                    {!name && (
                      <Typography
                        style={{ alignSelf: 'center', justifySelf: 'right' }}
                      >
                        No file selected...
                      </Typography>
                    )}
                    <Input
                      className={classes.input}
                      id="raised-button-file"
                      multiple
                      type="file"
                      onChange={this.handleSelectedFile}
                    />
                    <label htmlFor="raised-button-file">
                      <PrimaryButton
                        type="submit"
                        component="span"
                        className={classes.selectButton}
                      >
                        Select File
                      </PrimaryButton>
                    </label>
                  </div>
                  <div>
                    <Divider />
                    <PrimaryButton
                      type="submit"
                      loading={this.state.loading}
                      className={classes.button}
                      style={{ width: '380px' }}
                    >
                      Upload
                      <CloudUploadIcon className={classes.rightIcon} />
                    </PrimaryButton>
                  </div>
                </div>
              </div>
            </Form>
          )}
        </Fragment>
      </Fragment>
    );
  }
}

Content.propTypes = {
  classes: PropTypes.object.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  addTaskAttachment: PropTypes.func.isRequired,
  taskId: PropTypes.string.isRequired,
  notifyError: PropTypes.func.isRequired,
  attachments: PropTypes.array.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    taskId: state.board.selectedTask,
    attachments: onlyTaskAttachments(ownProps, state),
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      notifySuccess,
      addTaskAttachment,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(Content));
