import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import AddIcon from '@material-ui/icons/AddCircle';
import Tooltip from '@material-ui/core/Tooltip';
import Logo from '../../../../../../lib/Logo/Member';
import BlankProfile from '../../../../../../../assets/blankprofile.png';
import { taskOpenMembers } from '../../../../../../../actions/task';

const styles = theme => ({
  title: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(15),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  icon: {
    fontSize: 20,
    color: '#2a3d41',
    padding: '0px 5px',
    '&:hover': {
      color: '#ca3921',
    },
    marginTop: '2.5px',
    width: 20,
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
    padding: '0px 5px',
  },
});

class Members extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
    this.editMember = this.editMember.bind(this);
  }

  componentWillMount() {
    this.setState({ loading: false });
  }

  editMember() {
    this.props.taskOpenMembers();
  }

  render() {
    const { members, classes, projectRole } = this.props;
    if (this.state.loading) return <div> Loading... </div>;
    return (
      <Fragment>
        <div style={{
          display: 'flex',
          flexDirection: 'row',
          width: '100%',
          marginBottom: 20,
        }}
        >
          <Logo size="23px" style={{ paddingRight: 5 }} />
          <Typography className={classes.title} style={{ paddingRight: 5 }}>

            Members:
          </Typography>
          <div style={{ display: 'flex', flexWrap: 'wrap' }}>
            {
              members.map((member) => {
                return (
                  <Fragment
                    key={member.id}
                  >
                    {(!member.image || member.image === 'null')
                    && (
                      <Tooltip placement="top-start" title={member.name}>
                        <img alt="" className={classes.customIcon} src={BlankProfile} />
                      </Tooltip>
                    )}
                    {(member.image && member.image !== 'null')
                    && (
                    <Tooltip placement="top-start" title={member.name}>
                      <img alt="" className={classes.customIcon} src={member.image} />
                    </Tooltip>
                    )}
                  </Fragment>
                );
              })
              }
            {(projectRole !== 'viewer')
            && <AddIcon className={classes.icon} onClick={this.editMember} />
            }
          </div>
        </div>
      </Fragment>
    );
  }
}

Members.propTypes = {
  members: PropTypes.array.isRequired,
  classes: PropTypes.object.isRequired,
  taskOpenMembers: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ taskOpenMembers }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Members));
