import {
  FETCH_EXPORT_BOARD,
  FETCH_EXPORT_PROYECT,
  FETCH_EXPORT_PROYECTS_USER,
} from '../../constants/api/export';


export const fetchExportBoard = boardId => ({
  type: FETCH_EXPORT_BOARD,
  fetch: true,
  url: '/export/board',
  params: {
    method: 'GET',
  },
  queryParams: {
    board_id: boardId,
  },
});

export const fetchExportProject = projectId => ({
  type: FETCH_EXPORT_PROYECT,
  fetch: true,
  url: `/export/project`,
  params: {
    method: 'GET',
  },
  queryParams: {
    project_id: projectId,
  },
});

export const fetchExportProjectsUser = () => ({
  type: FETCH_EXPORT_PROYECTS_USER,
  fetch: true,
  url: `/export/projects`,
  params: {
    method: 'GET',
  },
});
