import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';

import Header from './Header';
import MainContent from './MainContent';
import MainSidebar from './MainContent/Sidebar';
import SubContent from './SubContent';
import SubSidebar from './SubContent/Sidebar';

import { onlyTask } from '../../../../selectors/tasks';
import { onlyTaskLabels } from '../../../../selectors/labels';
import { onlyTaskHours } from '../../../../selectors/hours';
import { toggleTaskEditForm } from '../../../../actions/board';
import { fetchGetTasks } from '../../../../actions/api/tasks';
import { fetchGetHours } from '../../../../actions/api/hours';
import { onlyTaskComments } from '../../../../selectors/comments';
import { fetchTaskComments } from '../../../../actions/api/comments';
import { fetchTaskChecklist } from '../../../../actions/api/checklist';
import { getTaskAttachment } from '../../../../actions/api/attachments';

const styles = {
  wrapper: {
    display: 'grid',
    gridTemplateColumns: 'repeat(8, 1fr)',
    gridGap: '0px',
    width: '800px',
    maxHeight: 'calc(100vh - 100px)',
    gridTemplateRows: '3fr 2fr',
  },
  title: {
    borderBottom: '2px solid #d8d8d8',
  },
  mainContent: {
    gridColumn: 'span 6 / auto',
  },
  mainSidebar: {
    gridColumn: 'span 2 / auto',
    borderLeft: '2px solid #d8d8d8',
  },
  subContent: {
    gridColumn: 'span 6 / auto',
    backgroundColor: '#f2f2f2',
  },
  subSidebar: {
    gridColumn: 'span 2 / auto',
    backgroundColor: '#2a3d41',
  },
  DialogPadding: {
    maxHeight: 'calc(100% - 25px)',
  },
};

class TaskEdit extends Component {
  constructor(props) {
    super(props);
    this.handleCloseTask = this.handleCloseTask.bind(this);
  }

  componentWillMount() {
    const { task } = this.props;
    this.props.fetchGetHours(task.id);
    this.props.fetchTaskComments(task.id);
    this.props.fetchTaskChecklist(task.id);
    this.props.getTaskAttachment(task.id);
  }

  handleCloseTask() {
    const { selectedBoardId } = this.props;
    this.props.fetchGetTasks(selectedBoardId);
    this.props.toggleTaskEditForm();
  }

  render() {
    const {
      classes, task,
    } = this.props;

    return (
      <Dialog
        maxWidth={false}
        open
        onClose={this.handleCloseTask}
        onExited={this.handleCloseTask}
        classes={{
          paperScrollPaper: classes.DialogPadding,
        }}
      >
        <div className={classes.title}>
          <Header
            taskName={task.name}
            taskCompleted={!!task.completed}
            clickClose={this.handleCloseTask}
          />
        </div>
        <div className={classes.wrapper}>
          <DialogContent className={classes.mainContent}>
            <MainContent />
          </DialogContent>

          <DialogContent className={classes.mainSidebar}>
            <MainSidebar />
          </DialogContent>

          <DialogContent className={classes.subContent}>
            <SubContent />
          </DialogContent>

          <DialogContent className={classes.subSidebar}>
            <SubSidebar />
          </DialogContent>
        </div>

      </Dialog>
    );
  }
}

TaskEdit.propTypes = {
  task: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired,
  toggleTaskEditForm: PropTypes.func.isRequired,
  fetchGetHours: PropTypes.func.isRequired,
  fetchGetTasks: PropTypes.func.isRequired,
  fetchTaskComments: PropTypes.func.isRequired,
  fetchTaskChecklist: PropTypes.func.isRequired,
  getTaskAttachment: PropTypes.func.isRequired,
  selectedBoardId: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    task: onlyTask(ownProps, state),
    hours: onlyTaskHours(ownProps, state),
    projectRole: state.board.selectedRole,
    // members: [], // members selector
    labels: onlyTaskLabels(ownProps, state), // Labels selector
    comments: onlyTaskComments(ownProps, state), // Comments selector
    openComments: state.task.openComments,
    selectedBoardId: state.user.selectedBoardId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleTaskEditForm,
    fetchGetHours,
    fetchGetTasks,
    fetchTaskComments,
    fetchTaskChecklist,
    getTaskAttachment,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(TaskEdit));
