import React, { Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import InvitationEditing from './InvitationEditing';

const ModalContainer = ({ invitationEditing }) => {
  return (
    <Fragment>
      { invitationEditing && <InvitationEditing /> }
    </Fragment>
  );
};

ModalContainer.propTypes = {
  invitationEditing: PropTypes.bool.isRequired,
};

const mapStateToProps = (state) => {
  return {
    invitationEditing: state.modal.invitations.invitationEditing,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(ModalContainer);
