import React from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import ActivityContent from './Content/Activity';
import HoursContent from './Content/Hours';
import CommentsContent from './Content/Comments';
import AttachmentContent from './Content/Attachments';
import { onlyTask } from '../../../../../selectors/tasks';

const styles = {
  mainContent: {
  },
};

const SubComponent = ({
  classes, openLogs, openHours, openComments, openAttachment, task,
}) => {
  return (
    <div className={classes.mainContent}>
      { openLogs && <ActivityContent task={task} />}
      { openHours && <HoursContent /> }
      { openComments && <CommentsContent task={task} /> }
      { openAttachment && <AttachmentContent /> }
    </div>
  );
};

SubComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  task: PropTypes.object.isRequired,
  openLogs: PropTypes.bool.isRequired,
  openHours: PropTypes.bool.isRequired,
  openComments: PropTypes.bool.isRequired,
  openAttachment: PropTypes.bool.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    openLogs: state.subTask.openLogs,
    openHours: state.subTask.openReportedHours,
    openComments: state.subTask.openComments,
    openAttachment: state.subTask.openAttachments,
    task: onlyTask(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(SubComponent));
