import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import { withStyles } from '@material-ui/core/styles';
import Tooltip from '@material-ui/core/Tooltip';
import Dialog from '../../../lib/Dialog';
import CircularProgress from '../../../lib/CircularProgress';

import { fetchGetArchivedTasks, fetchGetTasks } from '../../../../actions/api/tasks';
import { fetchGetColumns } from '../../../../actions/api/columns';
import { archiveSearch } from '../../../../actions/project';
import { allBoardsFromModal } from '../../../../selectors/boards';
import { toggleManageArchiveModal, setFetchingArchived } from '../../../../actions/modals/archived';

import Board from './Board';

const styles = {
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
  search: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end',
  },
  titleText: {
    color: '#2a3d41',
    fontSize: 15,
    fontWeight: 'bold',
  },
  icon: {
    alignSelf: 'center',
    color: '#2a3d41',
    '&:hover': {
      color: '#ca3921',
    },
    float: 'left',
  },
  container: {
    width: '700px',
    alignSelf: 'center',
    padding: '30px',
    marginTop: '20px',
  },
  titles: {
    display: 'grid',
    gridTemplateColumns: '2fr 2fr 1fr',
    marginBottom: '10px',
    marginTop: '20px',
  },
};

class Archived extends Component {
  constructor(props) {
    super(props);
    this.handleClose = this.handleClose.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  componentWillMount() {
    this.props.setFetchingArchived(true);
    this.props.boards.forEach((b) => {
      this.props.fetchGetColumns(b.id)
        .then(() => this.props.fetchGetArchivedTasks(b.id))
        .then(() => this.props.setFetchingArchived(false));
    });
  }

  handleClose() {
    const { selectedBoardId } = this.props;
    this.props.fetchGetTasks(selectedBoardId);
    this.props.toggleManageArchiveModal();
  }

  handleChange(text) {
    this.props.archiveSearch({ text });
  }

  render() {
    const { isFetching, boards, classes } = this.props;
    const title = 'Archived';
    if (isFetching) {
      return (
        <Dialog
          open
          title={title}
          onClose={this.handleClose}
          clickClose={this.handleClose}
          onExited={this.handleClose}
        >
          <div style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            minWidth: 200,
          }}
          >
            <CircularProgress style={{ position: 'unset' }} />
          </div>
        </Dialog>
      );
    }
    return (
      <Dialog
        open
        title={title}
        onClose={this.handleClose}
        clickClose={this.handleClose}
        onExited={this.handleClose}
      >
        <div style={{ border: '1px solid #5f7a80 ', borderRadius: '5px', display: 'flex' }}>
          <Tooltip placement="top-start" title="Search">
            <SearchIcon
              className={classes.icon}
            />
          </Tooltip>
          <InputBase
            onClick={e => e.stopPropagation()}
            onChange={e => this.handleChange(e.target.value)}
            type="search"
            placeholder="Search…"
            style={{ width: '100%', marginRight: 5 }}
          />
        </div>
        <div style={{ minWidth: 500 }}>
          {boards.map(b => (
            <Board key={b.id} board={b} />
          ))}
        </div>
      </Dialog>
    );
  }
}

Archived.propTypes = {
  isFetching: PropTypes.bool.isRequired,
  toggleManageArchiveModal: PropTypes.func.isRequired,
  fetchGetArchivedTasks: PropTypes.func.isRequired,
  fetchGetColumns: PropTypes.func.isRequired,
  fetchGetTasks: PropTypes.func.isRequired,
  setFetchingArchived: PropTypes.func.isRequired,
  boards: PropTypes.array.isRequired,
  classes: PropTypes.any.isRequired,
  archiveSearch: PropTypes.func.isRequired,
  selectedBoardId: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    isFetching: state.modal.archived.isFetching,
    boards: allBoardsFromModal(ownProps, state),
    selectedBoardId: state.user.selectedBoardId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleManageArchiveModal,
    fetchGetArchivedTasks,
    fetchGetColumns,
    fetchGetTasks,
    setFetchingArchived,
    archiveSearch,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Archived));
