import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';

import Dialog from '../../../lib/Dialog';
import Form from '../../../lib/Form';
import TextField from '../../../lib/TextField';
import PrimaryButton from '../../../lib/Button/PrimaryButton';

import { toggleSendInvitationForm } from '../../../../actions/modals/invitations';
import { fetchCreateInvitation } from '../../../../actions/api/invitations';
import { notifySuccess, notifyError } from '../../../../actions/notification';

class InvitationModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      role: '',
    };
    this.handleClose = this.handleClose.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.emailChange = this.emailChange.bind(this);
  }

  roleChange = (event) => {
    this.setState({ role: event.target.value });
  };

  emailChange(value) {
    this.setState({ email: value });
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    const { email, role } = this.state;
    if (email === this.props.currentUserEmail) {
      this.props.notifyError('Cannot invite yourself');
    } else if (email === '') {
      this.props.notifyError('Email field cannot be empty');
    } else if (role === '') {
      this.props.notifyError('Role field cannot be empty');
    } else {
      this.props.fetchCreateInvitation(this.props.projectId, { email, role })
        .then((response) => {
          if (response.ok) {
            this.props.notifySuccess('Your invitation was sent succesfully');
            this.props.toggleSendInvitationForm();
          }
        });
    }
  }

  handleClose() {
    this.props.toggleSendInvitationForm();
  }

  render() {
    const { isFetching } = this.props;
    const { email } = this.state;
    const title = 'Send Invitation';
    const footer = (
      <div>
        <PrimaryButton
          onClick={this.handleSubmit}
          loading={isFetching}
        >

          Send Invitation
        </PrimaryButton>
      </div>
    );
    return (
      <Dialog
        open
        title={title}
        footer={footer}
        onClose={this.handleClose}
        clickClose={this.handleClose}
        onExited={this.handleClose}
      >
        <Form
          onSubmit={this.handleSubmit}
        >
          <TextField
            onChange={this.emailChange}
            type="email"
            name={email}
            fullWidth
            label="Email"
            required
            id="name"
            autoFocus
          />
          <FormControl style={{ minWidth: 120 }}>
            <InputLabel htmlFor="role">Role</InputLabel>
            <Select
              value={this.state.role}
              onChange={this.roleChange}
              input={<Input id="role" />}
            >
              <MenuItem value="viewer">Viewer</MenuItem>
              <MenuItem value="developer">Developer</MenuItem>
              <MenuItem value="manager">Manager</MenuItem>
            </Select>
          </FormControl>
        </Form>
      </Dialog>
    );
  }
}

InvitationModal.propTypes = {
  isFetching: PropTypes.bool.isRequired,
  fetchCreateInvitation: PropTypes.func.isRequired,
  toggleSendInvitationForm: PropTypes.func.isRequired,
  notifySuccess: PropTypes.any.isRequired,
  notifyError: PropTypes.any.isRequired,
  currentUserEmail: PropTypes.string.isRequired,
  projectId: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    showInvitationForm: state.user.invitationForm,
    isFetching: state.app.isFetching,
    currentUserEmail: state.auth.uid,
    projectId: state.modal.invitations.selectedProject,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchCreateInvitation,
    toggleSendInvitationForm,
    notifySuccess,
    notifyError,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(InvitationModal);
