import {
  TOGGLE_EDIT_MODAL_COLUMN,
  TOGGLE_DELETE_MODAL_COLUMN,
} from '../../constants/modals/columns';

export const toggleEditColumnForm = id => ({
  type: TOGGLE_EDIT_MODAL_COLUMN,
  payload: {
    body: {
      id,
    },
  },
});

export const toggleDeleteColumnForm = id => ({
  type: TOGGLE_DELETE_MODAL_COLUMN,
  payload: {
    body: {
      id,
    },
  },
});
