import {
  MODEL_UPDATE_BOARD_ORDER,
  MODEL_UPDATE_COLUMN_ORDER,
  MODEL_UPDATE_TASK_ORDER,
  MODEL_UPDATE_TASK_ORDER_WITH_COLUMN,
} from '../constants/order';

export const modelUpdateBoardOrder = body => ({
  type: MODEL_UPDATE_BOARD_ORDER,
  payload: {
    body,
  },
});

export const modelUpdateColumnOrder = body => ({
  type: MODEL_UPDATE_COLUMN_ORDER,
  payload: {
    body,
  },
});

export const modelUpdateTaskOrder = body => ({
  type: MODEL_UPDATE_TASK_ORDER,
  payload: {
    body,
  },
});

export const modelUpdateTaskOrderWithColumn = body => ({
  type: MODEL_UPDATE_TASK_ORDER_WITH_COLUMN,
  payload: {
    body,
  },
});
