import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import InfiniteScroll from 'react-infinite-scroller';

import moment from 'moment';

import Typography from '@material-ui/core/Typography';

import MaterialCircularProgress from '@material-ui/core/CircularProgress';

import { fetchGetLogsTask } from '../../../../../../../actions/api/tasks';
import BlankProfile from '../../../../../../../assets/blankprofile.png';

import { taskName } from '../../../../../../../selectors/tasks';

/* eslint-disable no-unused-vars, max-len, react/no-unused-prop-types */
const styles = theme => ({
  heading: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(19),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 4,
  },
  log: {
    borderTop: '1px solid #ccc',
    width: 510,
    margin: 0,
    padding: 20,
  },
  info: {
    margin: 0,
  },
  loading: {
    textAlign: 'center',
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(12),
    marginTop: 10,
  },
  rightBlock: {
    display: 'inline-block',
    float: 'right',
  },
  leftBlock: {
    display: 'inline-block',
    color: '#2a3d41',
  },
  img: {
    display: 'inline-block',
  },
  name: {
    fontSize: theme.typography.pxToRem(14),
    fontWeight: 'bold',
    display: 'inline-block',
    marginTop: 3,
    marginLeft: 10,
  },
  description: {
    fontSize: theme.typography.pxToRem(12),
  },
  date: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(11),
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
  },
  load: {
    position: 'relative',
    left: '50%',
    bottom: '50%',
    color: '#ea5941',
    zIndex: 1,
  },
});

const defaultState = {
  logs: [],
  hasMoreItems: true,
  loading: true,
  oldDate: moment(),
  isFetching: false,
};

class Comments extends Component {
  constructor(props) {
    super(props);
    this.state = { ...defaultState };
    this.fetchPage = this.fetchPage.bind(this);
  }

  componentWillMount() {
    this.fetchPage(1);
  }

  componentWillReceiveProps() {
    this.setState({ ...defaultState });
    this.fetchPage(1);
  }

  fetchPage(n) {
    if (n !== 1) {
      this.setState({ isFetching: true });
    }
    this.props.fetchGetLogsTask(this.props.taskId, n).then((logs) => {
      this.setState((prevState) => {
        const newLogs = JSON.parse(JSON.stringify(prevState.logs));
        const hasMoreItems = Boolean(logs.body.length);
        const loading = prevState.loading ? !prevState.loading : prevState.loading;
        const { oldDate } = prevState;
        let tmpDate = prevState.oldDate;
        logs.body.forEach((l) => {
          const logDate = moment(l.created_at);
          if (logDate.isBefore(oldDate)) {
            newLogs.push(l);
          }
          if (logDate.isBefore(tmpDate)) {
            tmpDate = logDate;
          }
        });
        return {
          logs: newLogs, hasMoreItems, loading, oldDate: tmpDate,
        };
      });
      this.setState({ isFetching: false });
    }).catch(e => this.setState({ isFetching: false }));
  }

  render() {
    const { classes } = this.props;
    const loader = <Typography key="loading" className={classes.loading}>Loading...</Typography>;
    const items = [];
    if (this.state.loading) {
      return (
        <MaterialCircularProgress
          size={50}
          color="primary"
          className={classes.load}
        />
      );
    }
    this.state.logs.forEach((l) => {
      let name = '';
      let msg = '';
      let img = '';
      items.push(
        <Fragment key={l.created_at}>
          <div className={classes.log}>
            <div className={classes.info}>
              {
                l.message.forEach((m, i) => {
                  if (i === 0) {
                    name += m.name;
                    img += m.image;
                  } else {
                    const str = typeof m === 'object' ? `<b>${m.name}</b>` : m;
                    msg += str;
                    msg += ' ';
                  }
                })
              }
              <div className={classes.leftBlock}>
                <div style={{ display: 'flex', flexDirection: 'row', width: '100%' }}>
                  {(!img || img === 'null')
                  && (
                    <img alt="" className={classes.customIcon} src={BlankProfile} />
                  )}
                  {(img && img !== 'null')
                  && (
                    <img alt="" className={classes.customIcon} src={img} />
                  )}
                  <Typography className={classes.name}>{`${name}`}</Typography>
                </div>
                <Typography className={classes.description} dangerouslySetInnerHTML={{ __html: msg }} />
              </div>
              <div className={classes.rightBlock}>
                <Typography className={classes.date}>{moment(l.created_at).fromNow()}</Typography>
              </div>
            </div>
          </div>
        </Fragment>,
      );
    });
    return (
      <div style={{ overflow: 'scroll' }}>
        {
          this.state.logs.length > 0
          && (
            <InfiniteScroll
              pageStart={1}
              loadMore={this.fetchPage}
              hasMore={!this.state.isFetching && this.state.hasMoreItems}
              threshold={10}
              useWindow={false}
            >
              <div>
                {items}
              </div>
              { this.state.isFetching && loader }
            </InfiniteScroll>
          )
          }
        {
          !this.state.logs.length
          && <Typography style={{ margin: 10 }}>There are no notifications.</Typography>
          }
      </div>
    );
  }
}

Comments.propTypes = {
  classes: PropTypes.object.isRequired,
  fetchGetLogsTask: PropTypes.func.isRequired,
  taskId: PropTypes.string.isRequired,
  taskName: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    taskName: taskName(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchGetLogsTask,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Comments));
