import React, { Component, Fragment } from 'react';

import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlined';

import InputBase from '@material-ui/core/InputBase';


import EditIcon from '@material-ui/icons/Edit';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import SearchIcon from '@material-ui/icons/Search';
import ManageLabels from '@material-ui/icons/List';
import Divider from '@material-ui/core/Divider';

import Tooltip from '@material-ui/core/Tooltip';
import ExpansionPanel from '../lib/ExpansionPanel';
import AppLogo from '../app/AppLogo/index';

import { notifySuccess } from '../../actions/notification';

import { selectBoard, toggleBoardSearch } from '../../actions/user';
import { toggleCreateColumnForm } from '../../actions/board';
import { taskSearch } from '../../actions/task';

import { fetchExportBoard } from '../../actions/api/export';

import { toggleEditBoardForm, toggleDeleteBoardForm } from '../../actions/modals/boards';
import { toggleShowBoardLabels } from '../../actions/modals/labels';
import { getProjectRole } from '../../selectors/projects';
import language from '../../constants/language';
import BoardComponent from '../board';
import DragContext from './DragContext';



/* eslint-disable no-return-assign */
const styles = theme => ({
  root: {
    marginTop: 10,
    marginBottom: 0,
    paddingLeft: 0,
  },
  boarName: {
    paddingLeft: '6px',
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(16),
    fontWeight: 'bold',
    marginTop: 2,
  },
  grow: {
    flexGrow: 1,
  },
  iconDivider: {
    width: 1,
    height: 14,
    paddingTop: 8,
  },
  icon: {
    height: 22,
    marginLeft: 4,
    marginRight: 4,
    color: '#2a3d41',
    '&:hover': {
      color: '#ca3921',
    },
  },
  card: {
    maxHeight: '42px',
    padding: '0 0 0 0',
  },
  summary: {
    alignItems: 'center',
    display: 'flex',
    flexDirection: 'row',
    flex: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  search: {
    display: 'flex',
    alignItems: 'center',
  },
  inputInput: {
    width: 0,
    transition: 'width 0.5s',
  },
  inputActive: {
    width: 200,
    transition: 'width 0.5s',
  },
  tabs: {
    width: '100%',
  },
});



class BoardListItem extends Component {
  constructor(props) {
    super(props);
    this.searchInputRef = React.createRef();
    this.handleSearchbar = this.handleSearchbar.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleEditBoard = this.handleEditBoard.bind(this);
    this.handleDownloadBoard = this.handleDownloadBoard.bind(this);
    this.handleDeleteBoard = this.handleDeleteBoard.bind(this);
    this.handleChangeBoard = this.handleChangeBoard.bind(this);
    this.handleManangeLabels = this.handleManangeLabels.bind(this);
    this.handleStopPropagation = this.handleStopPropagation.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.state = {
      show: false,
      busy: false
    };
  }

  handleClick (event) {
    this.setState({setAnchorEl: event.currentTarget});
  };

  async componentDidUpdate() {
    this.openPromise();
  }

  handleSearchbar(event) {
    if (event) event.stopPropagation();
    const { board, searchActive } = this.props;
    if (this.props.selectedBoardId !== board.id) {
      this.props.selectBoard(board.id);
      this.props.toggleBoardSearch(true);
    } else {
      this.props.toggleBoardSearch();
    }
    if (!searchActive && this.searchInputRef.focus) this.searchInputRef.focus();
  }

  handleChange(text) {
    if (!this.props.selectedBoardId) return;
    this.props.taskSearch({ text });
  }

  handleEditBoard(event) {
    if (event) event.stopPropagation();
    const { board } = this.props;
    this.props.taskSearch({});
    this.props.toggleEditBoardForm(board.id);
  }

  handleDownloadBoard(event) {
    if (event) event.stopPropagation();
    this.props.fetchExportBoard(this.props.board.id).then(response => {
      if (response.status === 200) {
        const element = document.createElement("a");
        const json = JSON.stringify(response.body, null, "\t");
        const file = new Blob([json], {type: 'application/json'});
        element.href = URL.createObjectURL(file);
        element.download = "export_board.json";
        document.body.appendChild(element);
        element.click();
      }
    })
  }


  handleStopPropagation(event) {
    if (event) event.stopPropagation();
  }

  handleDeleteBoard(event) {
    if (event) event.stopPropagation();
    const { board } = this.props;
    this.props.taskSearch({});
    this.props.toggleDeleteBoardForm(board.id);
  }

  handleManangeLabels(event) {
    if (event) event.stopPropagation();
    const { board } = this.props;
    this.props.taskSearch({});
    this.props.toggleShowBoardLabels(board.id);
  }

  async handleChangeBoard() {
    const { board, changeShowId } = this.props;
    const { show } = this.state;
    if (show) {
      await this.closePromise();
    }
    this.props.selectBoard(board.id);
    this.props.taskSearch({});
    this.props.toggleBoardSearch(false);
    changeShowId(board.id);
  }

  closePromise() {
    return new Promise(async (resolve) => {
      await this.setState({ show: false, busy: true });
      setTimeout(() => {
        resolve(0);
      }, 1000);
    });
  }

  openPromise() {
    const { board, selectedBoardId } = this.props;
    const { show, busy } = this.state;
    return new Promise((resolve) => {
      setTimeout(() => {
        if (((selectedBoardId === board.id) !== show) && !busy) {
          this.setState({ show: selectedBoardId === board.id });
        }
        resolve(0);
      }, 100);
    });
  }

  render() {
    const {
      classes, board, selectedBoardId, index, projectRole, searchActive, selectedLanguage,
    } = this.props;
    const { show } = this.state;
    const createRoles = ['owner', 'manager'];
    return (
      <DragContext draggableId={board.id} index={index} position={board.position}>
        <div className={classes.root}>
          <ExpansionPanel
            square
            classes={{ materialRoot: classes.card }}
            expanded={show}
            onChange={this.handleChangeBoard}
            summary={(
              <div className={classes.summary}>
                <AppLogo style={{ width: '24px', height: '24px' }} />
                <Typography className={classes.boarName}>{board.name}</Typography>
                <div className={classes.grow} />
                <div className={classes.search}>
                  <Tooltip placement="top-start" title={language[selectedLanguage].project.downloadBoardInfo}>
                  <div>
                    <CloudDownloadIcon
                      onClick={this.handleDownloadBoard}
                      className={classes.icon}
                    />
                  </div>
                  </Tooltip>
                  <Divider className={classes.iconDivider} />
                  <Tooltip placement="top-start" title={language[selectedLanguage].project.search}>
                  <div>
                    <SearchIcon
                      onClick={this.handleSearchbar}
                      className={classes.icon}
                    />
                  </div>
                  </Tooltip>
                  {selectedBoardId === board.id
                    && (
                      <InputBase
                        inputRef={input => this.searchInputRef = input}
                        className={searchActive ? classes.inputActive : classes.inputInput}
                        onClick={e => e.stopPropagation()}
                        onChange={e => this.handleChange(e.target.value)}
                        type="search"
                        placeholder="Search…"
                        autoFocus={searchActive}
                      />
                    )
                  }
                </div>
                {
                  createRoles.includes(projectRole)
                  && (
                    <Fragment>
                      <Divider className={classes.iconDivider} />
                      <Tooltip placement="top-start" title={language[selectedLanguage].project.editLabels}>
                        <ManageLabels
                          onClick={this.handleManangeLabels}
                          className={classes.icon}
                        />
                      </Tooltip>
                      <Divider className={classes.iconDivider} />
                      <Tooltip placement="top-start" title={language[selectedLanguage].project.editName}>
                        <EditIcon
                          onClick={this.handleEditBoard}
                          className={classes.icon}
                        />
                      </Tooltip>
                      <Divider className={classes.iconDivider} />
                      <Tooltip placement="top-start" title={language[selectedLanguage].project.deleteBoard}>
                        <DeleteOutlinedIcon
                          onClick={this.handleDeleteBoard}
                          className={classes.icon}
                        />
                      </Tooltip>
                    </Fragment>
                  )
                }
                <div style={{ flex: '0 0 0.5%' }} />
              </div>
            )}
          >
            <div className={classes.tabs}>

              { selectedBoardId === board.id
                ? <BoardComponent />: <div style={{ height: 1 }} />
              }
            </div>
          </ExpansionPanel>
        </div>
      </DragContext>
    );
  }
}
BoardListItem.propTypes = {
  board: PropTypes.any.isRequired,
  classes: PropTypes.object.isRequired,
  selectedBoardId: PropTypes.string.isRequired,
  selectBoard: PropTypes.func.isRequired,
  searchActive: PropTypes.bool.isRequired,
  toggleEditBoardForm: PropTypes.func.isRequired,
  toggleDeleteBoardForm: PropTypes.func.isRequired,
  toggleShowBoardLabels: PropTypes.func.isRequired,
  fetchExportBoard: PropTypes.func.isRequired,
  toggleBoardSearch: PropTypes.func.isRequired,
  index: PropTypes.number.isRequired,
  projectRole: PropTypes.string.isRequired,
  taskSearch: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  changeShowId: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    selectedLanguage: state.user.language,
    selectedBoardId: state.user.selectedBoardId,
    searchActive: state.user.searchActive,
    columnCreating: state.board.columnCreating,
    projectRole: getProjectRole(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleShowBoardLabels,
    toggleCreateColumnForm,
    toggleEditBoardForm,
    fetchExportBoard,
    toggleDeleteBoardForm,
    selectBoard,
    taskSearch,
    notifySuccess,
    toggleBoardSearch,
  }, dispatch);
};

export default withRouter(
  connect(
    mapStateToProps, mapDispatchToProps,
  )(withStyles(styles)(BoardListItem)),
);
