import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import shortid from 'shortid';
import { withStyles } from '@material-ui/core/styles';
import EditIcon from '@material-ui/icons/Edit';
import RejectIcon from '@material-ui/icons/Clear';
import Typography from '@material-ui/core/Typography';
import PopOver from '@material-ui/core/Popover';
import moment from 'moment';
import Linkify from 'react-linkify';
import TextField from '@material-ui/core/TextField';
import { truncateString } from '../../../../../../../utils';
import PrimaryButton from '../../../../../../lib/Button/PrimaryButton';
import Form from '../../../../../../lib/Form';
import { fetchDeleteComment, fetchUpdateComment } from '../../../../../../../actions/api/comments';
import { notifySuccess } from '../../../../../../../actions/notification';
import BlankProfile from '../../../../../../../assets/blankprofile.png';

const styles = {
  body: {
    alignSelf: 'start',
    flex: 1,
    margin: '10px 5px',
  },
  textContainer: {
    border: '2px solid #dedede',
    backgroundColor: '#f1f1f1',
    borderRadius: '3px',
    padding: '5px',
    margin: '10px 0px',
    display: 'flex',
    flexDirection: 'column',
  },
  textField: {
    backgroundColor: 'white',
    borderColor: 'black',
    flex: 1,
    width: '100%',
  },
  input: {
    flex: 1,
    backgroundColor: 'white',
    display: 'flex',
  },
  icon: {
    marginRight: 5,
    color: '#ccc',
    display: 'flex',
    alignItems: 'center',
  },
  commentName: {
    flex: 1,
    display: 'flex',
    alignItems: 'center',
  },
  edit: {
    color: '#b0b5b7',
    cursor: 'pointer',
    '&:hover': {
      color: '#828d92',
    },
  },
  reject: {
    color: '#e53935',
    cursor: 'pointer',
    '&:hover': {
      color: '#ff6f60',
    },
  },
  nameText: {
    fontSize: '12px',
    fontWeight: 'bold',
    color: '#2a3d41',
  },
  date: {
    fontSize: '10px',
    textAlign: 'right',
    color: '#2a3d41',
    flex: 1,
  },
  header: {
    flex: 1,
    display: 'flex',
    alignItems: 'center',
  },
  img: {
    display: 'inline-block',
  },
  customIcon: {
    width: '24px',
    'border-radius': '50%',
  },
  button: {
    boxShadow: 'unset',
    marginRight: 10,
    color: 'white',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  popover: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    margin: 10,
  },
  popoverButtons: {
    marginTop: 10,
    display: 'flex',
    justifyContent: 'space-between',
  },
  notchedOutline: {
    borderColor: '#b0b5b7 !important',
  },
  editContent: {
    margin: '10px 0px',
  },
  editHeader: {
    padding: 7,
    flex: 1,
    display: 'flex',
    alignItems: 'center',
  },
};

class Comments extends Component {
  constructor(props) {
    super(props);
    this.state = {
      edit: '',
      text: '',
      isFetching: false,
      anchorEl: null,
    };
    this.editComment = this.editComment.bind(this);
    this.deleteComment = this.deleteComment.bind(this);
    this.commentChange = this.commentChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleDeleteClose = this.handleDeleteClose.bind(this);
    this.handleDeleteOpen = this.handleDeleteOpen.bind(this);
    this.cancelSubmit = this.cancelSubmit.bind(this);
  }

  editComment(id, text) {
    this.setState({ edit: id, text });
  }

  commentChange(value) {
    this.setState({ text: value });
  }

  deleteComment(id) {
    this.setState({ isFetching: true });
    this.handleDeleteClose();
    this.props.fetchDeleteComment(id)
      .then((response) => {
        this.setState({ isFetching: false });
        if (response.ok) {
          this.props.notifySuccess('The comment was removed successfully');
        }
      });
  }

  handleDeleteOpen(event) {
    if (event) event.preventDefault();
    this.setState({ anchorEl: event.currentTarget });
  }

  handleDeleteClose() {
    this.setState({ anchorEl: null });
  }

  handleSubmit(event) {
    if (event) event.preventDefault();
    const { text, edit } = this.state;
    this.setState({ isFetching: true });
    this.props.fetchUpdateComment(edit, { text })
      .then((response) => {
        this.setState({ isFetching: false });
        if (response.ok) {
          this.setState({ edit: '', text: '' });
          this.props.notifySuccess('The comment was updated successfully');
        }
      });
  }

  cancelSubmit(event) {
    if (event) event.preventDefault();
    this.setState({ edit: '', text: '' });
  }

  render() {
    const {
      classes,
      comments,
      role,
      currentUserEmail,
    } = this.props;
    const {
      edit,
      text,
      isFetching,
      anchorEl,
    } = this.state;
    const componentDecorator = (href, linkText, key) => (
      <a href={href} key={key} target="_blank" rel="noopener noreferrer">
        {linkText}
      </a>
    );
    return (
      <div>
        <div>
          {
            comments.map((comment) => {
              if (edit === comment.id) {
                return (
                  <Form key={comment.id} className={classes.editContent}>
                    <div className={classes.editHeader}>
                      <div className={classes.icon}>
                        {(!comment.user.image || comment.user.image === 'null')
                          && (
                            <img alt="" className={classes.customIcon} src={BlankProfile} />
                          )}
                        {(comment.user.image && comment.user.image !== 'null')
                          && (
                            <img alt="" className={classes.customIcon} src={comment.user.image} />
                          )}
                      </div>
                      <Typography className={classes.nameText}>
                        {comment.user.name}
                      </Typography>
                    </div>
                    <div className={classes.input}>
                      <TextField
                        autoFocus
                        value={text}
                        onChange={e => this.commentChange(e.target.value)}
                        multiline
                        rowsMax="3"
                        className={classes.textField}
                        variant="outlined"
                        InputProps={{
                          classes: {
                            notchedOutline: classes.notchedOutline,
                          },
                        }}
                      />
                    </div>
                    <div style={{ display: 'flex', alignSelf: 'flex-end', margin: '5px 0px' }}>
                      <PrimaryButton
                        type="submit"
                        loading={isFetching}
                        onClick={this.handleSubmit}
                        className={classes.button}
                      >

                        Edit Comment
                      </PrimaryButton>
                      <PrimaryButton
                        type="submit"
                        onClick={this.cancelSubmit}
                        className={classes.button}
                      >

                        Cancel
                      </PrimaryButton>
                    </div>
                  </Form>
                );
              }
              return (
                <div key={comment.id} className={classes.textContainer}>
                  <div className={classes.header}>
                    <div className={classes.icon}>
                      {(!comment.user.image || comment.user.image === 'null')
                        && (
                          <img alt="" className={classes.customIcon} src={BlankProfile} />
                        )}
                      {(comment.user.image && comment.user.image !== 'null')
                        && (
                          <img alt="" className={classes.customIcon} src={comment.user.image} />
                        )}
                    </div>
                    <Typography className={classes.nameText}>
                      {truncateString(comment.user.name, 50)}
                    </Typography>
                    <Typography className={classes.date}>
                      {moment(comment.updated_at).fromNow()}
                    </Typography>
                  </div>
                  <div className={classes.body}>
                    {
                      comment.text.split('\n').map((paragraph) => {
                        return (
                          <Typography key={shortid.generate()}>
                            <Linkify componentDecorator={componentDecorator}>
                              {paragraph}
                            </Linkify>
                          </Typography>
                        );
                      })
                    }
                  </div>
                  <div className={classes.commentName}>
                    <div style={{ flexGrow: 1 }} />
                    {(currentUserEmail === comment.user.email)
                      && (
                        <EditIcon
                          onClick={() => this.editComment(comment.id, comment.text)}
                          className={classes.edit}
                        />
                      )
                    }
                    {((['owner', 'manager'].includes(role)) || (currentUserEmail === comment.user.email))
                      && (
                        <RejectIcon
                          onClick={this.handleDeleteOpen}
                          className={classes.reject}
                        />
                      )
                    }
                  </div>
                  {
                    anchorEl !== null
                    && (
                      <PopOver
                        anchorEl={anchorEl}
                        open={Boolean(anchorEl)}
                        onClose={this.handleDeleteClose}
                        anchorOrigin={{ horizontal: 'left', vertical: 'top' }}
                        transformOrigin={{ vertical: 'bottom', horizontal: 'left' }}
                      >
                        <div className={classes.popover}>
                          <Typography className={classes.nameText}>

                            Are you sure?
                          </Typography>
                          <div className={classes.popoverButtons}>
                            <PrimaryButton
                              size="small"
                              onClick={() => this.deleteComment(comment.id)}
                            >

                              DELETE
                            </PrimaryButton>
                          </div>
                        </div>
                      </PopOver>
                    )
                  }
                </div>
              );
            })
          }
        </div>
      </div>
    );
  }
}

Comments.propTypes = {
  classes: PropTypes.object.isRequired,
  comments: PropTypes.array.isRequired,
  role: PropTypes.string.isRequired,
  fetchDeleteComment: PropTypes.func.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  fetchUpdateComment: PropTypes.func.isRequired,
  currentUserEmail: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    currentUserEmail: state.auth.uid,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ fetchDeleteComment, fetchUpdateComment, notifySuccess }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Comments));
