import {
  FETCH_GET_ALL_COMMENTS,
  FETCH_CREATE_COMMENT,
  FETCH_UPDATE_COMMENT,
  FETCH_DELETE_COMMENT,
} from '../../constants/api/comments';

export const fetchTaskComments = id => ({
  type: FETCH_GET_ALL_COMMENTS,
  fetch: true,
  url: `/tasks/${id}/comments/`,
  params: {
    method: 'GET',
  },
});

export const fetchCreateComment = (id, body) => ({
  type: FETCH_CREATE_COMMENT,
  fetch: true,
  url: `/tasks/${id}/comments/`,
  params: {
    method: 'POST',
    body,
  },
});

export const fetchUpdateComment = (id, body) => ({
  type: FETCH_UPDATE_COMMENT,
  fetch: true,
  url: `/task_comments/${id}/`,
  params: {
    method: 'PUT',
    body,
  },
});

export const fetchDeleteComment = id => ({
  type: FETCH_DELETE_COMMENT,
  fetch: true,
  url: `/task_comments/${id}/`,
  params: {
    method: 'DELETE',
  },
});
