import {
  FETCH_GET_ALL_LABELS,
  FETCH_DELETE_LABEL,
  FETCH_CREATE_LABEL,
  FETCH_TASK_ADD_LABEL,
  FETCH_TASK_REMOVE_LABEL,
  FETCH_UPDATE_LABEL,
} from '../../constants/api/labels';

export const fetchBoardLabels = id => ({
  type: FETCH_GET_ALL_LABELS,
  fetch: true,
  url: `/boards/${id}/labels/`,
  params: {
    method: 'GET',
  },
});

export const fetchUpdateLabel = (id, body) => ({
  type: FETCH_UPDATE_LABEL,
  fetch: true,
  url: `/labels/${id}/`,
  params: {
    method: 'PUT',
    body,
  },
});

export const fetchDeleteLabel = id => ({
  type: FETCH_DELETE_LABEL,
  fetch: true,
  url: `/labels/${id}/`,
  params: {
    method: 'DELETE',
  },
});

export const fetchCreateLabel = body => ({
  type: FETCH_CREATE_LABEL,
  fetch: true,
  url: '/labels/',
  params: {
    method: 'POST',
    body,
  },
});

export const fetchAddLabelTask = (id, body) => ({
  type: FETCH_TASK_ADD_LABEL,
  fetch: true,
  url: `/labels/${id}/assign`,
  params: {
    method: 'POST',
    body,
  },
});

export const fetchRemoveLabelTask = (labelId, taskId) => ({
  type: FETCH_TASK_REMOVE_LABEL,
  fetch: true,
  url: `/tasks/${taskId}/labels/${labelId}/unassign`,
  params: {
    method: 'DELETE',
  },
});
