import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import MaterialExpansionPanel from '@material-ui/core/ExpansionPanel';
import MaterialExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MaterialExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Divider from '@material-ui/core/Divider';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

/* eslint-disable react/prop-types */
const style = {
  content: {
    margin: 0,
    padding: 0,
  },
  icon: {
    color: '#ea5941',
    width: '26px',
    height: '30px',
  },
  materialRoot: {
    minHeight: '0 !important',
    maxHeight: '48px',
  },
  marterialeExpanded: {
    margin: '10px !important',
  },
  materialPanel: {
    boxShadow: '0px 0px 0px 0px',
  },
};

const ExpansionPanel = ({
  children,
  headerColor,
  classes,
  summary,
  summaryStyle,
  ...props
}) => {
  return (
    <MaterialExpansionPanel {...props} className={classes.materialPanel}>
      <MaterialExpansionPanelSummary
        style={summaryStyle}
        expandIcon={<ExpandMoreIcon className={classes.icon} />}
        classes={{
          root: classes.materialRoot,
          content: classes.marterialeExpanded,
        }}
      >
        {summary}
      </MaterialExpansionPanelSummary>
      <Divider />
      <MaterialExpansionPanelDetails className={classes.content}>
        {children}
      </MaterialExpansionPanelDetails>
    </MaterialExpansionPanel>
  );
};

ExpansionPanel.propTypes = {
  children: PropTypes.any.isRequired,
  classes: PropTypes.any.isRequired,
  summary: PropTypes.any.isRequired,
  summaryStyle: PropTypes.any,
};

ExpansionPanel.defaultProps = {
  summaryStyle: {},
};

export default withStyles(style)(ExpansionPanel);
