import React from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import Labels from './Attributes/Labels';
import Checklist from './Attributes/Checklist';
import Deadline from './Attributes/Deadline';
import StartingDate from './Attributes/StartingDate';
import ExpectedHours from './Attributes/ExpectedHours';
import Priority from './Attributes/Priority';
import Description from './Attributes/Description';
import Members from './Attributes/Members';
import Roadblock from './Attributes/Roadblock';
import Commit from './Attributes/Commit';

import { onlyTask } from '../../../../../selectors/tasks';
import { onlyTaskLabels } from '../../../../../selectors/labels';
import { onlyTaskChecklist } from '../../../../../selectors/checklists';
import { onlyTaskMembers } from '../../../../../selectors/members';

const styles = {
  attributesWrapper: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: '15px',
  },
  item: {
    marginBottom: '15px',
  },
};

const MainComponent = ({
  classes, task, labels, checklist, openChecklist, members,
}) => {
  return (
    <div>

      { labels.length > 0 && (
        <div className={classes.item}>
          <Labels labels={labels} />
        </div>
      )
      }

      <div className={classes.attributesWrapper}>
        { task.starting_date !== null && <StartingDate starting_date={task.starting_date} />}
        { task.deadline !== null && <Deadline deadline={task.deadline} />}
        { task.estimated_hours > 0 && <ExpectedHours hours={task.estimated_hours} />}
        { task.priority !== 0 && <Priority priority={task.priority} />}
      </div>
      { task.commit.url !== null && (
      <div className={classes.item}>
        <Commit commit={task.commit} />
      </div>
      )}
      <div className={classes.item}>
        <Description description={task.description} />
      </div>
      { members.length > 0
        && (
          <div className={classes.item}>
            <Members members={members} />
          </div>
        )}
      <div className={classes.item}>
        <Roadblock roadblock={task.roadblock} />
      </div>
      { (checklist.length > 0 || openChecklist)
        && (
          <div className={classes.item}>
            <Checklist checklist={checklist} />
          </div>
        )}
    </div>
  );
};

MainComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  task: PropTypes.object.isRequired,
  labels: PropTypes.array.isRequired,
  checklist: PropTypes.array.isRequired,
  openChecklist: PropTypes.bool.isRequired,
  members: PropTypes.array.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    task: onlyTask(ownProps, state),
    checklist: onlyTaskChecklist(ownProps, state),
    labels: onlyTaskLabels(ownProps, state),
    openRoadblock: state.task.openRoadblock,
    openChecklist: state.task.openChecklist,
    members: onlyTaskMembers(ownProps, state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(MainComponent));
