import { createSelector } from 'redux-orm';

import orm from '../reducers/orm';

const getRef = obj => (obj != null ? obj.ref : null);

function getTasksbyColumn(session, columnId) {
  const tasks = session.Task
    .filter((o) => {
      return String(o.column_id) === columnId;
    })
    .orderBy(o => o.position)
    .toRefArray();
  return tasks;
}

function getColumnsByBoard(session, boardId) {
  const columns = session.Column
    .filter((o) => {
      return o.board_id === boardId;
    })
    .orderBy(o => o.position)
    .toRefArray()
    .map((col) => {
      return Object.assign({}, col, {
        tasks: getTasksbyColumn(session, col.id),
      });
    });
  return columns;
}

/*
returns the column which is going to be Edited in the
project/board route. (Edit Modal Form).
*/
export const oneColumnFormModal = createSelector(
  orm,
  (_, state) => state.orm,
  (_, state) => state.modal.columns.selectedColumn,
  (session, columnId) => {
    const column = session.Column.get(o => o.id === columnId).ref;
    return Object.assign({}, column, {
      tasks: getTasksbyColumn(session, column.id),
    });
  },
);

export const oneBoard = createSelector(
  orm,
  (_, state) => state.orm,
  (_, state) => state.user.selectedBoardId,
  (session, boardId) => {
    let board = session.Board
      .get(o => String(o.id) === boardId);
    board = getRef(board);
    if (board) {
      return Object.assign({}, board, {
        columns: getColumnsByBoard(session, board.id),
      });
    }
    return board;
  },
);

