export const boardName = createSelector(
  orm,
  (_, state) => state.orm,
  (_, state) => state.user.selectedBoardId,
  (session, boardId) => {
    const obj = session.Board
      .filter(o => String(o.id) === boardId)
      .toRefArray();
    return obj[0].name;
  },
);
