import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Typography from '@material-ui/core/Typography';
import PopOver from '@material-ui/core/Popover';
import { withStyles } from '@material-ui/core/styles';
import moment from 'moment';
import RejectIcon from '@material-ui/icons/Clear';
import Divider from '@material-ui/core/Divider';
import PrimaryButton from '../../../../../../lib/Button/PrimaryButton';
import BlankProfile from '../../../../../../../assets/blankprofile.png';
import { fetchUpdateTask } from '../../../../../../../actions/api/tasks';
import { fetchDeleteHour } from '../../../../../../../actions/api/hours';

const styles = {
  grid: {
    display: 'grid',
    gridTemplateColumns: '2fr 2fr 2fr 1fr',
    gridGap: 0,
    margin: '5px',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    marginRight: '3px',
    display: 'block',
    'border-radius': '50%',
  },
  text: {
    fontSize: '12px',
    fontWeight: 'bold',
    fontStyle: 'normal',
    fontStretch: 'normal',
    lineHeight: 'normal',
    letterSpacing: '0px',
    color: '#2a3d41',
    alignSelf: 'center',
  },
  reject: {
    color: '#2e4144',
    cursor: 'pointer',
    paddingRight: '2%',
    alignItems: 'center',
    float: 'right',
    '&:hover': {
      color: 'black',
    },
  },
  user: {
    display: 'flex',
    flex: 1,
  },
  icons: {
    display: 'flex',
    flex: 1,
    justifySelf: 'center',
  },
  popover: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    margin: 10,
  },
  popoverButtons: {
    marginTop: 10,
    display: 'flex',
    justifyContent: 'space-between',
  },
  nameText: {
    fontSize: '12px',
    fontWeight: 'bold',
    color: '#2a3d41',
  },
};

class HourItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mouse: false,
      anchorEl: null,
    };
    this.handleMouseEnter = this.handleMouseEnter.bind(this);
    this.handleMouseLeave = this.handleMouseLeave.bind(this);
    this.deleteHour = this.deleteHour.bind(this);
    this.handleDeleteOpen = this.handleDeleteOpen.bind(this);
    this.handleDeleteClose = this.handleDeleteClose.bind(this);
  }

  handleMouseEnter() {
    this.setState({ mouse: true });
  }

  handleMouseLeave() {
    this.setState({ mouse: false });
  }

  deleteHour(id) {
    this.props.fetchDeleteHour(id);
  }

  handleDeleteOpen(event) {
    if (event) event.preventDefault();
    this.setState({ anchorEl: event.currentTarget });
  }

  handleDeleteClose() {
    this.setState({ anchorEl: null });
  }

  render() {
    const {
      classes,
      item,
      projectRole,
      currentUserEmail,
    } = this.props;
    const { mouse, anchorEl } = this.state;
    return (
      <div
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
        key={item.id}
      >
        <div className={classes.grid}>
          <div className={classes.user}>
            {(!item.user.image || item.user.image === 'null')
            && (
              <Fragment>
                <img alt="" className={classes.customIcon} src={BlankProfile} />
                <Typography className={classes.text}>
                  {item.user.name}
                </Typography>
              </Fragment>
            )}
            {(item.user.image && item.user.image !== 'null')
            && (
              <Fragment>
                <img alt="" className={classes.customIcon} src={item.user.image} />
                <Typography className={classes.text}>
                  {item.user.name}
                </Typography>
              </Fragment>
            )}
          </div>

          <Typography className={classes.text} style={{}}>
            {item.hours}
          </Typography>
          <Typography className={classes.text}>
            {moment(new Date(item.date.replace('-', '/').replace('-', '/'))).format('D MMM Y')}
          </Typography>
          <div className={classes.icons}>
            {(((['owner', 'manager'].includes(projectRole)) || (currentUserEmail === item.user.email)) && (mouse))
            && (
            <RejectIcon
              className={classes.reject}
              onClick={this.handleDeleteOpen}
            />
            )}
          </div>
        </div>
        <Divider />
        {
          anchorEl !== null
          && (
            <PopOver
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={this.handleDeleteClose}
              anchorOrigin={{ horizontal: 'left', vertical: 'top' }}
              transformOrigin={{ vertical: 'bottom', horizontal: 'left' }}
            >
              <div className={classes.popover}>
                <Typography className={classes.nameText}>

                  Are you sure?
                </Typography>
                <div className={classes.popoverButtons}>
                  <PrimaryButton
                    size="small"
                    onClick={() => this.deleteHour(item.id)}
                  >

                    DELETE
                  </PrimaryButton>
                </div>
              </div>
            </PopOver>
          )
        }
      </div>
    );
  }
}

HourItem.propTypes = {
  classes: PropTypes.object.isRequired,
  item: PropTypes.object.isRequired,
  fetchDeleteHour: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
  currentUserEmail: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    currentUserEmail: state.auth.uid,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateTask,
    fetchDeleteHour,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(HourItem));
