import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';

import AddIcon from '@material-ui/icons/AddCircle';
import Close from '@material-ui/icons/Close';

import { fetchRemoveLabelTask } from '../../../../../../../actions/api/labels';
import { taskOpenLabels } from '../../../../../../../actions/task';

import Label from '../../../../../../lib/TaskComponents/Label';
import LabelLogo from '../../../../../../lib/Logo/Label';

const styles = theme => ({
  title: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(15),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  icon: {
    fontSize: 20,
    color: '#2a3d41',
    padding: '0px 5px',
    '&:hover': {
      color: '#ca3921',
    },
    marginTop: '2.5px',
    width: 20,
  },
});

const colorOnHover = (event) => {
  const closeElement = event.target;
  closeElement.style.color = '#ea5941';
};

const colorMouseLeave = (event) => {
  const closeElement = event.target;
  closeElement.style.color = '#2e4144';
};

class Labels extends Component {
  constructor(props) {
    super(props);
    this.removeLabel = this.removeLabel.bind(this);
    this.addLabel = this.addLabel.bind(this);
  }

  removeLabel(labelId) {
    if (this.props.projectRole !== 'viewer') {
      this.props.fetchRemoveLabelTask(labelId, this.props.taskId);
    }
  }

  addLabel() {
    this.props.taskOpenLabels();
  }

  render() {
    const { labels, classes, projectRole } = this.props;
    return (
      <Fragment>
        <div style={{
          display: 'flex',
          flexDirection: 'row',
          width: '100%',
          marginBottom: 20,
        }}
        >
          <LabelLogo size="23px" style={{ paddingRight: 5 }} />
          <Typography className={classes.title}>Labels:</Typography>
          <div style={{ display: 'flex', flexWrap: 'wrap' }}>
            {
              labels.map((label) => {
                return (
                  <div style={{ display: 'flex', flexDirection: 'row' }}>
                    <Label
                      key={label.id}
                      label={label}
                    />
                    {(projectRole !== 'viewer')
                  && (
                    <Close
                      style={{ cursor: 'pointer', fontSize: 11, marginRight: 2 }}
                      onClick={() => this.removeLabel(label.id)}
                      onMouseEnter={colorOnHover}
                      onMouseLeave={colorMouseLeave}
                    />
                  )}
                  </div>
                );
              })
              }
            {(projectRole !== 'viewer')
            && <AddIcon className={classes.icon} onClick={this.addLabel} />
          }
          </div>
        </div>
      </Fragment>
    );
  }
}

Labels.propTypes = {
  classes: PropTypes.object.isRequired,
  taskId: PropTypes.string.isRequired,
  labels: PropTypes.array.isRequired,
  fetchRemoveLabelTask: PropTypes.func.isRequired,
  taskOpenLabels: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    taskId: state.board.selectedTask,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    taskOpenLabels,
    fetchRemoveLabelTask,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Labels));
