import React, { Component } from 'react';
import { Draggable } from 'react-beautiful-dnd';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import Dotdotdot from 'react-dotdotdot';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import TimeIcon from '@material-ui/icons/Timelapse';
import Tooltip from '@material-ui/core/Tooltip';
import AccountCircle from '@material-ui/icons/AccountCircle';
import DropdownComponent from './DropdownComponent';
import Labels from '../lib/Labels';
import Priority from '../lib/Priority';
import { getProjectRole } from '../../selectors/projects';
import { oneTask } from '../../selectors/tasks';
import { oneTaskMembers } from '../../selectors/members';
import language from '../../constants/language';

import {
  toggleTaskEditForm,
} from '../../actions/board';

/* eslint-disable jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions */
const styles = {
  container: {
    display: 'flex',
    borderBottom: '1px solid #d8d8d8',
    backgroundColor: 'white',
    padding: '10px 0px 10px 10px',
    minHeight: 75,
  },
  leftBox: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    maxWidth: '190px',
  },
  rightBox: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-end',
    maxWidth: '90px',
  },
  title: {
    width: '100%',
    fontSize: 14,
    fontWeight: 'bold',
    color: '#2a3d41',
    wordWrap: 'break-word',
    paddingLeft: '5',
  },
  workHours: {
    display: 'flex',
    alignItems: 'center',
    fontSize: 10,
    color: '#417505',
  },
  estimatedHours: {
    display: 'flex',
    alignItems: 'center',
    fontSize: 10,
    color: '2a3d41',
  },
  checklistProgress: {
    display: 'flex',
    alignItems: 'center',
    fontSize: 10,
    color: '2a3d41',
  },
  timeIcon: {
    fontSize: 11,
    marginLeft: 3,
  },
  priorityContainer: {
    display: 'flex',
  },
  usersAsigned: {
    display: 'flex',
    alignItems: 'center',
    fontSize: 10,
    color: '#2a3d41',
  },
  icon: {
    color: '#2a3d41',
    fontSize: 11,
    marginLeft: 3,
  },
};

function getNames(assigned) {
  const names = [];
  assigned.forEach((obj) => {
    if (obj.name !== null) {
      names.push(obj.name);
    } else {
      names.push(obj.email.split('@')[0].charAt(0).toUpperCase() + obj.email.split('@')[0].slice(1));
    }
  });
  return names.join(', ');
}

class TaskComponent extends Component {
  constructor(props) {
    super(props);
    this.handleEdit = this.handleEdit.bind(this);
  }

  componentWillMount() { }

  handleEdit(ev) {
    ev.stopPropagation();
    this.props.toggleTaskEditForm({ id: this.props.task.id, role: this.props.projectRole });
  }

  render() {
    const {
      task,
      taskAssigned,
      index,
      projectRole,
      classes,
      search,
      selectedLanguage,
      filter,
    } = this.props;
    if (task === null) {
      return <div />;
    }
    const roles = ['owner', 'manager', 'developer'];

    return (
      <Draggable
        draggableId={task.id}
        index={index}
        isDragDisabled={!roles.includes(projectRole) || search || filter}
      >
        {provided => (
          <div
            onClick={this.handleEdit}
            className={classes.container}
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
          >
            <div className={classes.leftBox}>
              <Labels labels={task.labels} />
              {(task.priority > 0)
                  && <Priority priority={task.priority} />
                }
              <Dotdotdot clamp={4}>
                <Tooltip disableTouchListener disableFocusListener title={task.name.length > 75 ? task.name : ''}>
                  <Typography
                    className={classes.title}
                  >
                    {task.name}
                  </Typography>
                </Tooltip>
              </Dotdotdot>
            </div>
            <div className={classes.rightBox}>
              {task.total_hours !== null && task.total_hours > 0
                && (
                  <Typography className={classes.workHours}>

                    {language[selectedLanguage].tasks.worked}&nbsp;
                    {Math.floor(task.total_hours)}

                    :
                    {((task.total_hours % 1) * 60).toString().length >= 2
                      && Math.round((task.total_hours % 1) * 60)}
                    {((task.total_hours % 1) * 60).toString().length < 2
                      && `0${Math.round(((task.total_hours % 1) * 60)).toString()}`}
                    <TimeIcon className={classes.timeIcon} />
                  </Typography>
                )
              }
              {task.estimated_hours !== null && task.estimated_hours > 0
                && (
                  <Typography className={classes.estimatedHours}>

                    {language[selectedLanguage].tasks.estimated}&nbsp;
                    {Math.floor(task.estimated_hours)}

                    :
                    {((task.estimated_hours % 1) * 60).toString().length >= 2
                      && Math.round((task.estimated_hours % 1) * 60)}
                    {((task.estimated_hours % 1) * 60).toString().length < 2
                      && `0${Math.round(((task.estimated_hours % 1) * 60)).toString()}`}
                    <TimeIcon className={classes.timeIcon} />
                  </Typography>
                )
              }
              {(task.total_checklist !== undefined
                && task.total_checklist !== null
                && task.total_checklist !== 0)
                && (
                  <Typography className={classes.checklistProgress}>
                    {language[selectedLanguage].tasks.checklistProgress}&nbsp;
                    {`${task.done_checklist}/${task.total_checklist}`}
                  </Typography>
                )
              }
              {
                task.assigned !== null && task.assigned.length > 0
                && (
                  <Tooltip placement="top" title={getNames(taskAssigned)}>
                    <Typography className={classes.usersAsigned}>

                      {language[selectedLanguage].tasks.user}&nbsp;
                      {taskAssigned.length}
                      <AccountCircle className={classes.icon} />
                    </Typography>

                  </Tooltip>
                )

              }
              <div style={{ flexGrow: 1 }} />
              {projectRole !== 'viewer'
                && (
                  <DropdownComponent task={task} />
                )
              }
            </div>
          </div>
        )}
      </Draggable>
    );
  }
}

TaskComponent.propTypes = {
  taskId: PropTypes.string.isRequired,
  task: PropTypes.object.isRequired,
  taskAssigned: PropTypes.array.isRequired,
  index: PropTypes.number.isRequired,
  projectRole: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired,
  toggleTaskEditForm: PropTypes.func.isRequired,
  search: PropTypes.string.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  filter: PropTypes.bool.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    selectedLanguage: state.user.language,
    projectRole: getProjectRole(ownProps, state),
    task: oneTask(ownProps, state),
    taskAssigned: oneTaskMembers(ownProps, state),
    search: state.task.search,
    filter: state.project.filter,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    toggleTaskEditForm,
  }, dispatch);
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(withStyles(styles)(TaskComponent)),
);
