import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import shortid from 'shortid';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import DescriptionIcon from '../../../../../../lib/Logo/Description';

import { taskOpenDescription } from '../../../../../../../actions/task';
import { fetchUpdateTask } from '../../../../../../../actions/api/tasks';

/* eslint-disable jsx-a11y/interactive-supports-focus */
const styles = theme => ({
  title: {
    display: 'flex',
    marginBottom: 3,
  },
  titleText: {
    color: '#2a3d41',
    fontSize: theme.typography.pxToRem(15),
    fontWeight: 'bold',
    margin: 0,
    marginTop: 2,
    paddingRight: 5,
  },
  description: {
    fontSize: theme.typography.pxToRem(12),
    color: '#2a3d41',
    padding: 10,
    paddingLeft: 28,
  },
  redDescription: {
    fontSize: theme.typography.pxToRem(12),
    color: '#ea5941',
    padding: 10,
    paddingLeft: 28,
    cursor: 'pointer',
  },
  inputContainer: {
    display: 'flex',
    flexDirection: 'column',
    marginLeft: 28,
  },
  button: {
    boxShadow: 'unset',
    marginRight: 10,
    color: 'white',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
  },
  input: {
    fontSize: theme.typography.pxToRem(12),
    color: '#2a3d41',
  },
  notchedOutline: {
    borderColor: '#ea5941 !important',
  },
});

class Description extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: props.description || '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.openInput = this.openInput.bind(this);
    this.closeInput = this.closeInput.bind(this);
    this.updateDescription = this.updateDescription.bind(this);
  }

  handleChange(e) {
    this.setState({ text: e.target.value });
  }

  openInput() {
    if (this.props.projectRole !== 'viewer') {
      this.props.taskOpenDescription();
    }
  }

  closeInput() {
    this.props.taskOpenDescription();
    this.setState({ text: this.props.description || '' });
  }

  updateDescription() {
    this.props.fetchUpdateTask(this.props.taskId, { description: this.state.text })
      .then(() => {
        this.props.taskOpenDescription();
      });
  }

  render() {
    const { classes, open } = this.props;
    const { text } = this.state;
    const placeholder = 'Add a more detailed description...';
    const showText = text || placeholder;
    return (
      <Fragment>
        <div className={classes.title}>
          <DescriptionIcon size="23px" style={{ paddingRight: 5 }} />
          <Typography className={classes.titleText}> Description </Typography>
        </div>
        <div>
          <Fragment>
            {
              !open
              && (
                <div
                  role="button"
                  onClick={this.openInput}
                  onKeyPress={this.openInput}
                >
                  {/* <Typography
                    className={text ? classes.description : classes.redDescription}
                    style={
                     this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}
                   }
                  >
                    {showText}
                  </Typography> */}
                  {
                    showText.split('\n').map((paragraph) => {
                      return (
                        <Typography
                          key={shortid.generate()}
                          className={text ? classes.description : classes.redDescription}
                          style={
                        this.props.projectRole !== 'viewer' ? { cursor: 'pointer' } : {}
                      }
                        >
                          {paragraph}
                        </Typography>
                      );
                    })
                  }
                </div>
              )
            }
            {
              open
              && (
                <div className={classes.inputContainer}>
                  <TextField
                    id="outlined-full-width"
                    placeholder={placeholder}
                    multiline
                    autoFocus
                    margin="none"
                    variant="outlined"
                    value={text}
                    onChange={this.handleChange}
                    InputProps={{
                      classes: {
                        input: classes.input,
                        notchedOutline: classes.notchedOutline,
                      },
                    }}
                  />
                  <div style={{ marginTop: 8, display: 'flex' }}>
                    <Button
                      className={classes.button}
                      size="small"
                      variant="contained"
                      onClick={this.updateDescription}
                    >

                      Save
                    </Button>
                    <Button
                      className={classes.button}
                      size="small"
                      variant="contained"
                      onClick={this.closeInput}
                    >

                      Cancel
                    </Button>
                  </div>
                </div>
              )
            }
          </Fragment>
        </div>
      </Fragment>
    );
  }
}

Description.propTypes = {
  taskId: PropTypes.string.isRequired,
  open: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired,
  description: PropTypes.string,
  fetchUpdateTask: PropTypes.func.isRequired,
  taskOpenDescription: PropTypes.func.isRequired,
  projectRole: PropTypes.string.isRequired,
};

Description.defaultProps = {
  description: '',
};

const mapStateToProps = (state) => {
  return {
    taskId: state.board.selectedTask,
    open: state.task.openDescription,
    projectRole: state.board.selectedRole,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    fetchUpdateTask,
    taskOpenDescription,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Description));
