import React, { Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import EditModal from './EditModal';
import DeleteModal from './DeleteModal';

const ModalContainer = ({ columnEditing, columnDeleting }) => {
  return (
    <Fragment>
      { columnEditing && <EditModal /> }
      { columnDeleting && <DeleteModal /> }
    </Fragment>
  );
};

ModalContainer.propTypes = {
  columnEditing: PropTypes.bool.isRequired,
  columnDeleting: PropTypes.bool.isRequired,
};

const mapStateToProps = (state) => {
  return {
    columnEditing: state.modal.columns.columnEditing,
    columnDeleting: state.modal.columns.columnDeleting,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(ModalContainer);
